import React, { useEffect, useState } from "react";
import './credit.scss';
import { Link } from 'react-router-dom';
import KeyboardBackspaceIcon from '@material-ui/icons/KeyboardBackspace';
import api from '../../../api';
import Load from '../../../componets/Loading/Load';




import { useLocation } from "react-router-dom";

const CreditDetailCredit = () => {

    let credit_id = useLocation();
    useEffect(() => {
        getData();
    }, []);
    const [detailCredits, setdetailCredits] = useState('');
    const [plural_pago, setPlural_pago] = useState('pago');
    const [loading, setLoading] = useState(true);


    const getData = async () => {
        let id_credit = credit_id.state.id
        api.lists.showCredit(id_credit)
            .then(res => {
                console.log(res)
                setdetailCredits(res);
                setLoading(false);
                if (res.num_of_payments > 1 ) {
                    setPlural_pago('pagos');
                }
            });
    };
    if (loading == true) {
        return <Load />
    } else {
        return (
            <div className='card-panel'>
                <div className='title-card'>
                    <Link to="/credit"><KeyboardBackspaceIcon fontSize="large" /> </Link>
                    <h1 className='text-center'>Detalles de mi Crédito</h1>
                </div>
                <div className='body-card mi-credit'>
                    <div className='text-center mi-credit-amount'>
                        $ {detailCredits.value} {detailCredits.currency}
                                     </div>
                    <div className='mi-credit-details text-center'>
                        {/* <div className='mt-4'>
                            <h4 className='text-center'>Aprobado</h4>
                        </div> */}
                        <div className='mt-4'>
                            <p>Folio:</p>
                            <span className='bold mi-credit-data'>{detailCredits.folio}</span>
                        </div>
                        <div className='mt-4'>
                            <p>Número de pagos:</p>
                            <span className='bold mi-credit-data'>{detailCredits.num_of_payments} {plural_pago} en {detailCredits.num_date} {detailCredits.date_name} </span>
                        </div>
                        <div className='mt-4'>
                            <p>Monto a Pagar:</p>
                            <span className='bold mi-credit-data'>$ {detailCredits.total_payment}</span>
                        </div>
                        <div className='mt-4'>
                            <p>% de interes:</p>
                            <span className='bold mi-credit-data'> {detailCredits.interest} %</span>
                        </div>
                    </div>
                </div>
            </div>
        )
    }
};

export default CreditDetailCredit;