import React from "react";
import CreditPending from '../Credit/CreditPending';
import CreditTransfered from "./CreditTransfered";



class MyCredits extends React.Component {
    constructor(props) {
        super(props);
        this.abortController = new AbortController();
        this.signal = this.abortController.signal;
    }
    render() {
        let requested=this.props.data.credits_requested ? this.props.data.credits_requested : []
        let transfered=this.props.data.credits_transfered ? this.props.data.credits_transfered : []
        return (
            <div className='card-panel p-4'>
                <div className='title-card'>
                    <h1>Mis Créditos</h1>
                </div>
                <div className='body-card'>
                    {requested.length == 0 && transfered.length == 0
                        ?
                        <div className='credit-item text-center'>
                            <label>Todavía no tienes Créditos</label>
                        </div>
                        :
                        <div className='credit-item'>
                            <CreditPending credits={this.props} />
                            <CreditTransfered credits={this.props} />
                        </div>

                    }

                </div>
            </div>
        );
    }
}

export default MyCredits;