import React, { Component } from "react";
import api from '../../../api';
import InformationUser from './DataInfoForm';
import FileUser from './DataFileForm';

//VALIDATOR
import isName from '../../../componets/Validator/NameValidate';
import isLastName from '../../../componets/Validator/LastNameValidate';
import isPhone from '../../../componets/Validator/PhoneValidate';
import isCURP from '../../../componets/Validator/CurpValidate';
import isAddress from '../../../componets/Validator/AddressValidate';
import isSalary from '../../../componets/Validator/SalaryValidate';
import isProfession from '../../../componets/Validator/ProfessionValidate';
import Load from '../../../componets/Loading/Load';
import swal from 'sweetalert';


import axios from 'axios';



export default class DataInfo extends Component {
    constructor(props) {
        super(props);
        this.state = {
            isEdit: false,
            disabledButton: false,
            user: {},
            client: {},
            professions: [],
            NumbersCode: [],
            filesUsers: {},
            professionOption: '',
            codeOption: '',
            error: {
                nameError: false,
                lastNameError: false,
                phoneError: false,
                curpError: false,
                addressError: false,
                salaryError: false,

            },
            messageRequest: false,
            messageRequestFile: false,
            loading: true,
            chequeado: true,
        }
        this.abortController = new AbortController();
        this.signal = this.abortController.signal;
    }
    componentDidMount() {
        this.fetchUser()
    }

    fetchUser = () => {
        this.setState({ loading: true });
        api.lists.profile(this.signal)
            .then(res => {
                console.log(res)
                let firstEdit = false;
                if (res.user[0].name == null && res.user[0].client_info == null) {
                    firstEdit = true;
                }
                this.setState({
                    ...this.state,
                    isEdit: firstEdit,
                    user: {
                        ...this.state.user,
                        name: res.user[0].name,
                        last_name: res.user[0].last_name,
                        email: res.user[0].email,
                        phone: res.user[0].phone,
                        code: res.user[0].code ? res.user[0].code : 152,
                        level: res.user[0].level,
                    },
                    client: res.user[0].client_info != null
                        ?
                        {
                            ...this.state.client,
                            curp: res.user[0].client_info.curp,
                            address: res.user[0].client_info.address,
                            profession_id: res.user[0].client_info.profession_id,
                            salary: res.user[0].client_info.salary,
                            policies_privacy: res.user[0].client_info.policies_privacy,
                            terms_conditions: res.user[0].client_info.terms_conditions,
                            photo_face: res.user[0].client_info.photo_face,
                            photo_ine_front: res.user[0].client_info.photo_ine_front,
                            photo_ine_back: res.user[0].client_info.photo_ine_back,
                            domiciliary_proof: res.user[0].client_info.domiciliary_proof,

                        }
                        :
                        {},
                });

                let professions = [];
                res.profession.forEach(element => {
                    professions.push({
                        label: element.name,
                        value: element.id
                    });
                });

                let NumbersCode = [];
                res.codeNumbers.forEach(element => {
                    NumbersCode.push({
                        label: element.code + '  -  ' + element.name,
                        value: element.id
                    });
                });

                this.setState({
                    professions: professions,
                    loading: false,
                    NumbersCode: NumbersCode,

                });

                Object.values(this.state.professions).map((e) =>
                    res.user[0].client_info != null ?
                        e.value === res.user[0].client_info.profession_id ?
                            this.setState({ professionOption: e }) : ''
                        :
                        this.setState({ professionOption: '' })
                );

                Object.values(this.state.NumbersCode).map((e) =>
                    res.user[0].code != null ?
                        e.value == res.user[0].code ?
                            this.setState({ ...this.state, codeOption: e }) : 152
                        :
                        this.setState({ ...this.state, codeOption: 152 })
                );

            });
    }

    componentWillUnmount() {
        this.abortController.abort();
    }

    handleEditProfile = e => {
        this.setState({
            isEdit: true,
        });
    }

    handleCancel = e => {
        e.preventDefault();
        this.setState({
            isEdit: false,
            error: {
                nameError: false,
                lastNameError: false,
                phoneError: false,
                curpError: false,
                addressError: false,
                salaryError: false,

            },
            messageRequest: false,
            loading: true

        });
        this.fetchUser();
    }

    handleUserChange = (name, value) => {
        let code_Option = ''
        switch (name) {
            case 'name':
                let validateName = isName(value)
                if (validateName != "nodata") {
                    this.setState({
                        error: {
                            ...this.state.error,
                            nameError: true,
                            nameMessageError: isName(value),
                        }
                    });
                } else {
                    this.setState({
                        error: {
                            ...this.state.error,
                            nameError: false,
                            nameMessageError: '',
                        }
                    });
                }

                break;
            case 'last_name':
                let validateLastName = isLastName(value)
                if (validateLastName != "nodata") {
                    this.setState({
                        error: {
                            ...this.state.error,
                            lastNameError: true,
                            lastNameMessageError: isLastName(value),
                        }
                    });
                } else {
                    this.setState({
                        error: {
                            ...this.state.error,
                            lastNameError: false,
                            lastNameMessageError: '',
                        }
                    });
                }

                break;
            case 'phone':
                let validatePhone = isPhone(value)
                if (validatePhone != "nodata") {
                    this.setState({
                        error: {
                            ...this.state.error,
                            phoneError: true,
                            phoneMessageError: isPhone(value),
                        }
                    });
                } else {
                    this.setState({
                        error: {
                            ...this.state.error,
                            phoneError: false,
                            phoneMessageError: '',
                        }
                    });
                }

                break;
            case 'code':
                code_Option = value;
                console.log(code_Option)
                this.setState({
                    codeOption: code_Option,
                });
                break;

        }
        this.setState({
            user: {
                ...this.state.user,
                [name]: value
            }
        });
    }
    handleClientChange = (name, value) => {
        let profeOption = ''
        switch (name) {
            case 'curp':
                let validateCurp = isCURP(value)
                if (validateCurp != "nodata") {
                    this.setState({
                        error: {
                            ...this.state.error,
                            curpError: true,
                            curpMessageError: isCURP(value),
                        }
                    });
                } else {
                    this.setState({
                        error: {
                            ...this.state.error,
                            curpError: false,
                            curpMessageError: '',
                        }
                    });
                }

                break;
            case 'address':
                let validateAddre = isAddress(value)
                if (validateAddre != "nodata") {
                    this.setState({
                        error: {
                            ...this.state.error,
                            addressError: true,
                            addressMessageError: isAddress(value),
                        }
                    });
                } else {
                    this.setState({
                        error: {
                            ...this.state.error,
                            addressError: false,
                            addressMessageError: '',
                        }
                    });
                }
                break;
            case 'salary':
                let validateSalary = isSalary(value)
                if (validateSalary != "nodata") {
                    this.setState({
                        error: {
                            ...this.state.error,
                            salaryError: true,
                            salaryMessageError: isSalary(value),
                        }
                    });
                } else {
                    this.setState({
                        error: {
                            ...this.state.error,
                            salaryError: false,
                            salaryMessageError: '',
                        }
                    });
                }
                break;
            case "profession":
                let validateProfession = isProfession(value)
                if (validateProfession != "nodata") {
                    this.setState({
                        error: {
                            ...this.state.error,
                            professionError: true,
                            professionMessageError: isProfession(value),
                        }
                    });
                } else {
                    profeOption = value;
                    this.setState({
                        professionOption: profeOption,
                        error: {
                            ...this.state.error,
                            professionError: false,
                            professionMessageError: '',
                        }
                    });
                }
                break;

        }

        this.setState({

            client: {
                ...this.state.client,
                [name]: value
            }
        });
    }

    handleCheckChange = (e) => {
        this.setState({
            client: {
                ...this.state.client,
                [e.target.name]: e.target.checked
            },
            chequeado: true,
        });
    }
    handleFile = (e) => {
        let files = e.target.files[0]
        console.log(e.target.name)
        this.setState({
            filesUsers: {
                ...this.state.filesUsers,
                [e.target.name]: files
            }
        })
    }

    handleSubmit = (e) => {
        e.preventDefault();
        this.setState({ loading: true, disabledButton: true })
        let profesion = this.state.client.profession_id;
        if (this.state.client.profession_id != parseInt(this.state.professionOption.value)) {
            if (parseInt(this.state.professionOption.value) != '') {
                profesion = this.state.professionOption
            }
        }
        const body = {
            name: this.state.user.name,
            last_name: this.state.user.last_name,
            phone: this.state.user.phone,
            curp: this.state.client.curp,
            address: this.state.client.address,
            salary: this.state.client.salary,
            profession_id: profesion,
            policies_privacy: this.state.client.policies_privacy,
            terms_conditions: this.state.client.terms_conditions,
            code_number_id: this.state.user.code,
        }
        console.log(body)
        api.update.userInfo(body)
            .then((response) => {
                if (response.errors) {
                    let errorsMe = [];
                    let myError = response.errors;
                    Object.values(myError).forEach((error, i) => {
                        errorsMe.push({
                            label: error[0],
                            id: i,
                        });
                    });
                    this.setState({
                        ...this.state,
                        messageRequest: errorsMe,
                        loading: false,
                        disabledButton: false,
                        chequeado: false

                    });
                } else {
                    this.setState({
                        ...this.state,
                        isEdit: false,
                        messageRequest: false,
                        disabledButton: false,
                        chequeado: false

                    });
                    this.fetchUser();

                }

            })
            .catch((error) => {
                let errorsMe = [];

                let myError = error.response.data.errors;
                Object.values(myError).forEach((error, i) => {
                    errorsMe.push({
                        label: error[0],
                        id: i,
                    });
                });
                this.setState({
                    messageRequest: errorsMe,
                    loading: false,

                });

            })
    }

    handleSubmitImages = (e) => {
        e.preventDefault();
        this.setState({ loading: true, disabledButton: true })
        const files = this.state.filesUsers;
        console.log(files)
        if (files.photo_face == undefined || files.photo_ine_front == undefined || files.photo_ine_back == undefined || files.domiciliary_proof == undefined) {
            this.setState({
                messageRequestFile: [{
                    label: 'Sube todos los archivos que se te indican',
                    id: 1
                }],
                loading: false,
                disabledButton: false,

            });
        } else {
            let formData = new FormData()
            formData.append('photo_face', this.state.filesUsers.photo_face)
            formData.append('photo_ine_front', this.state.filesUsers.photo_ine_front)
            formData.append('photo_ine_back', this.state.filesUsers.photo_ine_back)
            formData.append('domiciliary_proof', this.state.filesUsers.domiciliary_proof)
            let token = localStorage.getItem("token");
            axios.post('http://165.22.0.9/api/profile/change/userFilesInformation', formData, {
                headers: {
                    'Content-Type': 'multipart/form-data',
                    Authorization: `Bearer ${token}`
                }
            })
                .then((response) => {
                    console.log(response)
                    this.setState({
                        messageRequestFile: false,
                        fileKey: Date.now(),
                        loading: false,
                        disabledButton: false,

                    });
                    this.fetchUser();
                })
                .catch((error) => {
                    console.log(error)
                    this.setState({
                        loading: false,
                        disabledButton: false,
                    });
                })
        }
    }
    render() {
        if (this.state.loading) { return <Load /> }
        return (
            <React.Fragment>
                <div className='card-panel p-5'>
                    <div>
                        <InformationUser
                            state={this.state}
                            edit={this.state.isEdit}
                            handleEditProfile={this.handleEditProfile}
                            handleUserChange={this.handleUserChange}
                            handleClientChange={this.handleClientChange}
                            handleCheckChange={this.handleCheckChange}
                            handleCancel={this.handleCancel}
                            handleSubmit={this.handleSubmit}
                        />
                    </div>
                    <div>
                        <FileUser
                            state={this.state}
                            fileKey={this.state.fileKey}
                            handleFile={(e) => this.handleFile(e)}
                            handleSubmitImages={(e) => this.handleSubmitImages(e)}

                        />
                    </div>
                </div>
            </React.Fragment>
        );
    }
}
