import React, { useState, useEffect } from 'react';
import '../../../src/assets/css/style.scss';
import Label from './../../componets/Label/Label';
import Input from './../../componets/Input/Input';
import LoginButton from './../../componets/LoginButton/LoginButton';
import Header from '../../componets/Header/Hedear';
import { NavLink } from 'react-router-dom';
import axios from 'axios';
import RequestMessage from '../../componets/Messages/RequestMessage';
import SelectCodeNumbers from '../../componets/Select/SelectCodeNumbers';



const Register = () => {
    const [email, setEmail] = useState('');
    const [code, setCode] = useState(152);
    const [phone, setPhone] = useState('');
    const [password, setPassword] = useState('');
    const [passwordconfirm, setPasswordconfirm] = useState('');
    const [emailError, setEmailError] = useState(false);
    const [phoneError, setPhoneError] = useState(false);
    const [codeError, setCodeError] = useState(false);
    const [passwordError, setPasswordError] = useState(false);
    const [passwordErrorConfirm, setPasswordErrorConfirm] = useState(false);

    const [disabledButton, setDisabledButton] = useState(true);
    const [ErrorsMessage, setErrorsMessage] = useState('');
    const [messageRequest, setMessageRequest] = useState('');
    const [codeNumbers, setCodeNumbers] = useState('');

    useEffect(() => {
        getCode();
    }, []);

    const getCode = async () => {
        const abortController = new AbortController();
        const signal = abortController.signal;

        const data = await fetch("http://165.22.0.9/api/auth/indexRegister", { signal: signal });
        const codes = await data.json();
        console.log(codes);
        setCodeNumbers(codes)

    };

    function handleChange(name, value) {
        setMessageRequest('');
        switch (name) {
            case 'email':
                const regex = /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
                if (!value.trim()) {
                    setEmailError('Correo Electrónico requerido')
                    setPasswordError(true);
                    setPasswordErrorConfirm(true);

                } else if (!regex.test(String(value).toLowerCase())) {
                    setEmailError('Correo Electrónico no válido')
                    setPasswordError(true);
                    setPasswordErrorConfirm(true);
                } else {
                    setEmail(value);
                    setEmailError(false)
                    setPasswordError(true);
                    setPasswordErrorConfirm(true);
                    setPhoneError('Teléfono requerido');
                }
                setCode(152);
                break;
            case 'code':
                if (!value.trim()) {
                    setCodeError('Lada Requerida');
                    setDisabledButton(true);
                } else {
                    setCode(value);
                    setCodeError(false);
                    setDisabledButton(true);
                }
                break;
            case 'phone':
                if (!value.trim()) {
                    setPhoneError('Teléfono requerido');
                    setDisabledButton(true);
                } else if (!(/^[0-9]{0,20}(\.?)[0-9]{0,2}$/.test(value))) {
                    setPhoneError('Teléfono inválido');
                    setDisabledButton(true);
                } else {
                    setPhone(value);
                    setPhoneError(false);
                    setDisabledButton(true);
                }
                break;
            case 'password':
                if (value.length < 8) {
                    setPasswordError(true);
                    setPasswordErrorConfirm(true);
                    setDisabledButton(true);
                } else {
                    setPasswordError(false);
                    setPassword(value);
                    setDisabledButton(true);
                    setPasswordErrorConfirm(true);
                }
                break;
            default:
                if (password != value) {
                    setPasswordErrorConfirm(true);
                    setDisabledButton(true);
                } else if (!value.trim()) {
                    setPasswordErrorConfirm(true);
                    setDisabledButton(true);
                } else {
                    setDisabledButton(false);
                    setPasswordErrorConfirm(false);
                    setPasswordconfirm(value);
                }
                break;
        }
    };

    function handleSubmit() {
        let account = { email, password, passwordconfirm, phone, code }
        console.log(account);
        if (account) {
            axios.post('http://165.22.0.9/api/auth/signup', {
                email: email,
                phone: phone,
                code_number_id: code,
                password: password,
                password_confirmation: passwordconfirm
            })
                .then((response) => {
                    console.log(response)
                    localStorage.setItem('token', response.data.access_token);
                    localStorage.setItem('user', JSON.stringify(response.data.user));
                    window.location = '/credit';
                })
                .catch((error) => {
                    let errorsMe = [];

                    let myError = error.response.data.errors;
                    console.log(myError)
                    Object.values(myError).forEach((error, i) => {
                        console.log(error[0])
                        errorsMe.push({
                            label: error[0],
                            id: i,
                        });
                    });
                    console.log(errorsMe)
                    setMessageRequest(errorsMe);

                })

        }
    };



    return (
        <section>
            <Header />
            <div className='card-login card-form space-form'>
                <h3 className='text-center'>Regístrate</h3>

                <div className='form-group'>
                    <LoginButton />
                </div>
                <div className='form-group'>
                    {messageRequest &&
                        <RequestMessage attribute={messageRequest} />
                    }
                </div>

                <div className='form-group'>
                    <Label text='Correo electrónico' />
                    <Input
                        attribute={{
                            id: 'email',
                            name: 'email',
                            type: 'email',
                            placeholder: 'Correo Electrónico',

                        }}
                        handleChange={handleChange}
                    />
                    {emailError &&
                        <label className='label-error'>{emailError}</label>
                    }
                </div>
                <div className='row'>
                    <div className='form-group col-6'>
                        <Label text='Código' />
                        <SelectCodeNumbers
                            id='code'
                            name='code'
                            value={code}
                            options={codeNumbers}
                            isDisabled={true}
                            handleChange={handleChange}
                        />
                        {codeError &&
                            <label className='label-error'>{codeError}</label>
                        }
                    </div>
                    <div className='form-group col-6'>
                        <Label text='Teléfono' />
                        <Input
                            attribute={{
                                id: 'phone',
                                name: 'phone',
                                type: 'text',
                                placeholder: 'Teléfono',

                            }}
                            handleChange={handleChange}
                        />
                        {phoneError &&
                            <label className='label-error'>{phoneError}</label>
                        }
                    </div>
                </div>
                <div className='form-group'>
                    <Label text='Contraseña' />
                    <Input
                        attribute={{
                            id: 'password',
                            name: 'password',
                            type: 'password',
                            placeholder: 'Contraseña'
                        }}
                        handleChange={handleChange}
                        param={passwordError}
                    />
                    {passwordError &&
                        <label className='label-error'>La contraseña debe contener al menos 8 carácteres.</label>
                    }

                </div>

                <div className='form-group'>
                    <Label text='Confirmar contraseña' />
                    <Input
                        attribute={{
                            id: 'passwordconfirm',
                            name: 'passwordconfirm',
                            type: 'password',
                            placeholder: 'Confirmar contraseña',
                        }}
                        handleChange={handleChange}
                        param={passwordErrorConfirm}

                    />
                    {passwordErrorConfirm &&
                        <label className='label-error'>La confirmación de la contraseña no coincide.</label>
                    }
                </div>
                <div className='text-center'>
                    <button disabled={disabledButton} className='btn-dark btn-block bt-submit' onClick={handleSubmit}>Regístrarme</button>
                </div>

                <div className='login-btn-sidebar'>
                    <p>
                        Ya tengo cuenta <NavLink className='link' to="/login">Iniciar Sesión</NavLink>
                    </p>
                </div>
            </div>
        </section>

    )
};

export default Register;