import React from 'react';
import api from '../../../api';
import Load from '../../../componets/Loading/Load';
import InfoClientBasic from '../Requests/InfoClienteBasic';
import { Link } from 'react-router-dom';



class User extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            clients: [],
            loading: true,
        }
        this.abortController = new AbortController();
        this.signal = this.abortController.signal;
    }
    componentDidMount() {
        this.getClientsUsers()
    }
    getClientsUsers = () => {
        api.lists.getClients(this.signal)
            .then(res => {
                console.log(res)
                this.setState({
                    ...this.state,
                    clients: res,
                    loading:false,
                });
            });
    }
    render() { 
        if (this.state.loading) {
            return <Load />
        }
        let clients = '';
        let clientsAll = this.state.clients ? this.state.clients : [];

        clients = Object.values(clientsAll).map((info) => {
            return (
                <tr key={info.id} className='text-center'>
                    <td>{info.id}</td>
                    <td><InfoClientBasic infoClient={info} /></td>
                    <td><Link className='simple-link' to={{ pathname: '/history_credits_client', state: { id: info.id} }}>Historial</Link></td>
                </tr>
            )

        })
        return ( 
            <section className='container'>
                <div className='row'>
                    <div className='col-12 mt-4'>
                        <div className='card p-4'>
                            Lista de clientes
                            <div className='table-responsive'>
                                <table className="table">
                                    <thead>
                                        <tr className="text-center">
                                            <th scope="col">ID</th>
                                            <th scope="col">Nombre</th>
                                            <th scope="col">Historial</th>
                                        </tr>
                                    </thead>
                                    <tbody>
                                       {clients}
                                    </tbody>
                                </table>
                            </div>
                        </div>
                    </div>
                </div>
            </section>
        );
    }
}
 
export default User;