import React from "react";
import InfoClientBasic from '../Requests/InfoClienteBasic';
import Load from '../../../componets/Loading/Load';
import api from '../../../api';
import moment from 'moment';
import CheckIcon from '@material-ui/icons/Check';
import MessagesSupport from '../Requests/InfoMessageSupport';
import swal from 'sweetalert';
import { Link } from 'react-router-dom';


class ProblemCreditsSupport extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            messages_problem_credits: [],
            loading: true,
        }
        this.abortController = new AbortController();
        this.signal = this.abortController.signal;
    }
    componentDidMount() {
        this.getMessageProblemCredits()
    }
    getMessageProblemCredits = () => {
        api.lists.getMessageProblemCredit(this.signal)
            .then(res => {
                console.log(res)
                this.setState({
                    ...this.state,
                    messages_problem_credits: res,
                    loading: false,
                });
            });
    }
    handleReadMessage = (e, id_msg) => {
        e.preventDefault();
        this.setState({ loading: true });
        api.update.admin.ready_message(id_msg)
            .then((response) => {
                console.log(response)
                if (response.type == "error" && response.code == 500) {
                    this.setState({
                        // messageRequest: [{ label: response.message, id: 1 }],
                        loading: false,
                    });
                } else {
                    swal({
                        title: "Correcto!",
                        text: response.message,
                        icon: "success",
                        timer: 2000,
                        button: false
                    });
                    this.getMessageProblemCredits();
                }

            })
            .catch((error) => {
                console.log(error)
            })
    }
    render() {
        if (this.state.loading) {
            return <Load />
        }
        let messages = '';
        let messagesAll = this.state.messages_problem_credits ? this.state.messages_problem_credits : [];

        messages = Object.values(messagesAll).map((info) => {
            return (
                <tr key={info.id} className='text-center'>
                    <td>{info.id}</td>
                    <td><InfoClientBasic infoClient={info.user} /></td>
                    <td>{info.support_type.name}</td>
                    <td><MessagesSupport messaje={info.message} /></td>
                    <td>{moment(info.created_at).format("DD-MM-YYYY")}</td>
                    <td>
                        <button type="button" className="btn btn-success btn-sm mr-2"
                            onClick={(e) => {
                                swal({
                                    title: '¿Estás seguro que deseas marcar este mensaje como leído?',
                                    text: 'Una vez marcado podrás visualizarlo en el apartado de "Mensajes Leídos"',
                                    icon: 'warning',
                                    buttons: true,
                                    dangerMode: true,
                                })
                                    .then(willDelete => {
                                        if (willDelete) { this.handleReadMessage(e, info.id) }
                                        else { swal.close(); }
                                    })
                            }} ><CheckIcon /></button>
                    </td>

                </tr>
            )

        })
        return (
            <section className='container'>
                <div className='row'>
                    <div className='col col-12'>
                        <div className='card p-4 mt-4 mb-4'>
                            <div className="col col-12">
                                <div className='row'>
                                    <div className='col-10'>
                                        <h5>Mensajes de Problemas con Créditos</h5>
                                    </div>
                                    <div className='col-2 text-center'>
                                    <Link className='simple-link' to={{ pathname: '/message_ready_problemsCredits' }}>Mensajes Leídos</Link>
                                        
                                    </div>
                                </div>
                            </div>
                            <div className='card-body'>
                                <div className='table-responsive'>
                                    <table className="table">
                                        <thead>
                                            <tr className='text-center'>
                                                <th scope="col">ID</th>
                                                <th scope="col">Cliente</th>
                                                <th scope="col">Asunto</th>
                                                <th scope="col">Mensaje</th>
                                                <th scope="col">Fecha</th>
                                                <th scope="col">Marcar Leído</th>
                                            </tr>
                                        </thead>
                                        <tbody>
                                            {this.state.messages_problem_credits.length == 0
                                                ?
                                                <tr className='text-center'>
                                                    <td colSpan="6">No hay Mensajes</td>
                                                </tr>
                                                :
                                                messages
                                            }
                                        </tbody>
                                    </table>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </section>
        );
    }
}

export default ProblemCreditsSupport;