import React from "react";
import { BrowserRouter as Router, Switch, Route } from "react-router-dom";
import Credit from './Credit/Credit';
import Profile from './Profile/Profile';
import Menu from './Menu';
import MenuBottom from './MenuBottom';
import PasswordUpdate from './Profile/PasswordUpdate';
import PaymentMethod from './Credit/PaymentMethods';
import CreditDetail from './Credit/CreditDetail';
import CreditDetailCredit from './Credit/CreditDetailCredit';
import CreditDetailPayments from './Credit/CreditDetailPayments';
import CreditDetailHistory from './Credit/CreditDetailHistory';
import Notification from './Notifications/Notifications';

const routes = [
  {
    path: "/credit",
    component: Credits
  },
  {
    path: "/profile",
    component: Profiles
  },
  {
    path: "/password_update",
    component: PasswordUpdates
  },
  {
    path: "/payment_methods",
    component: PaymentMethods
  },
  {
    path: "/credit_details",
    component: CreditDetails
  },
  {
    path: "/details_info_credit",
    component: CreditDetailCredits
  },
  {
    path: "/notifications",
    component: Notifications
  },
  {
    path: "/credit_payments",
    component: CreditPayments
  },
  {
    path: "/credit_history",
    component: CreditHistory
  },
//   {
//     path: "/profile",
//     component: Profiles,
//     routes: [
//       {
//         path: "/profile/password_update",
//         component: PasswordUpdates
//       }
//     ]
//   }
];

export default function RouteConfigExample() {
    return (
        <Router>
            <div className='panel'>
              <Menu/>
                <MenuBottom/>
                <Switch>
                  {routes.map((route, i) => (
                      <RouteWithSubRoutes key={i} {...route} />
                  ))}
                  </Switch>
            </div>
        </Router>
    );
}

function RouteWithSubRoutes(route) {
  return (
    <Route
      path={route.path}
      render={props => (
        <route.component {...props} routes={route.routes} />
      )}
    />
  );
}

function Credits() {
  return <div className='content-panel'><Credit/></div>;
}

function Profiles() {
    return <div className='content-panel'><Profile/></div>;
  }

