function PasswordUpdates() {
  return <div className='content-panel'><PasswordUpdate/></div>;
}

function PaymentMethods() {
  return <div className='content-panel'><PaymentMethod/></div>;
}

function CreditDetails() {
  return <div className='content-panel'><CreditDetail/></div>;
}

function CreditDetailCredits() {
  return <div className='content-panel'><CreditDetailCredit/></div>;
}

function CreditPayments() {
  return <div className='content-panel'><CreditDetailPayments/></div>;
}
function CreditHistory() {
  return <div className='content-panel'><CreditDetailHistory/></div>;
}

function Notifications() {
  return <div className='content-panel'><Notification/></div>;
}