import React, { useState } from 'react';
import { makeStyles, Theme, createStyles } from '@material-ui/core/styles';
import Modal from '@material-ui/core/Modal';
import Backdrop from '@material-ui/core/Backdrop';
import Fade from '@material-ui/core/Fade';
import '../../../assets/css/style.scss';
import CloseIcon from '@material-ui/icons/Close';
import CheckBoxIcon from '@material-ui/icons/CheckBox';
import api from '../../../api';
import axios from 'axios';



const useStyles = makeStyles((theme: Theme) =>
    createStyles({
        modal: {
            display: 'flex',
            alignItems: 'center',
            justifyContent: 'center',
        },
        paper: {
            backgroundColor: theme.palette.background.paper,
            border: 'none !important',
            borderRadius: '15px',
            width: '95%',
            maxWidth: '800px',
            maxHeight: '600px',
            overflowY: 'scroll',
            scrollBehavior: 'smooth',
            boxShadow: theme.shadows[5],
            padding: theme.spacing(2, 4, 3),
        },
    }),
);

export default function TransitionsModal(props) {
    const classes = useStyles();
    const [open, setOpen] = React.useState(false);
    const [pathImages, setPathImages] = useState('');

    const handleOpen = () => {
        setOpen(true);
        setPathImages(api.base_url_img())

    };

    const handleClose = () => {
        setOpen(false);
    };

    
console.log(props)
return (
    <div>
        <div onClick={handleOpen}>
            <div className='simple-link'>
                <p>Mensaje</p>
            </div>
        </div>
        <Modal
            aria-labelledby="transition-modal-title"
            aria-describedby="transition-modal-description"
            className={classes.modal}
            open={open}
            onClose={handleClose}
            closeAfterTransition
            BackdropComponent={Backdrop}
            BackdropProps={{
                timeout: 500,
            }}
        >
            <Fade in={open}>
                <div className={classes.paper}>
                    <div className='data-modal'>
                        <div style={{ textAlign: "right" }}><button onClick={handleClose}><CloseIcon fontSize="large" /></button></div>
                        <div className='modal-header'>
                            <h5>Mensaje</h5>
                        </div>
                        <div className='modal-body row'>
                            <div className='col col-4 mb-3'>
                                <p>
                                    {props.messaje ? props.messaje : '-'}
                                </p>
                            </div>
                        </div>
                    </div>
                </div>
            </Fade>
        </Modal>
    </div>
);
}