import React from "react";
import SimulatorBar from '../../componets/SimulatorBar/SimulatorBar';

class Simulator extends React.Component{
    
    render() {
        return(
            <section className='simulator'>
                
                <h1>¿Cuánto <span className='bolder'>dinero</span> necesitas?</h1>
                
                <div className='text-center mt-4'>
                    <SimulatorBar/>
                </div>
            </section>
        );
    }
}

export default Simulator;