import React from "react";
import { Link } from 'react-router-dom';
import Pdf from 'react-to-pdf';
import { Document, Page, Text, View, StyleSheet } from "@react-pdf/renderer";
import KeyboardBackspaceIcon from '@material-ui/icons/KeyboardBackspace';


const styles = StyleSheet.create({
    page: {
        flexDirection: "row",
        width:'auto',
    },
    section: {
        flexGrow: 1
    }
});
const ref = React.createRef();

const GeneratePDF = (props) => {
    return (
        <React.Fragment>
            <div className='card-backFlagPDF col-5'>
            <Link to="/credit"><KeyboardBackspaceIcon fontSize="large" /> </Link>
            </div>
            <Document>
                <Page size="A4" style={styles.page}>
                    <View style={styles.section}>
                        <Text>
                            <div className="opps" ref={ref}>
                                <div className="opps-header">
                                    <div className="opps-reminder">Ficha digital. No es necesario imprimir.</div>
                                    <div className="opps-info">
                                        <div className="opps-brand"></div>
                                        <div className="opps-ammount">
                                            <h3>Monto a pagar</h3>
                                            <h2>$ {props.info.value} <sup>{props.info.currency}</sup></h2>
                                            <p>OXXO cobrará una comisión adicional al momento de realizar el pago.</p>
                                        </div>
                                    </div>
                                    <div className="opps-reference">
                                        <h3 className="title-reference">Referencia</h3>
                                        <h1 className="reference">{props.info.reference}</h1>
                                        <p>Num Folio: {props.info.folio}</p>
                                        <p>Nombre: {props.info.client_name}</p>

                                    </div>
                                </div>
                                <div className="opps-instructions">
                                    <h3>Instrucciones</h3>
                                    <ol>
                                        <li>Indica en caja que quieres realizar un pago de <strong>OXXOPay</strong>.</li>
                                        <li>Dicta al cajero el número de referencia en esta ficha para que tecleé directamete en la pantalla de venta.</li>
                                        <li>Realiza el pago correspondiente con dinero en efectivo.</li>
                                        <li>Al confirmar tu pago, el cajero te entregará un comprobante impreso. <strong>En él podrás verificar que se haya realizado correctamente.</strong> Conserva este comprobante de pago.</li>
                                    </ol>
                                    <div className="opps-footnote">Al completar estos pasos recibirás un correo de <strong>Creditok</strong> confirmando tu pago.</div>
                                </div>
                            </div>
                            <div className='card-buttonPDF mb-footer col-4'>
                                <Pdf targetRef={ref} filename='TalonPago.pdf'>
                                    {({ toPdf }) => <button className='btn-dark btn-block' onClick={toPdf}>Descargar PDF</button>}
                                </Pdf>
                            </div>
                        </Text>
                    </View>
                </Page>
            </Document>

        </React.Fragment>
    )
};

export default GeneratePDF;
