import React from 'react';
import { makeStyles, Theme, createStyles } from '@material-ui/core/styles';
import Modal from '@material-ui/core/Modal';
import Backdrop from '@material-ui/core/Backdrop';
import Fade from '@material-ui/core/Fade';
import './modal.scss';

const useStyles = makeStyles((theme: Theme) =>
    createStyles({
        modal: {
            display: 'flex',
            alignItems: 'center',
            justifyContent: 'center',
        },
        paper: {
            backgroundColor: theme.palette.background.paper,
            border: 'none !important',
            borderRadius: '15px',
            width: '95%',
            maxWidth: '500px',
            boxShadow: theme.shadows[5],
            padding: theme.spacing(2, 4, 3),
        },
    }),
);

export default function TransitionsModal() {
    const classes = useStyles();
    const [open, setOpen] = React.useState(false);

    const handleOpen = () => {
        setOpen(true);
    };

    const handleClose = () => {
        setOpen(false);
    };

    return (
        <div>
            <div onClick={handleOpen}>
                <div className='card-method bg-method'>
                    <p>Transferencia</p>
                </div>
            </div>
            <Modal
                aria-labelledby="transition-modal-title"
                aria-describedby="transition-modal-description"
                className={classes.modal}
                open={open}
                onClose={handleClose}
                closeAfterTransition
                BackdropComponent={Backdrop}
                BackdropProps={{
                    timeout: 500,
                }}
            >
                <Fade in={open}>
                    <div className={classes.paper}>
                        <div className='modal-width'>
                            <div className='mi-credit'>
                                <h4 className='text-center'>Detalles de Pago</h4>
                                <p className='text-center mt-4'>Monto a pagar</p>
                                <div className='text-center mi-credit-amount'>
                                    $ 500.00
                                </div>
                                <div className='mi-credit-details text-center'>
                                    <div className='mt-4'>
                                        <p>Banco:</p>
                                        <span className='bold mi-credit-data'>Nombre del banco</span>
                                    </div>
                                    <div className='mt-4'>
                                        <p>Cuenta Bancaría:</p>
                                        <span className='bold mi-credit-data'># de cuenta</span>
                                    </div>
                                    <div className='mt-4'>
                                        <p>Concepto:</p>
                                        <span className='bold mi-credit-data'>Concepto</span>
                                    </div>
                                    <div className='mt-4'>
                                        <p>Referencia:</p>
                                        <span className='bold mi-credit-data'>Referencia</span>
                                    </div>
                                    <div className='mt-4'>
                                        <p>Fecha límite de pago:</p>
                                        <span className='bold mi-credit-data'>DD/MM/AAAA</span>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </Fade>
            </Modal>
        </div>
    );
}