import React from 'react';
import Cards from 'react-credit-cards';
import 'react-credit-cards/lib/styles.scss';
import RequestMessage from '../../../componets/Messages/RequestMessage';
import api from '../../../api';
import { Link } from 'react-router-dom';
import KeyboardBackspaceIcon from '@material-ui/icons/KeyboardBackspace';
import isName from '../../../componets/Validator/NameValidate';
import isAccountNumbers from '../../../componets/Validator/AccountNumbersValidate';
import isCVC from '../../../componets/Validator/CvcValidate';
import Load from '../../../componets/Loading/Load';
import swal from 'sweetalert';




export default class PaymentForm extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            card: {
                cvc: '',
                exp_month: '',
                exp_year: '',
                focus: '',
                name: '',
                number: '',
            },
            expiry: '',
            messageRequest: false,
            disabledButton: false,
            error: {
                nameError: false,
                accountError: false,
                cvcError: false,
            },
            loading: false,


        }
    }

    handleInputFocus = (e) => {
        this.setState({ focus: e.target.name });
    }

    handleInputChange = (e) => {
        const { name, value } = e.target;
        if (name == 'exp_month') {
            let expiricy = value + '/' + this.state.card.exp_year
            this.setState({ ...this.state, expiry: expiricy });

        } else if (name == 'exp_year') {
            let expiricy = this.state.card.exp_month + '/' + value
            this.setState({ ...this.state, expiry: expiricy });
        }
        switch (name) {
            case 'number':
                let validateAccountNum = isAccountNumbers(value)
                console.log(validateAccountNum)
                if (validateAccountNum != "nodata") {
                    this.setState({
                        error: {
                            ...this.state.error,
                            accountError: true,
                            accountMessageError: isAccountNumbers(value),
                        }
                    });
                } else {
                    this.setState({
                        error: {
                            ...this.state.error,
                            accountError: false,
                            accountMessageError: '',
                        }

                    });
                }
                break;
            case 'name':
                let validateName = isName(value)
                if (validateName != "nodata") {
                    this.setState({
                        error: {
                            ...this.state.error,
                            nameError: true,
                            nameMessageError: isName(value),
                        }
                    });
                } else {
                    this.setState({
                        error: {
                            ...this.state.error,
                            nameError: false,
                            nameMessageError: '',
                        }
                    });
                }

                break;
            case 'exp_month':
                break;
            case 'exp_year':
                break;
            case 'cvc':
                let validateCVC = isCVC(value)
                if (validateCVC != "nodata") {
                    this.setState({
                        error: {
                            ...this.state.error,
                            cvcError: true,
                            cvcMessageError: isCVC(value),
                        }
                    });
                } else {
                    this.setState({
                        error: {
                            ...this.state.error,
                            cvcError: false,
                            cvcMessageError: '',
                        }
                    });
                }
                break;
        }
        this.setState({ card: { ...this.state.card, [name]: value } });
    }
    successHandler = (token) => {
        /* token keys: id, livemode, used, object */
       
        const body = {
            // token: token.id,
            token: "tok_test_visa_4242",
            schedule_payment_id: this.props.schedule_id,
            payment_platform_id: this.props.plataform_id,
        }
        console.log(body)
        api.create.payment_card(body)
            .then((response) => {
                console.log(response)
                this.setState({ ...this.state, loading:false });
                swal({
                    title: "Correcto!",
                    text: 'La información se mandó correctamente',
                    icon: "success",
                    button: true,
                    dangerMode: false,
                })
                    .then(willDelete => {
                        if (willDelete) {
                            this.setState({ ...this.state, messageRequest: false, disabledButton: false, loading:false });
                            window.location = '/credit';
                        }
                    })
                
            })
            .catch((error) => {
                console.log(error)
                this.setState({ ...this.state, messageRequest: false, disabledButton: false, loading:false });
            })
    };

    errorHandler = (err) => {
        console.log(err);
        let errorsMe = [];
        errorsMe.push({
            label: err.message_to_purchaser,
            id: 1,
        });
        this.setState({ ...this.state, messageRequest: errorsMe, disabledButton: false, loading:false });
    };

    handleSubmit = (e) => {
        e.preventDefault();
        this.setState({
            disabledButton: true,
            messageRequest: false,
            loading: true,

        });
        if (this.state.card.name == '' || this.state.card.cvc == '' || this.state.card.exp_month == '' || this.state.card.exp_year == '' || this.state.card.number == '') {
            this.setState({
                messageRequest: [{
                    label: 'Llena correctamente todos los campos que se te piden',
                    id: 1
                }],
                loading: false,
                disabledButton: false,

            });
        } else {
            window.Conekta.setPublicKey("key_eYvWV7gSDkNYXsmr");
            window.Conekta.Token.create(this.state, this.successHandler, this.errorHandler);

        }


    }


    render() {
        if(this.state.loading){
            return <Load/>
        }
        return (
            <div className='card-panel'>
                <div className='title-card'>
                    <Link to="/credit"><KeyboardBackspaceIcon fontSize="large" /> </Link>
                </div>
                {this.state.messageRequest &&
                    <div className='col-xl-12 col-xs-12 col-md-12 col-sm-12'>
                        <div className='form-group'><br />
                            <RequestMessage attribute={this.state.messageRequest} />
                        </div><br />
                    </div>
                }
                <div id='PaymentForm' className='PaymentForm row'>
                    <div className='col col-12 col-md-6 pt-4'>
                        <Cards
                            cvc={this.state.card.cvc}
                            expiry={this.state.expiry}
                            focused={this.state.card.focus}
                            name={this.state.card.name}
                            number={this.state.card.number}
                        />
                    </div>
                    <div className='col col-12 col-md-6'>
                        <form id="card-form">
                            <div className="form-group">
                                <label htmlFor='number'>Número de tarjeta<span className='text-danger'>*</span></label>
                                <input
                                    type="text"
                                    name="number"
                                    id='number'
                                    maxlength="16"
                                    placeholder="Número de tarjeta de crédito o debito"
                                    onChange={this.handleInputChange}
                                    onFocus={this.handleInputFocus}
                                    data-conekta="card[number]"
                                    className="form-control input"
                                />
                                {this.state.error.accountError &&
                                    <label className='label-error'>{this.state.error.accountMessageError}</label>
                                }
                            </div>
                            <div className="form-group">
                                <label htmlFor='name'>Nombre <span className='text-danger'>*</span></label>
                                <input
                                    type="text"
                                    name="name"
                                    id='name'
                                    placeholder="Nombre"
                                    data-conekta="card[name]"
                                        onChange={this.handleInputChange}
                                        onFocus={this.handleInputFocus}
                                        className="form-control input"
                                />
                                {this.state.error.nameError &&
                                    <label className='label-error'>{this.state.error.nameMessageError}</label>
                                }
                            </div>
                            <label htmlFor='expiry'>Fecha de expiración (MM/AAAA) <span className='text-danger'>*</span></label>
                            <div className='row'>
                                <div className="form-group col col-4">
                                    <input
                                        type="text"
                                        name="exp_month"
                                        id='exp_month'
                                        placeholder="Mes"
                                        maxlength="2"
                                        onChange={this.handleInputChange}
                                        onFocus={this.handleInputFocus}
                                        data-conekta="card[exp_month]"
                                        className="form-control input"
                                    />
                                </div>
                                <div className="form-group col col-1 mt-3">
                                    <label>/</label>
                                </div>
                                    <div className="form-group col col-4">
                                        <input
                                            type="text"
                                            name="exp_year"
                                            id='exp_year'
                                            maxlength="4"
                                            placeholder="Año"
                                            onChange={this.handleInputChange}
                                            onFocus={this.handleInputFocus}
                                            data-conekta="card[exp_year]"
                                            className="form-control input"
                                        />
                                    </div>
                                    <div className="form-group col col-6">
                                        <label htmlFor='cvc'>CVC <span className='text-danger'>*</span></label>
                                        <input
                                            type="text"
                                            name="cvc"
                                            id='cvc'
                                            placeholder="CVC"
                                            maxlength="3"
                                            onChange={this.handleInputChange}
                                            onFocus={this.handleInputFocus}
                                            data-conekta="card[cvc]"
                                            className="form-control input"
                                        />
                                        {this.state.error.cvcError &&
                                            <label className='label-error'>{this.state.error.cvcMessageError}</label>
                                        }
                                    </div>
                                </div>
                            <div className="form-group">
                                <button disabled={this.state.disabledButton} onClick={this.handleSubmit} className='btn-dark btn-block'>Pagar</button>
                            </div>
                        </form>
                    </div>
                </div>
            </div>
        );
    }
}