import React from "react";

class CreditSupport extends React.Component {
    render() {
        return (
            <section className='container'>
                <div className='row'>
                    <div className='col col-12'>
                        <div className='card p-4 mt-4 mb-4'>
                            <h5>
                                Historial de Actividades de Administradores
                            </h5>
                            <div className='card-body'>
                                <div className='table-responsive'>
                                    <table className="table">
                                    <thead>
                                            <tr>
                                                <th scope="col">ID</th>
                                                <th scope="col">Usuario/modify</th>
                                                <th scope="col">Fecha</th>
                                                <th scope="col">Hora</th>
                                                <th scope="col">Acción/activitytype</th>
                                                <th scope="col">Detalles</th>
                                            </tr>
                                        </thead>
                                        <tbody>
                                            <tr>
                                                <th scope="row">1</th>
                                                <td>Nombre del personal</td>
                                                <td>Fecha</td>
                                                <td>Acción</td>
                                            </tr>
                                        </tbody>
                                    </table>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </section>
        );
    }
}

export default CreditSupport;