import React from "react";


const SelectCodeNumbers = ({ id, name, value, options, isDisabled, handleChange }) => {
  let numbersCode = !options ? [] : options.codeNumbers ? options.codeNumbers : options
  let flag = options.codeNumbers ? 'register' : 'profile'
  return (

    <div>
      <select className='input'
        id={id}
        name={name}
        value={(value.value==undefined) ? value!=null? value : 152 : value.value}
        disabled={(isDisabled == true) ? null : true}
        onChange={(e) => handleChange(e.target.name, e.target.value)}
      >
        {flag == 'register'
          ?
          numbersCode.map((elem, index) => (
            <option key={index} value={elem.id} >{elem.code}&nbsp;&nbsp;&nbsp;-&nbsp;&nbsp;&nbsp;{elem.name}  </option>
          ))
          :
          numbersCode.map((elem, index) => (
            <option key={index} value={elem.value} >{elem.label}  </option>
          ))
        }
      </select>
    </div>
  );
}

export default SelectCodeNumbers;