import React, { useEffect, useState } from "react";
import './credit.scss';
import { Link } from 'react-router-dom';
import KeyboardBackspaceIcon from '@material-ui/icons/KeyboardBackspace';
import api from '../../../api';
import Load from '../../../componets/Loading/Load';
import moment from 'moment';
import { useLocation } from "react-router-dom";



const CreditDetailPayments = () => {
    let credit_id = useLocation();
    useEffect(() => {
        getData();
    }, []);

    const [detailCreditsSchedule, setdetailCreditsSchedule] = useState('');
    const [plural_pago, setPlural_pago] = useState('pago');
    const [loading, setLoading] = useState(true);


    const getData = async () => {
        let id_credit = credit_id.state.id
        api.lists.showCreditSchedule(id_credit)
            .then(res => {
                setdetailCreditsSchedule(res);
                setLoading(false);
                if (res.num_of_payments > 1) {
                    setPlural_pago('pagos');
                }


            });
    };
    if (loading == true) {
        return <Load />
    } else {
        let next_schedule_payments = '';
        let next_payments2 = detailCreditsSchedule.schedule.next_payment ? detailCreditsSchedule.schedule.next_payment : [];
        next_schedule_payments = Object.values(next_payments2).map((next_payment) => {
            return (
                <tr className='text-center'>
                    <td>$ {next_payment.quantity}</td>
                    <td>$ {next_payment.total_interest}</td>
                    <td>$ {next_payment.total}</td>
                    <td>{next_payment.payment_state ? next_payment.payment_state.name : ''}</td>
                    <td>{moment(next_payment.due_date).format("DD-MM-YYYY")}</td>
                    <td> <Link to={{ pathname: '/payment_methods', state: { id: next_payment.schedule_id } }}>Pagar</Link></td>
                </tr>

            )
        })

        let late_schedule_payments = '';
        let late_payments2 = detailCreditsSchedule.schedule.late_payment ? detailCreditsSchedule.schedule.late_payment : [];

        late_schedule_payments = Object.values(late_payments2).map((late_payment) => {
            return (
                <tr className='text-center'>
                    <td>$ {late_payment.quantity}</td>
                    <td>$ {late_payment.total_interest}</td>
                    <td>$ {late_payment.total}</td>
                    <td>{late_payment.payment_state ? late_payment.payment_state.name : ''}</td>
                    <td>{moment(late_payment.due_date).format("DD-MM-YYYY")}</td>
                    <td> <Link to={{ pathname: '/payment_methods', state: { id: late_payment.schedule_id } }}>Pagar</Link></td>
                </tr>

            )
        })
        return (
            <React.Fragment>
                <div className='card-panel'>
                    <div className='title-card'>
                        <Link to="/credit"><KeyboardBackspaceIcon fontSize="large" /> </Link>
                        <h1 className='text-center'>Detalles del siguiente pago</h1>
                    </div>
                    <div className='body-card mi-credit'>
                        <div className='text-center mi-credit-amount'>
                            $ {detailCreditsSchedule.value} {detailCreditsSchedule.currency}
                        </div>
                        <div className='mi-credit-details text-center'>
                            <div className='mt-4'>
                                <h4 className='text-center'>{detailCreditsSchedule.credit_status}</h4>
                                <h4 className='text-center'>Folio: {detailCreditsSchedule.folio}</h4>
                            </div>
                        </div>
                        <div className='mi-credit-details'>
                            <div className='row'>
                                <div className='col-3 text-center'>
                                    <label>Pagos atrasados: {detailCreditsSchedule.schedule.tota_late_payments}</label>
                                </div>
                                <div className='col-3 text-center'>
                                    <label>Pagos hechos: {detailCreditsSchedule.schedule.total_payments_done}</label>
                                </div>
                                <div className='col-3 text-center'>
                                    <label>Pagos pendientes: {detailCreditsSchedule.schedule.total_pending_payments}</label>
                                </div>
                                <div className='col-3 text-center'>
                                    <label>Saldo Total del Crédito: {detailCreditsSchedule.balance.value} {detailCreditsSchedule.balance.currency}</label>
                                </div>
                            </div>
                        </div>
                        <div className='table-responsive'>
                            <table className="table">
                                <thead>
                                    <tr className='text-center'>
                                        <th scope="col">Monto</th>
                                        <th scope="col">Intereses Totales</th>
                                        <th scope="col">Monto Total</th>
                                        <th scope="col">Estado</th>
                                        <th scope="col">Vencimiento</th>
                                        <th scope="col">Realizar Pago</th>
                                    </tr>
                                </thead>
                                <tbody>
                                    {next_schedule_payments}
                                    {late_schedule_payments}
                                </tbody>
                            </table>
                        </div>
                    </div>
                </div>
            </React.Fragment>
        )
    }
};

export default CreditDetailPayments;