import React from 'react';
import { makeStyles, Theme, createStyles } from '@material-ui/core/styles';
import Modal from '@material-ui/core/Modal';
import Backdrop from '@material-ui/core/Backdrop';
import Fade from '@material-ui/core/Fade';
import CloseIcon from '@material-ui/icons/Close';


const useStyles = makeStyles((theme: Theme) =>
    createStyles({
        modal: {
            display: 'flex',
            alignItems: 'center',
            justifyContent: 'center',
        },
        paper: {
            backgroundColor: theme.palette.background.paper,
            border: 'none !important',
            borderRadius: '15px',
            width: '95%',
            maxWidth: '500px',
            boxShadow: theme.shadows[5],
            padding: theme.spacing(2, 4, 3),
        },
    }),
);

export default function TransitionsModal(props) {
    const classes = useStyles();
    const [open, setOpen] = React.useState(false);

    const handleOpen = () => {
        setOpen(true);
    };

    const handleClose = () => {
        setOpen(false);
    };

    return (
        <div>
            <div onClick={handleOpen}>
                <div className='simple-link'>
                    <p>Crédito ${props.infoCredits.credit_date.credit.value} {props.infoCredits.credit_date.credit.currency.iso} - {props.infoCredits.credit_date.num_date} {props.infoCredits.credit_date.num_date > 1 ? props.infoCredits.credit_date.date.plural_name : props.infoCredits.credit_date.date.name}</p>
                </div>
            </div>
            <Modal
                aria-labelledby="transition-modal-title"
                aria-describedby="transition-modal-description"
                className={classes.modal}
                open={open}
                onClose={handleClose}
                closeAfterTransition
                BackdropComponent={Backdrop}
                BackdropProps={{
                    timeout: 500,
                }}
            >
                <Fade in={open}>
                    <div className={classes.paper}>
                        <div className='data-modal'>
                        <div style={{ textAlign: "right" }}><button onClick={handleClose}><CloseIcon fontSize="large" /></button></div>
                            <div className='modal-header'>
                                <h3>Datos del crédito solicitado</h3>
                            </div>
                           <div className='modal-body'>
                               <ul style={{listStyle: "none"}}>
                                   <li>Monto del Crédito: <b>${props.infoCredits.credit_date.credit.value} {props.infoCredits.credit_date.credit.currency.iso}</b> </li>
                                   <li>Plazo a pagar: <b>{props.infoCredits.credit_date.num_date} {props.infoCredits.credit_date.num_date > 1 ? props.infoCredits.credit_date.date.plural_name : props.infoCredits.credit_date.date.name}</b></li>
                                   <li>% de interes: <b>{props.infoCredits.credit_date.interest} %</b></li>
                                   <li>Número de pagos: <b>{props.infoCredits.credit_date.num_of_payments}</b></li>
                                   <li>Estatus del Crédito: <b>{props.infoCredits.credit_state.name}</b></li>
                               </ul>
                           </div>
                        </div>
                    </div>
                </Fade>
            </Modal>
        </div>
    );
}