import React, { useEffect, useState } from "react";
import './credit.scss';
import { Link } from 'react-router-dom';
import KeyboardBackspaceIcon from '@material-ui/icons/KeyboardBackspace';
import api from '../../../api';
import Load from '../../../componets/Loading/Load';
import moment from 'moment';
import { useLocation } from "react-router-dom";

const CreditDetailPayments = () => {
    let credit_id = useLocation();
    useEffect(() => {
        getData();
    }, []);

    const [showHistoryCredit, setShowHistoryCredit] = useState('');
    const [loading, setLoading] = useState(true);


    const getData = async () => {
        let id_credit = credit_id.state.id
        api.lists.showHistoryCredit(id_credit)
            .then(res => {
                console.log(res)
                setShowHistoryCredit(res);
                setLoading(false);


            });
    };
    if (loading == true) {
        return <Load />
    } else {
        //PAGOS HECHOS
        let done_payments = '';
        console.log(showHistoryCredit)
        let done_payments2 = showHistoryCredit.schedule.payment_done ? showHistoryCredit.schedule.payment_done : [];
        console.log(done_payments2)
        done_payments = Object.values(done_payments2).map((done) => {
            console.log(done)
            return (
                <tr className='text-center' style={{ backgroundColor: '#BDECB6' }}>
                    <td >$ {done.quantity}</td>
                    <td>$ {done.total_interest}</td>
                    <td>$ {done.total}</td>
                    <td>{done.payment_state ? done.payment_state.name : ''}</td>
                    <td>{moment(done.due_date).format("DD-MM-YYYY")}</td>
                </tr>

            )
        })

        ////PAGOS NO HECHOS
        let no_done_payments = '';
        let no_done_payments2 = showHistoryCredit.schedule.no_payment ? showHistoryCredit.schedule.no_payment : [];
        console.log(no_done_payments2)
        no_done_payments = Object.values(no_done_payments2).map((noDone) => {
            console.log(noDone)
            return (
                <tr className='text-center' style={{ backgroundColor: '#FFB4BF' }}>
                    <td >$ {noDone.quantity}</td>
                    <td>$ {noDone.total_interest}</td>
                    <td>$ {noDone.total}</td>
                    <td>{noDone.payment_state ? noDone.payment_state.name : ''}</td>
                    <td>{moment(noDone.due_date).format("DD-MM-YYYY")}</td>
                </tr>

            )
        })

        ///PAGOS RECHAZADOS
        let rejected_payments = '';
        let rejected_payments2 = showHistoryCredit.schedule.reject_payment ? showHistoryCredit.schedule.reject_payment : [];
        console.log(done_payments2)
        rejected_payments = Object.values(rejected_payments2).map((reject) => {
            console.log(reject)
            return (
                <tr className='text-center' style={{ backgroundColor: '#FDFD96' }}>
                    <td >$ {reject.quantity}</td>
                    <td>$ {reject.total_interest}</td>
                    <td>$ {reject.total}</td>
                    <td>{reject.payment_state ? reject.payment_state.name : ''}</td>
                    <td>{moment(reject.due_date).format("DD-MM-YYYY")}</td>
                </tr>

            )
        })
        return (
            <React.Fragment>
                <div className='card-panel'>
                    <div className='title-card'>
                        <Link to="/credit"><KeyboardBackspaceIcon fontSize="large" /> </Link>
                        <h1 className='text-center'>Historial de Pagos</h1>
                    </div>
                    <div className='body-card mi-credit'>
                        <div className='text-center mi-credit-amount'>
                            $ {showHistoryCredit.value} {showHistoryCredit.currency}
                        </div>
                        <div className='mi-credit-details text-center'>
                            <div className='mt-4'>
                                <h4 className='text-center'>Folio: {showHistoryCredit.folio}</h4>
                            </div>
                        </div>
                        <div className='table-responsive'>
                            <table className="table">
                                <thead>
                                    <tr className='text-center'>
                                        <th scope="col">Monto</th>
                                        <th scope="col">Intereses</th>
                                        <th scope="col">Monto Total</th>
                                        <th scope="col">Estado</th>
                                        <th scope="col">Fecha</th>
                                    </tr>
                                </thead>
                                {done_payments.length == 0 && no_done_payments.length == 0 && rejected_payments.length == 0
                                    ?
                                    <tbody>
                                        <tr>
                                            <th colspan="5" className="text-center">
                                                <h4><span>No tienes historial de pagos</span></h4>
                                            </th>
                                        </tr>
                                    </tbody>
                                    :
                                    <tbody>
                                        {done_payments}
                                        {no_done_payments}
                                        {rejected_payments}
                                    </tbody>
                                }
                            </table>
                        </div>
                    </div>
                </div>
            </React.Fragment>
        )
    }
};

export default CreditDetailPayments;