import React from "react";

const InputFileValue = ( {attribute,fileKey, handleChange, param} ) => {
    return(
        <div>
          <input
            capture={attribute.camera}
            id={attribute.id}
            name={attribute.name}
            type={attribute.type}
            accept={attribute.accept}
            key={fileKey}
            onChange={(e) => handleChange(e)}
            className={param ? 'input-error' : 'input'}
          />
        </div>
    );
}

export default InputFileValue;