import React, { useState } from 'react';
import { makeStyles, Theme, createStyles } from '@material-ui/core/styles';
import Modal from '@material-ui/core/Modal';
import Backdrop from '@material-ui/core/Backdrop';
import Fade from '@material-ui/core/Fade';
import '../../../assets/css/style.scss';
import CloseIcon from '@material-ui/icons/Close';
import CheckBoxIcon from '@material-ui/icons/CheckBox';
import api from '../../../api';
import axios from 'axios';



const useStyles = makeStyles((theme: Theme) =>
    createStyles({
        modal: {
            display: 'flex',
            alignItems: 'center',
            justifyContent: 'center',
        },
        paper: {
            backgroundColor: theme.palette.background.paper,
            border: 'none !important',
            borderRadius: '15px',
            width: '95%',
            maxWidth: '800px',
            maxHeight: '600px',
            overflowY: 'scroll',
            scrollBehavior: 'smooth',
            boxShadow: theme.shadows[5],
            padding: theme.spacing(2, 4, 3),
        },
    }),
);

export default function TransitionsModal(props) {
    const classes = useStyles();
    const [open, setOpen] = React.useState(false);
    const [pathImages, setPathImages] = useState('');

    const handleOpen = () => {
        setOpen(true);
        setPathImages(api.base_url_img())

    };

    const handleClose = () => {
        setOpen(false);
    };


    if (props.infoClient.client_info != null) {
        return (
            <div>
                <div onClick={handleOpen}>
                    <div className='simple-link'>
                        <p>{props.infoClient.name} {props.infoClient.last_name}</p>
                    </div>
                </div>
                <Modal
                    aria-labelledby="transition-modal-title"
                    aria-describedby="transition-modal-description"
                    className={classes.modal}
                    open={open}
                    onClose={handleClose}
                    closeAfterTransition
                    BackdropComponent={Backdrop}
                    BackdropProps={{
                        timeout: 500,
                    }}
                >
                    <Fade in={open}>
                        <div className={classes.paper}>
                            <div className='data-modal'>
                                <div style={{ textAlign: "right" }}><button onClick={handleClose}><CloseIcon fontSize="large" /></button></div>
                                <div className='modal-header'>
                                    <h3>Datos del Cliente</h3>
                                </div>
                                <div className='modal-body row'>
                                    <div className='col col-4 mb-3'>
                                        <p>Correo Electrónico</p>
                                        <p className='input'>
                                            {props.infoClient.email ? props.infoClient.email : '-'}
                                        </p>
                                    </div>
                                    <div className='col col-4 mb-3'>
                                        <p>Nombre</p>
                                        <p className='input'>
                                            {props.infoClient.name ? props.infoClient.name : '-'}
                                        </p>
                                    </div>

                                    <div className='col col-4 mb-3'>
                                        <p>Apellidos</p>
                                        <p className='input'>
                                            {props.infoClient.last_name ? props.infoClient.last_name : '-'}
                                        </p>
                                    </div>
                                    <div className='col col-4 mb-3'>
                                        <p>Curp</p>
                                        <p className='input'>
                                            {props.infoClient.client_info.curp ? props.infoClient.client_info.curp : '-'}
                                        </p>
                                    </div>
                                    <div className='col col-4 mb-3'>
                                        <p>Código de País</p>
                                        <p className='input'>
                                            {props.infoClient.code_phone ? props.infoClient.code_phone.code + '  ' + props.infoClient.code_phone.name : "-"}
                                        </p>
                                    </div>
                                    <div className='col col-4 mb-3'>
                                        <p>Teléfono</p>
                                        <p className='input'>
                                            {props.infoClient.phone ? props.infoClient.phone : "-"}
                                        </p>
                                    </div>
                                    <div className='col col-4 mb-3'>
                                        <p>Nivel de cliente</p>
                                        <p className='input'>
                                            {props.infoClient.client_info.level.name ? props.infoClient.client_info.level.name : '-'}
                                        </p>
                                    </div>
                                    <div className='col col-4 mb-3'>
                                        <p>Ocupación</p>
                                        <p className='input'>
                                            {props.infoClient.client_info.profession.name ? props.infoClient.client_info.profession.name : '-'}
                                        </p>
                                    </div>
                                    <div className='col col-2 mb-3'>
                                        <p>Salario</p>
                                        <p className='input'>
                                            $ {props.infoClient.client_info.salary ? props.infoClient.client_info.salary : '-'}
                                        </p>
                                    </div>
                                    <div className='col col-6 mb-3'>
                                        <p>Dirección</p>
                                        <p className='input'>
                                            {props.infoClient.client_info.address ? props.infoClient.client_info.address : '-'}
                                        </p>
                                    </div>
                                    
                                </div>
                            </div>
                        </div>
                    </Fade>
                </Modal>
            </div>
        );
    } else {
        return (
            <div>
                <div onClick={handleOpen}>
                    <div className='simple-link'>
                        <p>{props.infoClient.name} {props.infoClient.last_name}</p>
                    </div>
                </div>
                <Modal
                    aria-labelledby="transition-modal-title"
                    aria-describedby="transition-modal-description"
                    className={classes.modal}
                    open={open}
                    onClose={handleClose}
                    closeAfterTransition
                    BackdropComponent={Backdrop}
                    BackdropProps={{
                        timeout: 500,
                    }}
                >
                    <Fade in={open}>
                        <div className={classes.paper}>
                            <div className='data-modal'>
                                <div style={{ textAlign: "right" }}><button onClick={handleClose}><CloseIcon fontSize="large" /></button></div>
                                <div className='modal-header'>
                                    <h3>Datos del Cliente</h3>
                                </div>
                                <div className='modal-body row'>
                                    <div className='col col-4 mb-3'>
                                        <p>Correo Electrónico</p>
                                        <p className='input'>
                                            {props.infoClient.email ? props.infoClient.email : '-'}
                                        </p>
                                    </div>
                                    <div className='col col-4 mb-3'>
                                        <p>Nombre</p>
                                        <p className='input'>
                                            {props.infoClient.name ? props.infoClient.name : '-'}
                                        </p>
                                    </div>

                                    <div className='col col-4 mb-3'>
                                        <p>Apellidos</p>
                                        <p className='input'>
                                            {props.infoClient.last_name ? props.infoClient.last_name : '-'}
                                        </p>
                                    </div>
                                    <div className='col col-4 mb-3'>
                                        <p>Curp</p>
                                        <p className='input'>
                                            -
                                         </p>
                                    </div>
                                    <div className='col col-4 mb-3'>
                                        <p>Código de País</p>
                                        <p className='input'>
                                            {props.infoClient.code_phone ? props.infoClient.code_phone.code + '  ' + props.infoClient.code_phone.name : "-"}  
                                        </p>
                                    </div>
                                    <div className='col col-4 mb-3'>
                                        <p>Teléfono</p>
                                        <p className='input'>
                                            {props.infoClient.phone ? props.infoClient.phone : "-"}
                                        </p>
                                    </div>
                                    <div className='col col-4 mb-3'>
                                        <p>Nivel de cliente</p>
                                        <p className='input'>
                                            -
                                        </p>
                                    </div>
                                    <div className='col col-4 mb-3'>
                                        <p>Ocupación</p>
                                        <p className='input'>
                                            -
                                        </p>
                                    </div>
                                    <div className='col col-2 mb-3'>
                                        <p>Salario</p>
                                        <p className='input'>
                                            -
                                        </p>
                                    </div>
                                    <div className='col col-6 mb-3'>
                                        <p>Dirección</p>
                                        <p className='input'>
                                            -
                                        </p>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </Fade>
                </Modal>
            </div>
        );
    }
}