import React from "react";
import './credit.scss';
import api from '../../../api';
import QueryBuilderIcon from '@material-ui/icons/QueryBuilder';
import RequestMessage from '../../../componets/Messages/RequestMessage';
import swal from 'sweetalert';
import { Link } from 'react-router-dom';
import Load from '../../../componets/Loading/Load';



class CreditPending extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            credits_requested:this.props.credits.data.credits_requested ? this.props.credits.data.credits_requested:[],
            messageRequest: false,
            loading:true,

        }
        this.abortController = new AbortController();
        this.signal = this.abortController.signal;
    }

    componentDidMount() {
        this.loading()
    }
    loading = () => {
        this.setState({
            ...this.state,
            loading: false,
        });
    }

    componentWillUnmount() {
        this.abortController.abort();
    }

    handleCancelCredit = (e, id) => {
        e.preventDefault();
        this.setState({
            ...this.state,
            loading: true,
        });
         api.delete.credit_cancel(id)
        .then((response) => {
                if (response.code == 500 && response.type == 'error') {
                    this.setState({
                        messageRequest: [{ label: response.message, id: 1 }],
                        loading: false,
                    });
                } else {
                    // swal({
                    //     title: "Correcto!",
                    //     text: String(response.message),
                    //     icon: "success",
                    //     timer: 2000,
                    //     button: true
                    // });
                    this.setState({
                        messageRequest: false,
                        loading: true,
                        
                    });
                    window.location = '/credit';
                }
            })
            .catch((error) => {
                console.log(error)
                this.setState({
                    loading: false,

                });
            })

    }
    render() {
        if(this.state.loading){
            return <Load/>
        }
        let credits_requested2=this.props.credits.data.credits_requested ? this.props.credits.data.credits_requested :[];
        let request_C='';
        if (this.props.credits.data.credits_requested!=undefined) {
            request_C = Object.values(this.props.credits.data.credits_requested).map((credit) => {
               return (
                   <div className='credit-details pending'>
                       <div className='row m-0'>
                           <div className='col col-2 col-md-1 detail-icon'>
                               <QueryBuilderIcon fontSize="large"/>
                           </div>
                           <div className='col col-10 col-md-11 detail-content'>
                               <div className='message'>
                               En espera de aprobación
                               
                               </div>
                               <div className='detail-amount'>$ {credit.value} {credit.currency}</div>
                               <p>
   
                               <Link  to={{ pathname: '/details_info_credit', state: { id: credit.id} }}>Ver Detalles  </Link>  | &nbsp;
   
                                 <button  
                                 onClick={(e)=>{ 
                                    swal({
                                        title: '¿Estás seguro que deseas cancelar este crédito?', 
                                        text:'Si cancelas no podrás solicitar un nuevo crédito sino hasta dentro de 30 días',
                                        icon: 'warning', 
                                        buttons: true, 
                                        dangerMode: true,
                                    })
                                    .then(willDelete => {
                                        if(willDelete) {this.handleCancelCredit(e,credit.id);}
                                        else {swal.close();}
                                    })
                                }}>Cancelar Crédito</button>
   
                               </p>
                               
                           </div>
                       </div><br/>
                   </div>
   
               )
   
           })
        }

        return (
            <React.Fragment>
                {credits_requested2.length!=0 &&
                
                   
                    <div>
                        { this.state.messageRequest &&
                        <div className='col-xl-12 col-xs-12 col-md-12 col-sm-12'>
                            <div className='form-group'><br />
                                <RequestMessage attribute={this.state.messageRequest} />
                            </div><br />
                        </div>}
                        {request_C}
                    </div>
                }
            </React.Fragment>
            
    );
  }
}

export default CreditPending;