import React, { useEffect, useState } from "react";
import './credit.scss';
import { Link } from 'react-router-dom';
import KeyboardBackspaceIcon from '@material-ui/icons/KeyboardBackspace';
import api from '../../../api';
import Load from '../../../componets/Loading/Load';




import { useLocation } from "react-router-dom";

const CreditDetail = () => {
    let credit_id = useLocation();
    useEffect(() => {
        getData();
    }, []);

    const [detailCreditsSchedule, setdetailCreditsSchedule] = useState('');
    const [plural_pago, setPlural_pago] = useState('pago');
    const [loading, setLoading] = useState(true);


    const getData = async () => {
        let id_credit = credit_id.state.id
        api.lists.showCreditSchedule(id_credit)
            .then(res => {
                console.log(res)
                setdetailCreditsSchedule(res);
                setLoading(false);
                if (res.num_of_payments > 1) {
                    setPlural_pago('pagos');
                }


            });
    };
    if (loading == true) {
        return <Load />
    } else {

        return (
            <React.Fragment>
                <div className='card-panel'>
                    <div className='title-card'>
                        <Link to="/credit"><KeyboardBackspaceIcon fontSize="large" /> </Link>
                        <h1 className='text-center'>Detalles de mi Crédito</h1>
                    </div>
                    <div className='body-card mi-credit'>
                        <div className='text-center mi-credit-amount'>
                            $ {detailCreditsSchedule.value} {detailCreditsSchedule.currency}
                        </div>
                        <div className='mi-credit-details text-center'>
                            <div className='mt-4'>
                                <h4 className='text-center'>{detailCreditsSchedule.credit_status}</h4>
                            </div>
                            <div className='mt-4'>
                                <p>Folio:</p>
                                <span className='bold mi-credit-data'>{detailCreditsSchedule.folio}</span>
                            </div>
                            <div className='mt-4'>
                                <p>Número de pagos:</p>
                                <span className='bold mi-credit-data'>{detailCreditsSchedule.num_of_payments} {plural_pago} en {detailCreditsSchedule.num_date} {detailCreditsSchedule.date_name} </span>
                            </div>
                            <div className='mt-4'>
                                <p>Pagos Realizados:</p>
                                <span className='bold mi-credit-data'>{detailCreditsSchedule.schedule.payments_done}</span>
                            </div>
                            <div className='mt-4'>
                                <p>Pagos Pendientes:</p>
                                <span className='bold mi-credit-data'>{detailCreditsSchedule.schedule.pending_payments}</span>
                            </div>
                            <div className='mt-4'>
                                <p>Monto a Pagar:</p>
                                <span className='bold mi-credit-data'>$ {detailCreditsSchedule.total_payment} </span>
                            </div>
                            <div className='mt-4'>
                                <p>% de interes:</p>
                                <span className='bold mi-credit-data'> {detailCreditsSchedule.interest} % </span>
                            </div>
                        </div>
                    </div>
                </div>
            </React.Fragment>
        )
    }
};

export default CreditDetail;