import React from 'react';
import HomeOne from '../../assets/images/home-1.png';
import HomeTwoo from '../../assets/images/home-2.png';
import HomeThree from '../../assets/images/home-3.png';
import HpmeFour from '../../assets/images/home-4.png';
class Information extends React.Component{

    render(){
        return(
            <section className='steps-information'>
                <h4 className='title-steps text-center'>¿Cómo Funciona?</h4>
                <div className='home-information'>
                    <div className='information-container'>
                        <div className='img-information'>
                            <img src={HomeOne} alt="Paso 1 - Resgístrate" />
                        </div>
                        <div className='title-information'>
                            <p className='step'>1</p>
                            <h4>Paso 1</h4>
                        </div>
                        <p>Registraté en CreditOk.</p>
                    </div>
                    <div className='information-container'>
                        <div className='img-information'>
                            <img src={HomeTwoo} alt="Paso 2 - Ingresa tú información" />
                        </div>
                        <div className='title-information'>
                            <p className='step'>2</p>
                            <h4>Paso 2</h4>
                        </div>
                        
                        <p>Ingresa tu Información Personal.</p>
                    </div>
                    <div className='information-container'>
                        <div className='img-information'>
                            <img src={HomeThree} alt="Paso 3 - Solicita tú crédito" />
                        </div>
                        <div className='title-information'>
                            <p className='step'>3</p>
                            <h4>Paso 3</h4>
                        </div>
                        <p>Solicita tú crédito y el plan a pagar.</p>
                    </div>
                    <div className='information-container'>
                        <div className='img-information'>
                            <img src={HpmeFour} alt="Paso 4 - Espera a que tú crédito se apruebe" />
                        </div>
                        <div className='title-information'>
                            <p className='step'>4</p>
                            <h4>Paso 4</h4>
                        </div>
                        <p>Espera a que tú crédito se apruebe.</p>
                    </div>
                </div>
            </section>
        );
    }
}

export default Information;