import React from "react";
import './credit.scss';
import Load from '../../../componets/Loading/Load';
import api from '../../../api';
import RequestMessage from '../../../componets/Messages/RequestMessage';
import swal from 'sweetalert';
import SendComent from '../../../componets/Modal/SendComent';


class ApplyForCredit extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            credit: this.props.data ? this.props.data : [],
            messageRequest: false,
            disabledButton: false,
            disabledButtonAll: false,
            loading: true,
            flag_contact: false,
            typeSup: {},

        }
        this.abortController = new AbortController();
        this.signal = this.abortController.signal;
    }

    componentDidMount() {
        this.getSupportTypes()
    }

    getSupportTypes = () => {
        api.lists.get_supportTypes()
            .then(res => {
                console.log(res)
                res.forEach(element => {
                    let typeS = 'type' + element.id
                    this.setState({
                        ...this.state,
                        typeSup: {
                            ...this.state.typeSup,
                            [typeS]: { label: element.name, id: element.id }
                        },
                    loading: false,


                    });
                });


            });
    }

    componentWillUnmount() {
        this.abortController.abort();
    }
    handleRadioChange = (e, id) => {
        this.setState({
            ...this.state,
            credit_select: e.target.value,
            disabledButton: id,
        });
    }

    handleSubmitCredit = (e) => {
        e.preventDefault();
        this.setState({
            ...this.state,
            loading: true,
            disabledButtonAll: true,
        });
        const body = {
            credit_date_id: this.state.credit_select
        }
        console.log(body)
        api.create.new_credit(body)
            .then((response) => {
                console.log(response)
                if ((response.code == 500 && response.type == 'error')|| (response.code == 403 && response.type == 'error')) {
                    this.setState({
                        messageRequest: [{ label: response.message, id: 1 }],
                        loading: false,
                        disabledButtonAll: true,
                        flag_contact: response.flag_contact,
                    });
                } else {
                    window.location = '/credit';

                    this.setState({
                        messageRequest: false,
                        disabledButton: true,
                        credit_select: '',
                        loading: true,

                    });

                }
            })
            .catch((error) => {
                console.log(error)
                this.setState({
                    ...this.state,
                    loading: false,
                });
            })
    }

    render() {
        if (this.state.loading) {
            return <Load />
        }
        const infoUser = JSON.parse(localStorage.getItem("user"))
        let card_credit = '';
        if (this.props.data != undefined) {
            card_credit = Object.values(this.props.data).map((info) => {
                let credit_date_info = '';
                if (info.credit_date) {
                    credit_date_info = Object.values(info.credit_date).map((item) => {
                        let pago = 'pago';
                        if (item.num_of_payments != 1) {
                            pago = 'pagos';
                        }
                        return (
                            <div className='credit-option'>
                                <input
                                    type="radio"
                                    value={item.id}
                                    checked={this.state.credit_select == item.id ? true : false}
                                    onChange={(e) => { this.handleRadioChange(e, item.credit_id) }}
                                />
                                <label className='ml-2'>  {item.num_of_payments} {pago} de $ <b>{item.total_payment}</b> en {item.num_date} {item.date_name}</label>
                                <hr className='m-0' />
                            </div>
                        )
                    })
                }
                return (
                    <React.Fragment>
                        <form className='col col-12 col-md-6 col-xl-4'>
                            <div className='credit-amount'>
                                <div className='amount'>{info.value} {info.currency.iso}</div>
                                <div>{credit_date_info}</div>
                                <div className='text-center mt-2 mb-2'>
                                    {/* <Link className='btn-dark' to='/credit_details'>Solicitar Crédito</Link> */}
                                    <button className='btn-dark btn-block' disabled={(this.state.disabledButton == info.id ? false : true) || (this.state.disabledButtonAll == true ? true : false)} onClick={this.handleSubmitCredit} >Solicitar Crédito</button>
                                </div>
                            </div>
                        </form>
                    </React.Fragment>
                )
            })
        }

        return (
            <React.Fragment>
                <div className='card-panel p-4'>
                    {this.state.messageRequest &&
                        <div className='title-card'>
                            <div className='row'>
                                <div className='col col-12 col-md-9 form-group'><br />
                                    <RequestMessage attribute={this.state.messageRequest} />
                                </div>
                                {(this.state.messageRequest != false && this.state.flag_contact == true) &&
                                    <div className='col col-12 col-md-3 form-group'><br />
                                        <SendComent text='Contactar a Soporte' type_S={this.state.typeSup['type3']} />
                                    </div>

                                }
                                <br />
                            </div>
                        </div>
                    }
                    <div className='title-card row'>
                        <div className='col col-12 col-md-7 col-xl-9'>
                            <h1>Solicitar Crédito</h1>
                        </div>
                        <div className='col col-12 col-md-5 col-xl-3'>
                            {(infoUser.level != "Nivel 1" && infoUser.level != "Nivel 2") &&
                                <SendComent text='Solicitar más Crédito' type_S={this.state.typeSup['type2']} />
                            }
                        </div>
                    </div>
                    <div className="row">
                        {card_credit}
                    </div>
                </div>
            </React.Fragment>
        );
    }
}

export default ApplyForCredit;