import React from "react";
import InfoClientBasic from '../Requests/InfoClienteBasic';
import Load from '../../../componets/Loading/Load';
import api from '../../../api';
import moment from 'moment';
import MessagesSupport from '../Requests/InfoMessageSupport';
import { Link } from 'react-router-dom';
import KeyboardBackspaceIcon from '@material-ui/icons/KeyboardBackspace';


class MessageReadGeneralSupports extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            messages_general_support_read: [],
            loading: true,
        }
        this.abortController = new AbortController();
        this.signal = this.abortController.signal;
    }
    componentDidMount() {
        this.getMessageReadGeneralSupports()
    }
    getMessageReadGeneralSupports = () => {
        api.lists.getMessageReadGeneralSupport(this.signal)
            .then(res => {
                console.log(res)
                this.setState({
                    ...this.state,
                    messages_general_support_read: res,
                    loading: false,
                });
            });
    }

    render() {
        if (this.state.loading) {
            return <Load />
        }
        let messages = '';
        let messagesAll = this.state.messages_general_support_read ? this.state.messages_general_support_read : [];

        messages = Object.values(messagesAll).map((info) => {
            return (
                <tr key={info.id} className='text-center'>
                    <td>{info.id}</td>
                    <td><InfoClientBasic infoClient={info.user} /></td>
                    <td>{info.support_type.name}</td>
                    <td><MessagesSupport messaje={info.message} /></td>
                    <td>{moment(info.created_at).format("DD-MM-YYYY")}</td>
                    <td className="text-success">Leído</td>
                </tr>
            )

        })
        return (
            <section className='container'>
                <div className='row'>
                    <div className='col col-12'>
                        <div className='card p-4 mt-4 mb-4'>
                            <div className="col col-12">
                                <div className='row'>
                                    <div className='col-2'>
                                        <Link to="/general_support"><KeyboardBackspaceIcon fontSize="large" /> </Link>
                                    </div>
                                    <div className='col-10'>
                                        <h5>Mensajes Leídos de Soporte</h5>
                                    </div>
                                </div>
                            </div>
                            <div className='card-body'>
                                <div className='table-responsive'>
                                    <table className="table">
                                        <thead>
                                            <tr className='text-center'>
                                                <th scope="col">ID</th>
                                                <th scope="col">Cliente</th>
                                                <th scope="col">Asunto</th>
                                                <th scope="col">Mensaje</th>
                                                <th scope="col">Fecha</th>
                                                <th scope="col">Estaus</th>
                                            </tr>
                                        </thead>
                                        <tbody>
                                            {this.state.messages_general_support_read.length == 0
                                                ?
                                                <tr className='text-center'>
                                                    <td colSpan="6">No hay Mensajes</td>
                                                </tr>
                                                :
                                                messages
                                            }
                                        </tbody>
                                    </table>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </section>
        );
    }
}

export default MessageReadGeneralSupports;