import React from "react";
import './credit.scss';
import CheckCircleOutlineIcon from '@material-ui/icons/CheckCircleOutline';
import { Link } from 'react-router-dom';
import Load from '../../../componets/Loading/Load';



class CreditTransfered extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            credits_transfered: this.props ? this.props.credits.data.credits_transfered : [],
        }
        this.abortController = new AbortController();
        this.signal = this.abortController.signal;
    }

    componentDidMount() {
        this.loading()
    }
    loading = () => {
        this.setState({
            loading: false,
        });
    }

    componentWillUnmount() {
        this.abortController.abort();
    }
    render() {
        if(this.state.loading){
            return <Load/>
        }
        let credits_transfered2=this.props.credits.data.credits_transfered ? this.props.credits.data.credits_transfered :[];
        let request_T='';
        if (this.props.credits.data.credits_transfered) {
            request_T = Object.values(this.props.credits.data.credits_transfered).map((credit) => {
               return (
                   <div className='credit-details approved'>
                       <div className='row m-0'>
                           <div className='col col-2 col-md-1 detail-icon'>
                               <CheckCircleOutlineIcon fontSize="large" />
                           </div>
                           <div className='col col-10 col-md-11 detail-content'>
                               <div className='message'>Crédito Aprobado y Transferido</div>
                               <div className='detail-amount'>$ {credit.value} {credit.currency}</div>
                               <div><Link  to={{ pathname: '/credit_details', state: { id: credit.id} }}>Ver Detalles</Link> | &nbsp;
                               <Link  to={{ pathname: '/credit_payments', state: { id: credit.id} }}>Pagos</Link> |&nbsp;
                               <Link  to={{ pathname: '/credit_history', state: { id: credit.id} }}>Historial de pagos </Link></div>
                           </div>
                       </div><br />
                   </div>
   
               )
   
           })
        }
        return (
            <React.Fragment>
                {credits_transfered2.length!=0 &&
                    <div>
                        {request_T}
                    </div>
                }
            </React.Fragment>
            

        );
    }
}

export default CreditTransfered;