import React from "react";
import ApplyForCredit from './ApplyForCredit';
import CreditHistory from './CreditHistory';
import MyCredits from './MyCredits';
import api from '../../../api';
import Load from '../../../componets/Loading/Load';


class Credit extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            credits: [],
            creditAvailable: [],
            loading: true,
        }
        this.abortController = new AbortController();
        this.signal = this.abortController.signal;
    }
    componentDidMount() {
        this.getCredits()
        this.getCreditAvailable()
    }
    getCredits = () => {
        api.lists.getCredits(this.signal)
            .then(res => {
                console.log(res)
                this.setState({
                    ...this.state,
                    credits: res,
                });
            });
    }

    getCreditAvailable = () => {
        api.lists.creditsAvailable(this.signal)
            .then(res => {
                this.setState({
                    ...this.state,
                    creditAvailable: res,
                    loading:false,
                })
            });
    }

    componentWillUnmount() {
        this.abortController.abort();
    }
    render() {
        if(this.state.loading){
            return <Load/>
        }
        return (
            <div>
                <ApplyForCredit data={this.state.creditAvailable}/>
                <MyCredits data={this.state.credits}/>
                <CreditHistory data={this.state.credits}/>
            </div>
            
        );
    }
}

export default Credit;