import React from 'react';
import { makeStyles, Theme, createStyles } from '@material-ui/core/styles';
import Modal from '@material-ui/core/Modal';
import Backdrop from '@material-ui/core/Backdrop';
import Fade from '@material-ui/core/Fade';
import './modal.scss';

const useStyles = makeStyles((theme: Theme) =>
    createStyles({
        modal: {
            display: 'flex',
            alignItems: 'center',
            justifyContent: 'center',
        },
        paper: {
            backgroundColor: theme.palette.background.paper,
            border: 'none !important',
            borderRadius: '15px',
            width: '95%',
            maxWidth: '500px',
            boxShadow: theme.shadows[5],
            padding: theme.spacing(2, 4, 3),
        },
    }),
);

export default function TransitionsModal() {
    const classes = useStyles();
    const [open, setOpen] = React.useState(false);

    const handleOpen = () => {
        setOpen(true);
    };

    const handleClose = () => {
        setOpen(false);
    };

    return (
        <div>
            <button className='simple-link' type="button" onClick={handleOpen}>
                Políticas de Privacidad
            </button>
            <Modal
                aria-labelledby="transition-modal-title"
                aria-describedby="transition-modal-description"
                className={classes.modal}
                open={open}
                onClose={handleClose}
                closeAfterTransition
                BackdropComponent={Backdrop}
                BackdropProps={{
                timeout: 500,
                }}
            >
                <Fade in={open}>
                    <div className={classes.paper}>
                        <div className='modal-width'>
                            <p>
                                <b>Fecha de vigencia:</b> 2 de junio de 2020
                                <br/><br/>
                                <b>¿QUIÉN ES RESPONSABLE DE TUS DATOS PERSONALES?</b>
                                <br/><br/>
                                <b>IMPUSLO FX S.A. DE C.V.</b>, (en lo sucesivo, “Creditok”, “nosotros”,
                                “nuestros”) con domicilio en Calle 69 #550c x 66 y 68 Centro, 97000,
                                Mérida, Yucatán, al momento de recabar tus datos personales (como se
                                define más adelante), es responsable del uso y protección de los
                                mismos.
                                <br/><br/>
                                <b>¿QUÉ DATOS PERSONALES USAMOS?</b>
                                <br/><br/>
                                Creditok recabará de ti (en adelante el “Titular” o “tu”) los siguientes
                                datos personales (en lo sucesivo los “datos” o los “datos personales”) en
                                tu  calidad de uno de nuestros clientes: (i) datos de contacto; (ii) datos de
                                identificación; (iii) datos de localización; (iv) datos laborales y educativos;
                                (v) datos de comunicación; (vi) datos de tu teléfono celular o dispositivo
                                móvil; (vii) lista de contactos, mensajes de texto que envías y frecuencia
                                de llamada de tu teléfono móvil; (viii) datos patrimoniales y financieros,
                                según aplique; (ix) datos que proporciones en tu solicitud de préstamo;
                                (x) datos transaccionales de las operaciones que realizas con nosotros o
                                a través de nosotros, así como de las operaciones que realizas con
                                transmisores de dinero y otras instituciones financieras o comercios
                                electrónicos; y (xi) datos de uso de la aplicación de Creditok.
                                <br/><br/>
                                <b>¿USAMOS DATOS PERSONALES SENSIBLES?</b>
                                <br/><br/>
                                Creditok no recaba datos personales sensibles de ti.
                                <br/><br/>
                                <b>¿CÓMO OBTENEMOS Y ACTUALIZAMOS TUS DATOS?</b>
                                <br/><br/>
                                Los datos personales serán obtenidos y actualizados a través de:
                                <br/><br/>
                                <ol className='abc-lower'>
                                    <li>Ti, cuando bajas la aplicación de Creditok y/o nos solicitas un
                                        préstamo;</li>
                                    <li>Tus otras interacciones con nosotros, incluyendo datos personales
                                        que voluntariamente compartes con nuestro departamento de
                                        atención al cliente o con nuestros empleados y agentes;</li>
                                    <li>Tu teléfono celular o dispositivo móvil;</li>
                                    <li>Buros de crédito o agencias para prevenir el fraude;</li>
                                    <li>Cookies, web beacons y otros medios remotos de recolección
                                        automática de datos;</li>
                                    <li>Transmisores de dinero y otras instituciones financieras y
                                        comercios electrónicos, con tu consentimiento, cuando sea
                                        necesario; y/o</li>
                                    <li>Terceros y otras fuentes públicas, con tu consentimiento, cuando
                                        sea necesario.</li>
                                </ol>
                                <br/>
                                <b>¿PARA QUÉ USAMOS TUS DATOS?</b>
                                <br/><br/>
                                Las finalidades primarias (en lo sucesivo, las “Finalidades Primarias”) que
                                dan origen y son necesarias para el mantenimiento y cumplimiento de la
                                relación jurídica entre Creditok y tu son las siguientes:
                                <br/><br/>
                                <ol className='abc-lower'>
                                    <li>Llevar a cabo la identificación y el conocimiento mediante
                                recolección de datos personales, así como la integración de tu
                                expediente que se mantendrá en la infraestructura tecnológica y
                                sistemas de información de Creditok y en otros archivos;</li>
                                    <li>Incorporar los datos personales en instrumentos jurídicos, dar
                                cumplimiento a los contratos que celebres con Creditok,
                                incluyendo, pero no limitado a cierto contrato de usuario entre el
                                Titular y Creditok (como el mismo se modifique de tiempo en
                                tiempo, el “Contrato del Usuario”) y administrar y manejar dichos
                                contratos, incluyendo sin limitar, el Contrato de Usuario;</li>
                                    <li>Ceder o transmitir a un tercero, mediante cualquier medio, los
                                derechos y/u obligaciones derivadas de los contratos antes
                                señalados;</li>
                                    <li>Utilizar los datos personales, en cualquier tipo de acto o diligencia
                                de cobranza judicial o extrajudicial;</li>
                                    <li>Cumplir con todas las leyes, reglamentos y disposiciones de
                                carácter general aplicables;</li>
                                    <li>Prospección y evaluación de los datos personales para crear y
                                asignar al Titular, una calificación crediticia o grado de calificación
                                similar;</li>
                                    <li>Obtener datos personales para mejorar los modelos crediticios de
                                Creditok, sus modelos de datos y otros servicios; y</li>
                                    <li>Originar, dar servicio, mantener y exigir la deuda del Titular.</li>
                                </ol>
                                <br/>
                                Las finalidades secundarias (en lo sucesivo, las “Finalidades
                                Secundarias”) que no son necesarias para el mantenimiento y
                                cumplimiento de la relación jurídica entre Creditok y tú, son las
                                siguientes:
                                <br/><br/>
                                <ol>
                                    <li>Envío de noticias, invitaciones a eventos de Creditok y de ONSPORT
                                DEPORTES S.A. DE C.V. y/o sus empresas subsidiarias y afiliadas;</li>
                                    <li>Realización de encuestas para mejorar nuestro servicio; y</li>
                                    <li>La mercadotecnia, publicidad y/o promoción de los productos y/o
                                servicios que sean ofrecidos por Creditok al Titular, por cualquier
                                medio material y/o electrónico.</li>
                                </ol>
                                <br/>
                                Tu consentimiento es necesario para las finalidades secundarias. Si no
                                estás de acuerdo con el uso de tus datos de contacto para dichas
                                finalidades secundarias, envíanos un correo a  <a href='mailto:soporte@creditok.mx'>soporte@creditok.mx</a>.
                                <br/>
                                Esperaremos cinco días hábiles antes de usar tus datos personales para
                                finalidades secundarias.
                                <br/><br/>
                                <b>¿CON QUIÉN COMPARTIMOS SUS DATOS?</b>
                                <br/><br/>
                                Los datos personales se transferirán a nivel nacional e internacional, a las
                                personas o entidades ubicadas en los siguientes tipos, categorías o
                                sectores, con las siguientes finalidades:
                                <br/><br/>
                                <b>A.</b> ONSPORT DEPORTES S.A. DE C.V., a otras sociedades controladoras,
                                subsidiarias o afiliadas, o a una sociedad matriz o a cualquier sociedad
                                del mismo grupo de  Creditok, que operen bajo los mismos procesos y
                                políticas internas, con la finalidad de:
                                <br/><br/>
                                <ol className='roman-style'>
                                    <li>Llevar a cabo la identificación y el conocimiento mediante
                                recolección de datos personales del Titular, así como la integración
                                de los datos personales en la infraestructura tecnológica de
                                Creditok, así como en sus sistemas de información y otros
                                archivos;</li>
                                    <li>Incorporar los datos personales del Titular en instrumentos
                                jurídicos, dar cumplimiento a las obligaciones del Titular bajo los
                                contratos celebrados entre el Titular y Creditok, incluyendo de
                                manera enunciativa, más no limitativa, el Contrato de Usuario, así
                                como para administrar y manejar dichos contratos, incluyendo, de
                                forma enunciativa, más no limitativa, el Contrato de Usuario;</li>
                                    <li> Ceder o transmitir a un tercero, mediante cualquier medio, los
                                derechos y/u obligaciones derivadas de los contratos antes
                                señalados;</li>
                                    <li>Utilizar los datos personales, en cualquier tipo de acto o diligencia
                                de cobranza judicial o extrajudicial;</li>
                                    <li>Cumplir con todas las leyes, reglamentos y disposiciones de
                                carácter general aplicables;</li>
                                    <li>Prospectar y evaluar los datos personales para crear y asignar al
                                titular una calificación crediticia o grado de calificación similar.</li>
                                    <li>Obtener información para mejorar nuestro modelo crediticio, de
                                datos y de servicios;</li>
                                    <li>Originar, dar servicio, mantener y exigir la deuda del Titular.</li>
                                </ol>
                                <br/>
                                <b>B.</b>  A autoridades nacionales y extranjeras competentes, con la finalidad
                                de dar cumplimiento a alguna ley, reglamento o disposición legal
                                aplicable.
                                <br/><br/>
                                Asimismo,  y de acuerdo con las leyes vigentes, Creditok deberá, con el
                                objeto de cumplir con las Finalidades Primarias descritas en el presente,
                                transferir de tiempo en tiempo, información de crédito del Titular a las
                                Sociedades de Información Crediticia o Burós de Crédito, y en este
                                sentido, al aceptar el presente Aviso de Privacidad (en lo sucesivo, este
                                “Aviso”) y al consentir a la celebración del Contrato de Usuario, usted 
                                confirma que podemos transmitir su información a dichas Sociedades de
                                Información Crediticia o Burós de Crédito, incluyendo información sobre
                                el incumplimiento a los contratos celebrados entre el Titular y Creditok,
                                entre otros el Contrato del Usuario.
                                <br/><br/>
                                <b>CLÁUSULA PARA CONSENTIR LA TRANSFERENCIA</b>
                                <br/><br/>
                                Por favor considerar que no será necesario tener tu consentimiento para
                                llevar a cabo: (i) la transferencia indicada en los incisos A y B de la
                                sección anterior, por encontrarse en el supuesto establecido en la
                                fracción IV del artículo 37 de la Ley Federal de Protección de Datos
                                Personales en Posesión de los Particulares y (ii) aquellas transferencias
                                que se encuentren dentro de los supuestos establecidos en el Artículo 37
                                de la Ley Federal de Protección de Datos Personales en Posesión de los
                                Particulares.
                                <br/><br/>
                                <b>TUS DERECHOS ARCO</b>
                                <br/><br/>
                                Tú, por sí o mediante tu representante legal debidamente acreditado,
                                puedes ejercer tus derechos de acceso, rectificación, cancelación u
                                oposición al tratamiento de tus datos personales (los “Derechos ARCO”).
                                Para ello podrás solicitar un formato de ejercicio de Derechos ARCO
                                mediante el envío de un correo electrónico a la dirección <a href='mailto:soporte@creditok.mx'>soporte@creditok.mx</a>, en atención 
                                de Departamento de Protección de
                                Datos Personales/Equipo de Atención al Cliente, al número telefónico <span className='yellow'>+52
                                5541703510</span> o bien a la dirección de Creditok indicada en este Aviso  con
                                el nombre y los datos de contacto del Titular.
                                <br/><br/>
                                El formato de ejercicio de Derechos ARCO se deberá llenar, firmar y
                                presentarse acompañado de la siguiente documentación, a fin de que
                                pueda llevarse a cabo la autenticación de usted:
                                <br/><br/>
                                <ol>
                                    <li>Tu Identificación oficial vigente (Credencial del Instituto Nacional
                                Electoral, Pasaporte, Cartilla del servicio Militar Nacional o Cédula
                                Profesional).</li>
                                    <li>En los casos en que el ejercicio de los Derechos ARCO se realice a
                                través de tu representante legal, deberá acompañarse la
                                identificación oficial del representante, así como el poder
                                correspondiente protocolizado que acredite la representación legal
                                conferida por el Titular.</li>
                                    <li>Cuando se quiera ejercer el derecho de rectificación, deberá
                                exhibirse la documentación que acredite el cambio solicitado de
                                acuerdo a los datos personales a rectificar.</li>
                                </ol>
                                <br/>
                                Creditok responderá a dicho formato después de 20 días hábiles
                                contados a partir de la fecha en que fue recibido el mismo. Este plazo
                                podrá ser ampliado hasta por 20 días hábiles más, cuando el caso lo
                                amerite, previa notificación a usted. Creditok le comunicará la resolución
                                adoptada a través de las opciones elegidas por éste en el formato de
                                ejercicio de Derechos ARCO.
                                <br/><br/>
                                <b>PUEDES NEGARTE O REVOCAR EL CONSENTIMIENTO PARA TRATAR TUS DATOS</b>
                                <br/><br/>
                                En caso de que desees revocar tu consentimiento o negarte al
                                tratamiento de tus datos personales para las Finalidades Secundarias,
                                con posterioridad a la firma y aceptación del presente Aviso, deberás
                                enviar un correo electrónico a la dirección indicada en la sección anterior
                                al Departamento de Protección de Datos Personales/Equipo de Atención
                                al Cliente, <a href='mailto:soporte@creditok.mx'>soporte@creditok.mx</a>, siguiendo el procedimiento para la
                                Negativa al Tratamiento y Revocación del Consentimiento que se
                                describe en este Aviso.
                                <br/><br/>
                                De acuerdo con lo anterior, el Departamento de Protección de Datos
                                Personales/Equipo de Atención al Cliente es el encargado de atender
                                todas las solicitudes de ejercicio de los derechos ARCO (acceso,
                                rectificación, cancelación u oposición) mismo que puedes contactar en
                                caso de cualquier duda o solicitud con respecto a tus datos personales a
                                través del correo electrónico y teléfono antes indicados.
                                <br/><br/>
                                <b>¿QUÉ MEDIOS TIENES PARA LIMITAR EL USO Y DIVULGACIÓN DE DATOS PERSONALES?</b>
                                <br/><br/>
                                Creditok ha creado un listado de exclusión para personas que no deseen
                                recibir publicidad de Creditok. Si recibes publicidad de Creditok y no
                                deseas seguir recibiéndola, podrás enviar un correo electrónico con tu
                                nombre completo a <a href='mailto:soporte@creditok.mx'>soporte@creditok.mx</a>, solicitando tu eliminación de la
                                lista de distribución con base en la cual se lleva a cabo el envío de la
                                información o publicidad. En el mismo correo electrónico podrás solicitar
                                más datos acerca de este listado.
                                <br/><br/>
                                Puedes también inscribirte en el Registro para Evitar la Publicidad de la
                                Procuraduría Federal del Consumidor. Para mayor información,
                                visite <a href='https://www.profeco.gob.mx'>www.profeco.gob.mx</a>.
                                <br/><br/>
                                <b>CAMBIOS AL AVISO DE PRIVACIDAD</b>
                                <br/><br/>
                                El presente Aviso se encuentra publicado para su consulta, en la página
                                de Internet de  Creditok (www.creditok.mx). De igual forma, las
                                modificaciones que en cualquier momento se hagan al presente Aviso,
                                serán publicadas en dicha dirección.
                                <br/><br/>
                                Al hacer clic en el botón “ACEPTAR” al calce, el Titular otorga su
                                consentimiento expreso en términos del presente Aviso y por ende,
                                consiente que sus Datos Personales sean tratados por Creditok
                                conforme a lo previsto en el presente Aviso, destinados para las
                                finalidades indicadas en el presente y transferidos a los terceros
                                establecidos aquí indicados.
                                <br/><br/>
                                <b>DECISIONES AUTOMATIZADAS</b>
                                <br/><br/>
                                Tomamos algunas decisiones en relación con nuestros productos y
                                servicios en forma automatizada sin intervención de una persona física
                                en la valoración.
                                <br/><br/>
                                <b>I. Decisiones de otorgar préstamos</b>
                                <br/><br/>
                                Cuando solicitas un préstamo, usaremos un proceso automatizado para
                                decidir si prestamos dinero y/o para toma de decisiones respecto a tu
                                elegibilidad para nuestros productos o servicios.
                                <br/><br/>
                                Nuestros procesos de revisión y otorgamiento de crédito utilizan
                                inteligencia artificial para procesar tus datos personales y valorar tu
                                capacidad crediticia. El tratamiento de tus datos personales es
                                automatizado sin intervención o valoración humana. El uso de dichos
                                procesos automatizados para verificar tu capacidad crediticia significa
                                que podemos automáticamente decidir que no eres elegible para un
                                préstamo o para un plazo o condiciones particulares. Nuestros modelos
                                de suscripción y otorgamiento de crédito son probados regularmente
                                para asegurarnos que son justos, exactos y sin prejuicios.
                                <br/><br/>
                                <b>II. Detección de fraudes</b>
                                <br/><br/>
                                Creditok también utiliza procesos automatizados para detectar, combatir
                                y prevenir fraudes. Nuestros modelos de detección de fraudes
                                automáticamente deciden si una persona puede significar riesgo de
                                fraude o lavado de dinero. Si nuestros modelos de detección de fraude al
                                procesar una transacción o aprobar un crédito detectan riesgo, es posible
                                que el acceso a la persona se rehuse.
                                <br/><br/>
                                <b>II. Tus derechos</b>
                                <br/><br/>
                                Si solicitas un crédito y eres rechazado basado en una decisión
                                automatizada, tienes derecho a acceder a tu información y rectificarla
                                contactándonos como se establece en la sección Derechos ARCO.
                            </p>
                        </div>
                    </div>
                </Fade>
            </Modal>
        </div>
    );
}