import React from 'react';
import { NavLink } from 'react-router-dom';
import '../../componets/Buttons/buttons.scss';

class Sidebar extends React.Component{

  render(){
    return(
      <div className='sidebar d-flex align-content-center'>
        <div>
        <div className='sidebar-title'>
            <h3 className='text-center'>Solicítalo Ahora</h3>
        </div>

        <div className='sidebar-text'>
            <p className='text-center'>
                Un crédito que funciona para cualquier emergencia
            </p>
        </div>
        

        <div className='justify-center'>
            <NavLink className='btn-dark' to="/register">¡Solicitar mi crédito ahora!</NavLink>
        </div>

        <div className='login-btn-sidebar'>
            <p className='text-center mt-2'>
                ¿Ya tienes cuenta? <NavLink className='link' to="/login">Iniciar Sesión</NavLink>
            </p>
        </div>
        </div>
      </div>
    );
  }
}

export default Sidebar;