import React from "react";
import InputValue from './../../../componets/Input/InputValue';
import EditIcon from '@material-ui/icons/Edit';
import DeleteForeverIcon from '@material-ui/icons/DeleteForever';
import TurnedInNotIcon from '@material-ui/icons/TurnedInNot';
import api from '../../../api';

import isAccountNumbers from '../../../componets/Validator/AccountNumbersValidate';
import isClabe from '../../../componets/Validator/ClabeValidate';
import isBank from '../../../componets/Validator/BankValidate';
import Load from '../../../componets/Loading/Load';
import RequestMessage from '../../../componets/Messages/RequestMessage';
import Select from '../../../componets/Select/Select';
import CheckBoxIcon from '@material-ui/icons/CheckBox';


class BankAccounts extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            accounts: {},
            loading: true,
            isEdit: false,
            default_account: false,
            account_number: '',
            clabe: '',
            bank_id: '',
            accountError: false,
            clabeError: false,
            messageRequest: false,
            loading: true,
            disabledButton: false,
            disabledButtonDefault: false,
            disabledButtonDelete: false,
            disabledButtonUpdate: false,
            banks: [],

        }
        this.abortController = new AbortController();
        this.signal = this.abortController.signal;
    }
    componentDidMount() {
        this.fetchAccount()
    }

    fetchAccount = () => {
        this.setState({ loading: true });
        api.lists.profile(this.signal)
            .then(res => {
                console.log(res)
                let banks = [];
                res.banks.forEach(element => {
                    banks.push({
                        label: element.name,
                        value: element.id
                    });
                });
                console.log(banks)
                this.setState({
                    ...this.state,
                    banks: banks,

                });
                this.setState({
                    ...this.state,
                    accounts: res.user[0].account_numbers,
                    loading: false,

                });
            });
    }

    handleEdit = (id, account, clabe, bank_id) => {
        this.setState({
            id_account: id,
            account_number: account,
            clabe: clabe,
            bank_id: bank_id,
            accountError: false,
            clabeError: false,
            bankError: false,
            accountMessageError: '',
            clabeMessageError: '',
            bankMessageError: '',
            isEdit: true,

        });
    }

    handleClear = e => {
        this.setState({
            account_number: '',
            clabe: '',
            bank_id: '',
            accountError: false,
            clabeError: false,
            bankError: false,
            accountMessageError: '',
            clabeMessageError: '',
            bankMessageError: '',
        });
    }

    componentWillUnmount() {
        this.abortController.abort();
    }

    handleChange = (name, value) => {
        let bank_Option = ''
        switch (name) {
            case 'account_number':
                let validateAccountNum = isAccountNumbers(value)
                if (validateAccountNum != "nodata") {
                    this.setState({
                        accountError: true,
                        accountMessageError: isAccountNumbers(value),
                    });
                } else {
                    this.setState({
                        accountError: false,
                        accountMessageError: '',
                    });
                }
                break;
            case 'bank_id':
                let validateBank = isBank(value)
                if (validateBank != "nodata") {
                    this.setState({
                        bankError: true,
                        bankMessageError: isBank(value),
                    });
                } else {
                    bank_Option = value;
                    
                    this.setState({
                        bankError: false,
                        bankMessageError: '',
                        bank_id:bank_Option,
                    });
                }
                break;
            default:
                let validateClabe = isClabe(value)
                if (validateClabe != "nodata") {
                    this.setState({
                        clabeError: true,
                        clabeMessageError: isClabe(value),
                    });
                } else {
                    this.setState({
                        clabeError: false,
                        clabeMessageError: '',
                    });
                }
                break;
        }

        this.setState({
            [name]: value
        });
    }

    handleSubmitAccount = (e) => {
        e.preventDefault();
        this.setState({ loading: true, disabledButton: true });

        const body = {
            account_number: this.state.account_number,
            clabe: this.state.clabe,
            bank_id: this.state.bank_id,
        }
        api.create.accountNumbers(body)
            .then((response) => {
                console.log(response)
                if (response.errors) {
                    let errorsMe = [];
                    let myError = response.errors;
                    Object.values(myError).forEach((error, i) => {
                        errorsMe.push({
                            label: error[0],
                            id: i,
                        });
                    });
                    this.setState({
                        messageRequest: errorsMe,
                        loading: false,
                        disabledButton: false

                    });
                } else {
                    this.setState({
                        isEdit: false,
                        messageRequest: false,
                        account_number: '',
                        clabe: '',
                        bank_id:'',
                        accountError: false,
                        clabeError: false,
                        bankError: false,
                        disabledButton: false
                    });
                    this.fetchAccount();

                }

            })
            .catch((error) => {
                console.log(error)
                // let errorsMe = [];

                // let myError = error.response.data.errors;
                // Object.values(myError).forEach((error, i) => {
                //     errorsMe.push({
                //         label: error[0],
                //         id: i,
                //     });
                // });
                // this.setState({
                //     messageRequest: errorsMe,
                //     loading:false,

                // });

            })
    }

    handleDefaultAccount = (id) => {
        console.log(id)
        this.setState({ loading: true, disabledButtonDefault: true });
        api.update.setDefault(id)
            .then((response) => {
                console.log(response)
                if (response.errors) {
                    let errorsMe = [];
                    let myError = response.errors;
                    Object.values(myError).forEach((error, i) => {
                        errorsMe.push({
                            label: error[0],
                            id: i,
                        });
                    });
                    this.setState({
                        messageRequest: errorsMe,
                        loading: false,
                        disabledButtonDefault: false,

                    });
                } else {
                    this.setState({
                        isEdit: false,
                        messageRequest: false,
                        account_number: '',
                        clabe: '',
                        accountError: false,
                        clabeError: false,
                        messageRequest: false,
                        disabledButtonDefault: false,
                    });
                    this.fetchAccount();

                }

            })
            .catch((error) => {
                console.log(error)

            })
    }

    handleDeleteAccount = (id) => {
        console.log(id)
        this.setState({ loading: true, disabledButtonDelete: true, });
        api.delete.delete_account(id)
            .then((response) => {
                console.log(response)
                if (response.errors) {
                    let errorsMe = [];
                    let myError = response.errors;
                    Object.values(myError).forEach((error, i) => {
                        errorsMe.push({
                            label: error[0],
                            id: i,
                        });
                    });
                    this.setState({
                        messageRequest: errorsMe,
                        loading: false,
                        disabledButtonDelete: false,
                    });
                } else {
                    this.setState({
                        isEdit: false,
                        messageRequest: false,
                        account_number: '',
                        clabe: '',
                        accountError: false,
                        clabeError: false,
                        messageRequest: false,
                        disabledButtonDelete: false,
                    });
                    this.fetchAccount();

                }

            })
            .catch((error) => {
                console.log(error)

            })
    }

    handleUpdateAccount = (id, account_number, clabe, bank_id) => {
        console.log(id)
        this.setState({ loading: true, disabledButtonUpdate: true, });
        const body = {
            account_number: account_number,
            clabe: clabe,
            bank_id: bank_id,
        }
        api.update.accountNumber_update(body, id)
            .then((response) => {
                console.log(response)
                if (response.errors) {
                    let errorsMe = [];
                    let myError = response.errors;
                    Object.values(myError).forEach((error, i) => {
                        errorsMe.push({
                            label: error[0],
                            id: i,
                        });
                    });
                    this.setState({
                        messageRequest: errorsMe,
                        loading: false,
                        disabledButtonUpdate: false,

                    });
                } else {
                    this.setState({
                        isEdit: false,
                        messageRequest: false,
                        account_number: '',
                        clabe: '',
                        bank_id: '',
                        accountError: false,
                        clabeError: false,
                        bankError: false,
                        disabledButtonUpdate: false,
                    });
                    this.fetchAccount();

                }

            })
            .catch((error) => {
                console.log(error)

            })
    }
    render() {
        console.log(this.state)
        if (this.state.loading) { return <Load /> }
        let accounts_num = [];
        if (this.state.accounts != undefined) {
            accounts_num = Object.values(this.state.accounts).map((account) => {
                console.log(account)
                return (
                    <React.Fragment>
                        <div className='list-accounts mt-4'>
                            <div className='row'>
                            <div className='col-xl-1 col-xs-1 col-md-1 col-sm-1 d-flex justify-content-center'>
                                    {(account.default == 1 ? <label className='text-success'><CheckBoxIcon/></label> : '')}
                                </div>
                                <div className='col-xl-3 col-xs-12 col-md-3 col-sm-12'>
                                    <InputValue
                                        attribute={{
                                            id: 'bank_account',
                                            name: 'bank_account',
                                            type: 'text',
                                            value: account.account_number,
                                        }}
                                    />
                                </div>
                                <div className='col-xl-3 col-xs-12 col-md-3 col-sm-12'>
                                    <InputValue
                                        attribute={{
                                            id: 'clave',
                                            name: 'clave',
                                            type: 'text',
                                            value: account.clabe,

                                        }}
                                    />
                                </div>
                                <div className='col-xl-3 col-xs-12 col-md-3 col-sm-12'>
                                <Select
                                    id='bank'
                                    name='bank'
                                    value={account.bank_id}
                                    options={this.state.banks}
                                    isDisabled={false}
                                    handleChange={this.handleChange}
                                />
                                </div>
                                <div className='col-xl-2 col-xs-12 col-md-2 col-sm-12 mt-2 mt-md-0 d-flex justify-content-center'>
                                    <button fontSize="small" className='btn-update mr-2' onClick={() => this.handleEdit(account.id, account.account_number, account.clabe, account.bank_id)}><EditIcon /></button>
                                    <button fontSize="small" className='btn-delete mr-2' disabled={this.state.disabledButtonDelete} onClick={() => this.handleDeleteAccount(account.id)}><DeleteForeverIcon /></button>
                                    <button fontSize="small" className='btn-flag' disabled={this.state.disabledButtonDefault} onClick={() => this.handleDefaultAccount(account.id)}><TurnedInNotIcon /></button>
                                </div>
                            </div>
                            <hr className='mt-2' />
                        </div>
                    </React.Fragment>
                )
            })
        }

        return (
            <div className='card-panel mb-footer p-5'>
                <div className='title-card'>
                    <h1>Cuentas Bancarias</h1>
                </div>
                <div className='body-card'>
                    {this.state.messageRequest &&
                        <div className='col-xl-12 col-xs-12 col-md-12 col-sm-12'>
                            <div className='form-group'><br />
                                <RequestMessage attribute={this.state.messageRequest} />
                            </div><br />
                        </div>
                    }
                    <div className='new-account'>
                        <div className='row mt-2'>
                            <div className='col-xl-2 col-xs-12 col-md-2 col-sm-12'>
                                <InputValue
                                    attribute={{
                                        id: 'account_number',
                                        name: 'account_number',
                                        type: 'text',
                                        value: this.state.account_number,
                                        disabled: true,
                                        placeholder: 'Cuenta bancaría',

                                    }}
                                    handleChange={this.handleChange}
                                />
                                {this.state.accountError &&
                                    <label className='label-error'>{this.state.accountMessageError}</label>
                                }
                            </div>
                            <div className='col-xl-3 col-xs-12 col-md-3 col-sm-12'>
                                <InputValue
                                    attribute={{
                                        id: 'clabe',
                                        name: 'clabe',
                                        type: 'text',
                                        value: this.state.clabe,
                                        disabled: true,
                                        placeholder: 'Clabe',
                                    }}
                                    handleChange={this.handleChange}

                                />
                                {this.state.clabeError &&
                                    <label className='label-error'>{this.state.clabeMessageError}</label>
                                }
                            </div>
                            <div className='col-xl-3 col-xs-12 col-md-3 col-sm-12'>
                                <Select
                                    id='bank_id'
                                    name='bank_id'
                                    value={this.state.bank_id}
                                    options={this.state.banks}
                                    isDisabled={true}
                                    handleChange={this.handleChange}
                                />
                                {this.state.bankError &&
                                    <label className='label-error'>{this.state.bankMessageError}</label>
                                }
                            </div>
                            <div className='col-xl-2 col-xs-12 col-md-2 col-sm-12 text-center mt-2 mt-md-0'>
                                <button className='btn btn-outline-info btn-block' onClick={this.handleClear}>Limpiar Datos</button>
                            </div>
                            {this.state.isEdit == true
                                ? <div className='col-xl-2 col-xs-12 col-md-2 col-sm-12 text-center mt-2 mt-md-0'>
                                    <button className='btn-dark btn-block' disabled={this.state.disabledButtonUpdate} onClick={() => this.handleUpdateAccount(this.state.id_account, this.state.account_number, this.state.clabe, this.state.bank_id)}>Actualizar cuenta</button>
                                </div>
                                : <div className='col-xl-2 col-xs-12 col-md-2 col-sm-12 text-center mt-2 mt-md-0'>
                                    <button className='btn-dark btn-block' disabled={this.state.disabledButton} onClick={this.handleSubmitAccount}>Agregar cuenta</button>
                                </div>
                            }


                        </div>
                    </div>
                    {accounts_num}
                </div>
            </div>
        );
    }
}

export default BankAccounts;