import React, { useEffect, useState } from "react";
import api from '../../../api';
import Load from '../../../componets/Loading/Load';
import HistoryCreditsClients from './HistoryCreditsClients';




import { useLocation } from "react-router-dom";

const HistoryC = () => {
    let user = useLocation();
    console.log(user)
    useEffect(() => {
        getData();
    }, []);

    const [loading, setLoading] = useState(true);
    const [historial, setHistorial] = useState([]);



    const getData = async () => {
        let user_id = user.state.id
        api.lists.getHistoricalCredit(user_id)
        .then(res => {
            console.log(res)
            setHistorial(res);
            setLoading(false);
            });
    };
    if (loading == true) {
        return <Load />
    } else {

        return (
            <React.Fragment>
                <HistoryCreditsClients data={historial}/>
            </React.Fragment>
        )
    }
};

export default HistoryC;