import React from 'react';
import InfoClient from './InfoCliente';
import InfoCredit from './InfoCredit';
import api from '../../../api';
import moment from 'moment';
import RequestMessage from '../../../componets/Messages/RequestMessage';
import Load from '../../../componets/Loading/Load';
import swal from 'sweetalert';
import TransferedProof from '../../../componets/Modal/TransferedProof';




class Request extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            solicitudes: [],
            loading: true,
            messageRequest: false,
            disabledButton: false,

        }
        this.abortController = new AbortController();
        this.signal = this.abortController.signal;
    }
    componentDidMount() {
        this.getSolicitudes()
    }
    getSolicitudes = () => {
        api.lists.getSolicitudes(this.signal)
            .then(res => {
                console.log(res)
                this.setState({
                    ...this.state,
                    solicitudes: res.credit_user ? res.credit_user : [],
                    loading: false,
                });
            });
    }
    handleApprove = (e, id_state, id_credit_date) => {
        e.preventDefault();
        this.setState({ loading: true, disabledButton: true, });
        const body = {
            credit_state_id: id_state,
            id_credit_date: id_credit_date
        }
        api.update.approve_credit_admin(body, id_credit_date)
            .then((response) => {
                console.log(response)
                if (response.type == "error" && response.code == 500) {
                    this.setState({
                        messageRequest: [{ label: response.message, id: 1 }],
                        loading: false,
                        disabledButton: false,
                    });
                } else {
                    swal({
                        title: "Correcto!",
                        text: response.message,
                        icon: "success",
                        timer: 2000,
                        button: false
                    });
                    this.setState({
                        messageRequest: false,
                        disabledButton: false,
                    });
                    this.getSolicitudes();
                    // swal({
                    //     title: "Correcto",
                    //     text: response.message,
                    //     icon: "success",
                    //     button: true,
                    //     dangerMode: false,
                    // })
                    //     .then(willDelete => {
                    //         if (willDelete) {
                    //             this.setState({
                    //                 messageRequest: false,
                    //                 disabledButton: false,
                    //             });
                    //             this.getSolicitudes();
                    //         }
                    //     })
                }

            })
            .catch((error) => {
                console.log(error)

            })
    }

    handleReject = (e, id_state, id_credit_date) => {
        e.preventDefault();
        swal("Observaciones:", {
            content: "input",
        })
            .then((value) => {
                this.setState({ loading: true, disabledButton: true, });
                const body = {
                    credit_state_id: id_state,
                    id_credit_date: id_credit_date,
                    observations: value,
                }
                api.update.reject_credit_admin(body, id_credit_date)
                    .then((response) => {
                        if (response.type == "error" && response.code == 500) {
                            this.setState({
                                messageRequest: [{ label: response.message, id: 1 }],
                                loading: false,
                                disabledButton: false,
                            });
                        } else {
                            swal({
                                title: "Correcto!",
                                text: response.message,
                                icon: "success",
                                timer: 2000,
                                button: false
                            });
                            this.setState({
                                messageRequest: false,
                                disabledButton: false,
                            });
                            this.getSolicitudes();
                        }
                    })
                    .catch((error) => {
                        console.log(error)
                    })
            });
    }

    render() {
        if (this.state.loading) {
            return <Load />
        }
        console.log(this.state.solicitudes)
        let request_C = '';
        request_C = Object.values(this.state.solicitudes).map((solicitud) => {
            console.log(solicitud)
            return (
                <tr className="text-center">
                    <td>{solicitud.folio}</td>
                    <td><InfoCredit infoCredits={solicitud} /></td>
                    <td><InfoClient infoClient={solicitud.user} /></td>
                    <td>{moment(solicitud.created_at).format("DD-MM-YYYY")}</td>
                    <td>
                        {
                            solicitud.credit_state_id == 2
                                ?
                                <React.Fragment>
                                    <div>
                                        <TransferedProof credit_state_id='4' id_credit_date={solicitud.id} />
                                        <button type="button" disabled={this.state.disabledButton} className="btn btn-danger btn-sm mr-2" onClick={(e) => this.handleReject(e, 3, solicitud.id)}>Rechazar</button>
                                    </div>

                                </React.Fragment>
                                :
                                solicitud.credit_state_id == 3
                                    ?
                                    ''
                                    :
                                    <React.Fragment>
                                        <button type="button" disabled={this.state.disabledButton} className="btn btn-success btn-sm mr-2"
                                            onClick={(e) => {
                                                swal({
                                                    title: '¿Estás seguro que deseas aprobar este solicitud?',
                                                    text: 'Una vez aprobado se requiere marcar como depositado',
                                                    icon: 'warning',
                                                    buttons: true,
                                                    dangerMode: true,
                                                })
                                                    .then(willDelete => {
                                                        if (willDelete) { this.handleApprove(e, 2, solicitud.id) }
                                                        else { swal.close(); }
                                                    })
                                            }} >Aprobar</button>
                                        <button type="button" disabled={this.state.disabledButton} className="btn btn-danger btn-sm mr-2"
                                            onClick={(e) => {
                                                swal({
                                                    title: '¿Estás seguro que deseas rechazar esta solicitud?',
                                                    text: 'Si rechazas esta solicitud no podrás modificarla ',
                                                    icon: 'warning',
                                                    buttons: true,
                                                    dangerMode: true,
                                                })
                                                    .then(willDelete => {
                                                        if (willDelete) { this.handleReject(e, 3, solicitud.id) }
                                                        else { swal.close(); }
                                                    })
                                            }} >Rechazar</button>
                                    </React.Fragment>
                        }
                    </td>
                </tr>
            )
        })
        return (
            <section className='container'>
                <div className='row'>
                    <div className='col-12 mt-4'>
                        <div className='card p-4'>
                            Solicitudes Pendientes
                            {this.state.messageRequest &&
                                <div className='col-xl-12 col-xs-12 col-md-12 col-sm-12'>
                                    <div className='form-group'><br />
                                        <RequestMessage attribute={this.state.messageRequest} />
                                    </div><br />
                                </div>}
                            <div className='table-responsive'>
                                <table className="table">
                                    <thead>
                                        <tr className="text-center">
                                            <th scope="col">Folio</th>
                                            <th scope="col">Solicitud</th>
                                            <th scope="col">Cliente</th>
                                            <th scope="col">Fecha de Solicitud</th>
                                            <th scope="col">Acciones</th>
                                        </tr>
                                    </thead>
                                    <tbody>
                                        {this.state.solicitudes.length == 0
                                        ?
                                        <tr className='text-center'>
                                            <td colSpan="5">No hay solicitudes</td>
                                        </tr>
                                    :
                                        request_C
                                    }
                                    </tbody>
                                </table>
                            </div>
                        </div>
                    </div>
                </div>
            </section>
        );
    }
}

export default Request;