import React from 'react';
import api from '../../../api';
import Load from '../../../componets/Loading/Load';

class User extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            users: [],
            loading: true,
        }
        this.abortController = new AbortController();
        this.signal = this.abortController.signal;
    }
    componentDidMount() {
        this.getUsersAdministratives()
    }
    getUsersAdministratives = () => {
        api.lists.getAdministrativesUsers(this.signal)
            .then(res => {
                console.log(res)
                this.setState({
                    ...this.state,
                    users: res,
                    loading:false,
                });
            });
    }
    render() { 
        if (this.state.loading) {
            return <Load />
        }
        let users = '';
        let usersAll = this.state.users ? this.state.users : [];

        users = Object.values(usersAll).map((info) => {
            return (
                <tr key={info.id} className='text-center'>
                    <td>{info.id}</td>
                    <td>{info.name} {info.last_name}</td>
                    <td>{info.email}</td>
                    <td>{info.role.name}</td>
                </tr>

            )

        })
        return ( 
            <section className='container'>
                <div className='row'>
                    <div className='col-12 mt-4'>
                        <div className='card p-4'>
                            Usuarios Administrativos
                            <div className='table-responsive'>
                                <table className="table">
                                    <thead>
                                        <tr className='text-center'>
                                            <th scope="col">ID</th>
                                            <th scope="col">Nombre</th>
                                            <th scope="col">Correo</th>
                                            <th scope="col">Tipo</th>
                                        </tr>
                                    </thead>
                                    <tbody>
                                      {users}
                                    </tbody>
                                </table>
                            </div>
                        </div>
                    </div>
                </div>
            </section>
        );
    }
}
 
export default User;