import React, { useState } from 'react';
import { makeStyles, Theme, createStyles } from '@material-ui/core/styles';
import Modal from '@material-ui/core/Modal';
import Backdrop from '@material-ui/core/Backdrop';
import Fade from '@material-ui/core/Fade';
import '../../../assets/css/style.scss';
import CloseIcon from '@material-ui/icons/Close';
import CheckBoxIcon from '@material-ui/icons/CheckBox';
import api from '../../../api';
import axios from 'axios';



const useStyles = makeStyles((theme: Theme) =>
    createStyles({
        modal: {
            display: 'flex',
            alignItems: 'center',
            justifyContent: 'center',
        },
        paper: {
            backgroundColor: theme.palette.background.paper,
            border: 'none !important',
            borderRadius: '15px',
            width: '95%',
            maxWidth: '800px',
            maxHeight: '600px',
            overflowY: 'scroll',
            scrollBehavior: 'smooth',
            boxShadow: theme.shadows[5],
            padding: theme.spacing(2, 4, 3),
        },
    }),
);

export default function TransitionsModal(props) {
    const classes = useStyles();
    const [open, setOpen] = React.useState(false);
    const [pathImages, setPathImages] = useState('');

    const handleOpen = () => {
        setOpen(true);
        setPathImages(api.base_url_img())

    };

    const handleClose = () => {
        setOpen(false);
    };


    console.log(props.infoClient)
    if (props.infoClient.client_info != null) {
        let accountNumber = '';
        accountNumber = Object.values(props.infoClient.account_numbers).map((account) => {
            return (
                <React.Fragment>
                    <div className='col col-12 m-4'>
                        <div className="row">
                            <div className='col col-1 text-center mb-5 mt-4'>
                                {account.default == 1 ? <label className='text-success'><CheckBoxIcon /></label> : <label className='text-light'><CheckBoxIcon /></label>}
                            </div>
                            <div className='col col-3 text-center mb-5'>
                                <p>Num. de cuenta</p>
                                <p className='input'>
                                    {account.account_number ? account.account_number : '-'}
                                </p>
                            </div>
                            <div className='col col-4 text-center mb-5'>
                                <p>Clabe</p>
                                <p className='input'>
                                    {account.clabe ? account.clabe : '-'}
                                </p>
                            </div>
                            <div className='col col-3 text-center mb-5'>
                                <p>Banco</p>
                                <p className='input'>
                                    {account.bank ? account.bank.name : '-'}
                                </p>
                            </div>
                        </div>

                    </div>
                </React.Fragment>

            )
        })

        let path_IdentFrontal = api.base_url_img() + props.infoClient.client_info.photo_ine_fron
        let path_IdentPosterior = api.base_url_img() + props.infoClient.client_info.photo_ine_back
        let path_foto = api.base_url_img() + props.infoClient.client_info.photo_face
        let path_domicilio = api.base_url_img() + props.infoClient.client_info.domiciliary_proof

        return (
            <div>
                <div onClick={handleOpen}>
                    <div className='simple-link'>
                        <p>{props.infoClient.name} {props.infoClient.last_name}</p>
                    </div>
                </div>
                <Modal
                    aria-labelledby="transition-modal-title"
                    aria-describedby="transition-modal-description"
                    className={classes.modal}
                    open={open}
                    onClose={handleClose}
                    closeAfterTransition
                    BackdropComponent={Backdrop}
                    BackdropProps={{
                        timeout: 500,
                    }}
                >
                    <Fade in={open}>
                        <div className={classes.paper}>
                            <div className='data-modal'>
                                <div style={{ textAlign: "right" }}><button onClick={handleClose}><CloseIcon fontSize="large" /></button></div>
                                <div className='modal-header'>
                                    <h3>Datos del Cliente</h3>
                                </div>
                                <div className='modal-body row'>
                                    <div className='col col-4 mb-3'>
                                        <p>Correo Electrónico</p>
                                        <p className='input'>
                                            {props.infoClient.email ? props.infoClient.email : '-'}
                                        </p>
                                    </div>
                                    <div className='col col-4 mb-3'>
                                        <p>Nombre</p>
                                        <p className='input'>
                                            {props.infoClient.name ? props.infoClient.name : '-'}
                                        </p>
                                    </div>

                                    <div className='col col-4 mb-3'>
                                        <p>Apellidos</p>
                                        <p className='input'>
                                            {props.infoClient.last_name ? props.infoClient.last_name : '-'}
                                        </p>
                                    </div>
                                    <div className='col col-4 mb-3'>
                                        <p>Curp</p>
                                        <p className='input'>
                                            {props.infoClient.client_info.curp ? props.infoClient.client_info.curp : '-'}
                                        </p>
                                    </div>
                                    <div className='col col-4 mb-3'>
                                        <p>Código de País</p>
                                        <p className='input'>
                                            {props.infoClient.code_phone ? props.infoClient.code_phone.code + '  ' + props.infoClient.code_phone.name : "-"}
                                        </p>
                                    </div>
                                    <div className='col col-4 mb-3'>
                                        <p>Teléfono</p>
                                        <p className='input'>
                                            {props.infoClient.phone ? props.infoClient.phone : "-"}
                                        </p>
                                    </div>
                                    <div className='col col-4 mb-3'>
                                        <p>Nivel de cliente</p>
                                        <p className='input'>
                                            {props.infoClient.client_info.level.name ? props.infoClient.client_info.level.name : '-'}
                                        </p>
                                    </div>
                                    <div className='col col-4 mb-3'>
                                        <p>Ocupación</p>
                                        <p className='input'>
                                            {props.infoClient.client_info.profession.name ? props.infoClient.client_info.profession.name : '-'}
                                        </p>
                                    </div>
                                    <div className='col col-2 mb-3'>
                                        <p>Salario</p>
                                        <p className='input'>
                                            $ {props.infoClient.client_info.salary ? props.infoClient.client_info.salary : '-'}
                                        </p>
                                    </div>
                                    <div className='col col-6 mb-3'>
                                        <p>Dirección</p>
                                        <p className='input'>
                                            {props.infoClient.client_info.address ? props.infoClient.client_info.address : '-'}
                                        </p>
                                    </div>
                                    <hr />
                                    <div className='col col-12 m-4'>
                                        <span>Archivos</span>
                                    </div>
                                    <div className='col col-3 text-center mb-5'>
                                        <a href={path_IdentFrontal} download target='_blank' >
                                            Identificación - Frontal
                                        </a>
                                    </div>
                                    <div className='col col-3 text-center mb-5'>
                                        <a href={path_IdentPosterior} download target='_blank' >
                                            Identificación - Posterior
                                        </a>
                                    </div>
                                    <div className='col col-3 text-center mb-5'>
                                        <a href={path_foto} download target='_blank' >
                                            Foto
                                        </a>
                                    </div>
                                    <div className='col col-3 text-center mb-5'>
                                        <a href={path_domicilio} download target='_blank' >
                                            Compobante de domicilio
                                        </a>
                                    </div>
                                    <hr />
                                    <div className='col col-12 m-4'>
                                        <span>Datos Bancarios</span>
                                    </div>
                                    {accountNumber}
                                </div>
                            </div>
                        </div>
                    </Fade>
                </Modal>
            </div>
        );
    } else {
        return (
            <div>
                <div onClick={handleOpen}>
                    <div className='simple-link'>
                        <p>{props.infoClient.name} {props.infoClient.last_name}</p>
                    </div>
                </div>
                <Modal
                    aria-labelledby="transition-modal-title"
                    aria-describedby="transition-modal-description"
                    className={classes.modal}
                    open={open}
                    onClose={handleClose}
                    closeAfterTransition
                    BackdropComponent={Backdrop}
                    BackdropProps={{
                        timeout: 500,
                    }}
                >
                    <Fade in={open}>
                        <div className={classes.paper}>
                            <div className='data-modal'>
                                <div style={{ textAlign: "right" }}><button onClick={handleClose}><CloseIcon fontSize="large" /></button></div>
                                <div className='modal-header'>
                                    <h3>Datos del Cliente</h3>
                                </div>
                                <div className='modal-body row'>
                                    <div className='col col-4 mb-3'>
                                        <p>Correo Electrónico</p>
                                        <p className='input'>
                                            {props.infoClient.email ? props.infoClient.email : '-'}
                                        </p>
                                    </div>
                                    <div className='col col-4 mb-3'>
                                        <p>Nombre</p>
                                        <p className='input'>
                                            {props.infoClient.name ? props.infoClient.name : '-'}
                                        </p>
                                    </div>

                                    <div className='col col-4 mb-3'>
                                        <p>Apellidos</p>
                                        <p className='input'>
                                            {props.infoClient.last_name ? props.infoClient.last_name : '-'}
                                        </p>
                                    </div>
                                    <div className='col col-4 mb-3'>
                                        <p>Curp</p>
                                        <p className='input'>
                                            -
                                         </p>
                                    </div>
                                    <div className='col col-4 mb-3'>
                                        <p>Código de País</p>
                                        <p className='input'>
                                            -
                                        </p>
                                    </div>
                                    <div className='col col-4 mb-3'>
                                        <p>Teléfono</p>
                                        <p className='input'>
                                            {props.infoClient.phone ? props.infoClient.phone : "-"}
                                        </p>
                                    </div>
                                    <div className='col col-4 mb-3'>
                                        <p>Nivel de cliente</p>
                                        <p className='input'>
                                            -
                                        </p>
                                    </div>
                                    <div className='col col-4 mb-3'>
                                        <p>Ocupación</p>
                                        <p className='input'>
                                            -
                                        </p>
                                    </div>
                                    <div className='col col-2 mb-3'>
                                        <p>Salario</p>
                                        <p className='input'>
                                            -
                                        </p>
                                    </div>
                                    <div className='col col-6 mb-3'>
                                        <p>Dirección</p>
                                        <p className='input'>
                                            -
                                        </p>
                                    </div>
                                    <hr />
                                    <div className='col col-12 m-4'>
                                        <span>Archivos</span>
                                    </div>
                                    <div className='col col-3 text-center mb-5'>
                                        -
                                    </div>
                                    <div className='col col-3 text-center mb-5'>
                                        -
                                    </div>
                                    <div className='col col-3 text-center mb-5'>
                                        -
                                    </div>
                                    <div className='col col-3 text-center mb-5'>
                                        -
                                    </div>
                                    <hr />
                                    <div className='col col-12 m-4'>
                                        <span>Datos Bancarios</span>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </Fade>
                </Modal>
            </div>
        );
    }
}