const isAccountNumbers = (value) => {
    console.log(value)
    console.log(!value.trim())
    if (!value.trim()) {
        return 'El número de cuenta es requerido';
    } else if (value.length < 16) {
        return 'El Número de Tarjeta es Incorecto';
    }
    return "nodata";
}
export default isAccountNumbers;