import React from "react";
import './credit.scss';
import QueryBuilderIcon from '@material-ui/icons/QueryBuilder';
import Load from '../../../componets/Loading/Load';


import api from '../../../api';

class CreditRejected extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            credits_reject:this.props ? this.props.credits : [],
            loading:true,
        }
        this.abortController = new AbortController();
        this.signal = this.abortController.signal;
    }
    componentDidMount() {
        this.loading()
    }
    loading = () => {
        this.setState({
            ...this.state,
            loading: false,
        });
    }

    componentWillUnmount() {
        this.abortController.abort();
    }
    render() {
        if(this.state.loading){
            return <Load/>
        }
        let credits_reject2=this.props.credits ? this.props.credits : [];
        let request_R='';
        if (this.props.credits!=undefined) {
            request_R = Object.values(this.props.credits).map((credit) => {
                return (
                    <div className='row m-0'>
                        <div className='credit-details rejected'>
                            <div className='col-2 detail-icon'>
                                <QueryBuilderIcon fontSize="large" />
                            </div>
                            <div className='col-10 detail-content'>
                                <div className='message'>Crédito Rechazado</div>
                                <div className='detail-amount'>$ {credit.value} {credit.currency}</div>
                                <div><span>No te desanimes, solo 1 de cada 4 personas consiguen un crédito</span></div>
    
                            </div>
                        </div><br />
                    </div>
    
                )
   
           })
        }
        return (
            <React.Fragment>
                {credits_reject2.length!=0 &&
                    <div>
                        {request_R}
                    </div>
                }
            </React.Fragment>
            

        );
    }
}

export default CreditRejected;