import React from "react";
import InputValue from '../../../componets/Input/InputValue';
import Select from '../../../componets/Select/Select';
import Checkbox from '@material-ui/core/Checkbox';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import RequestMessage from '../../../componets/Messages/RequestMessage';
import Terms from '../../../componets/Modal/Terms';
import Politics from '../../../componets/Modal/Politics';
import SelectCodeNumbers from '../../../componets/Select/SelectCodeNumbers';

export default function InformationUser(props) {
    const user = props.state.user;
    const client = props.state.client;
    const editInfo = props.edit;
    const professions = props.state.professions;
    const codeNumbers = props.state.NumbersCode;
    const error = props.state.error;
    const messageRequest = props.state.messageRequest;
    let disabled_policies = "";
    if (editInfo == false && client.policies_privacy == 1 || editInfo == false && client.policies_privacy == 0) {
        disabled_policies = "disabled"
    } else if (editInfo == true && client.policies_privacy == 1 && props.state.chequeado == false) {
        disabled_policies = "disabled"
    }
    let disabled_terms = "";
    if (editInfo == false && client.terms_conditions == 1 || editInfo == false && client.terms_conditions == 0) {
        disabled_terms = "disabled"
    } else if (editInfo == true && client.terms_conditions == 1 && props.state.chequeado == false) {
        disabled_terms = "disabled"
    }
    return (
        <React.Fragment>

            <div className='title-card'>
                <h1>Datos Personales</h1>
            </div>
            <div className='body-card'>
                {messageRequest &&
                    <div className='col-xl-12 col-xs-12 col-md-12 col-sm-12'>
                        <div className='form-group'><br />
                            <RequestMessage attribute={messageRequest} />
                        </div><br />
                    </div>
                }
                <div className='user-info'>

                    <div className='row mt-2'>
                        <div className='col col-12 col-md-6'>
                            <div className='form-group'>
                                <p className='data-label'>Nombre <span className="text-danger">*</span></p>
                                <InputValue
                                    attribute={{
                                        id: 'name',
                                        name: 'name',
                                        type: 'text',
                                        value: user.name,
                                        disabled: editInfo,
                                        placeholder: 'Nombre'
                                    }}
                                    handleChange={props.handleUserChange}
                                />
                                {error.nameError &&
                                    <label className='label-error'>{error.nameMessageError}</label>
                                }
                            </div>
                        </div>
                        <div className='col col-12 col-md-6'>
                            <div className='form-group'>
                                <p className='data-label'>Apellidos <span className="text-danger">*</span></p>
                                <InputValue
                                    attribute={{
                                        id: 'last_name',
                                        name: 'last_name',
                                        type: 'text',
                                        value: user.last_name,
                                        disabled: editInfo,
                                        placeholder: 'Apellidos',
                                    }}
                                    handleChange={props.handleUserChange}
                                />
                                {error.lastNameError &&
                                    <label className='label-error'>{error.lastNameMessageError}</label>
                                }
                            </div>
                        </div>
                        <div className='col col-12 col-md-6'>
                            <div className='form-group'>
                                <p className='data-label'>CURP <span className="text-danger">*</span></p>
                                <InputValue
                                    attribute={{
                                        id: 'curp',
                                        name: 'curp',
                                        type: 'text',
                                        value: client.curp ? client.curp.toUpperCase() : '',
                                        disabled: editInfo,
                                        placeholder: 'CURP',
                                    }}
                                    handleChange={props.handleClientChange}
                                />
                                {error.curpError &&
                                    <label className='label-error'>{error.curpMessageError}</label>
                                }
                            </div>
                        </div>
                        <div className='col col-12 col-md-2'>
                            <div className='form-group'>
                                <p className='data-label'>Código <span className="text-danger">*</span></p>
                                <SelectCodeNumbers
                                    id='code'
                                    name='code'
                                    value={props.state.codeOption}
                                    options={codeNumbers}
                                    isDisabled={editInfo}
                                    handleChange={props.handleUserChange}
                                />
                            </div>
                        </div>
                        <div className='col col-12 col-md-4'>
                            <div className='form-group'>
                                <p className='data-label'>Teléfono <span className="text-danger">*</span></p>
                                <InputValue
                                    attribute={{
                                        id: 'phone',
                                        name: 'phone',
                                        type: 'text',
                                        value: user.phone,
                                        disabled: editInfo,
                                        placeholder: 'Telefono',
                                    }}
                                    handleChange={props.handleUserChange}

                                />
                                {error.phoneError &&
                                    <label className='label-error'>{error.phoneMessageError}</label>
                                }
                            </div>
                        </div>
                        
                        <div className='col col-12 col-md-6'>
                            <div className='form-group'>
                                <p className='data-label'>Dirección</p>
                                <InputValue
                                    attribute={{
                                        id: 'address',
                                        name: 'address',
                                        type: 'text',
                                        value: client.address,
                                        disabled: editInfo,
                                        placeholder: 'Dirección',
                                    }}
                                    handleChange={props.handleClientChange}
                                />
                                {error.addressError &&
                                    <label className='label-error'>{error.addressMessageError}</label>
                                }
                            </div>
                        </div>
                        <div className='col col-12 col-md-6'>
                            <div className='form-group'>
                                <p className='data-label'>Ocupación <span className="text-danger">*</span></p>
                                <Select
                                    id='profession'
                                    name='profession'
                                    value={props.state.professionOption}
                                    options={professions}
                                    isDisabled={editInfo}
                                    handleChange={props.handleClientChange}
                                />
                                {error.professionError &&
                                    <label className='label-error'>{error.professionMessageError}</label>
                                }
                            </div>
                        </div>
                        <div className='col col-12 col-md-6'>
                            <div className='form-group'>
                                <p className='data-label'>Salario</p>
                                <InputValue
                                    attribute={{
                                        id: 'salary',
                                        name: 'salary',
                                        type: 'numeric',
                                        value: client.salary,
                                        disabled: editInfo,
                                        placeholder: 'Salario',
                                    }}
                                    handleChange={props.handleClientChange}
                                />
                                {error.salaryError &&
                                    <label className='label-error'>{error.salaryMessageError}</label>
                                }
                            </div>
                        </div>
                    </div>
                    <div className='row mt-2'>
                        <div className='col-12 col-6 text-center'>
                            <FormControlLabel
                                value="bottom"
                                control={
                                    <Checkbox
                                        color="primary"
                                        name="policies_privacy"
                                        disabled={disabled_policies}
                                        checked={(client.policies_privacy == "1" ? true : false)}
                                        onChange={props.handleCheckChange}
                                    />
                                }
                                label={<Politics />}
                                labelPlacement="bottom"
                            />
                            <FormControlLabel
                                value="bottom"
                                control={
                                    <Checkbox
                                        color="primary"
                                        name="terms_conditions"
                                        disabled={disabled_terms}
                                        checked={(client.terms_conditions == 1 ? true : false)}
                                        onChange={props.handleCheckChange}
                                    />
                                }
                                label={<Terms />}
                                labelPlacement="bottom"
                            />
                        </div>

                        <div className='col-12 col-6 text-center'><br /><br />
                            {(editInfo == true)
                                ?
                                <>
                                    <button className='btn-dark mr-4' onClick={props.handleCancel} >Cancelar</button>
                                    <button className='btn-dark' disabled={props.state.disabledButton} onClick={props.handleSubmit} >Guardar</button>
                                </>
                                :
                                <button className='btn-dark' onClick={props.handleEditProfile}>Actualizar Información</button>
                            }
                        </div>
                    </div>
                </div>
            </div>
        </React.Fragment>
    );
}
