import React, {Component} from 'react';
import  Alert  from '@material-ui/lab/Alert/Alert';

export default class RequestMessage extends Component{
    render(){
        const mess = this.props.attribute;
        console.log(mess)
            const rows = Object.values(mess).map(meesa =>
               <div key={meesa.id}><label key={meesa.id}>{meesa.label}</label><br/></div>
                );
        return(
            <Alert variant="outlined" severity="warning">
                <label>{rows}</label>
            </Alert>
        );

    }
}
