const isName = (value) => {
    if (!value.trim()) {
        return 'El nombre es requerido';
    } else if (/[^A-Za-z\s\,]/.test(value)) {
        return 'El nombre es inválido, agrega letras únicamente';
    } else if (value.length < 3) {
        return 'El nombre es muy corto';
    }
    return "nodata";
}
export default isName;