import React, { Component } from "react";
import DataUser from './DataUser';
import DataInfo from './DataInfo';
import BankAccounts from './BankAccounts';
import MenuBottom from '../MenuBottom';
import api from '../../../api';
import SendComent from '../../../componets/Modal/SendComent';


export default class Profile extends Component {
    constructor(props) {
        super(props);
        this.state = {
            typeSup: {},

        }
        this.abortController = new AbortController();
        this.signal = this.abortController.signal;
    }
    componentDidMount() {
        this.getSupportTypes()

    }
    getSupportTypes = () => {
        api.lists.get_supportTypes()
            .then(res => {
                console.log(res)
                res.forEach(element => {
                    let typeS = 'type' + element.id
                    this.setState({
                        typeSup: {
                            ...this.state.typeSup,
                            [typeS]: { label: element.name, id: element.id }
                        }

                    });
                });


            });
    }
    render() {
        return (
            <React.Fragment>
                <DataUser />
                <DataInfo />
                <BankAccounts />
                <MenuBottom />
                <div className="className='col col-12 col-md-12 col-xl-12'" >
                    <div className="row" >
                        <SendComent text='Solicitar Soporte' type_S={this.state.typeSup['type1']} />
                    </div>
                </div>
            </React.Fragment>
        );
    }
}

