const isPhone = (value) => {
    if (!value.trim()) {
        return 'Teléfono Requerido';
    } else if (!(/^[0-9]{0,20}(\.?)[0-9]{0,2}$/.test(value))) {
        return 'Teléfono Incorecto';
    } else if (value < 3) {
        return 'Teléfono Incorecto';
    }
    return "nodata";
}
export default isPhone;