import React from "react";
import api from '../../../api';

import { Link } from 'react-router-dom';
import Load from '../../../componets/Loading/Load';

class DataUser extends React.Component {
    constructor(props) {
        super(props);

        this.state = {
            user: {},
            loading: true,

        }
        this.abortController = new AbortController();
        this.signal = this.abortController.signal;
    }

    componentDidMount() {
        this.fetchUser()
    }

    fetchUser = () => {
        api.lists.profile(this.signal)
            .then(res => {
                console.log(res)
                this.setState({
                    ...this.state,
                    user: {
                        ...this.state.user,
                        name: res.user[0].name,
                        last_name: res.user[0].last_name,
                        email: res.user[0].email,
                        phone: res.user[0].phone,
                        level: res.user[0].level,
                        social: res.user[0].social,
                    },
                    loading: false,

                });
            })
            .catch((error) => {
                console.log(error)
                this.setState({
                    loading: false,

                });
            })
    }

    componentWillUnmount() {
        this.abortController.abort();
    }
    render() {
        if(this.state.loading){return <Load/>}
        return (
            <div className='card-panel p-5'>
                <div className='title-card'>
                    <h1>Datos de cuenta</h1>
                </div>
                <div className='body-card'>
                    <div className='data-info'>
                        <div className='row mt-2'>
                            <div className='col col-12 col-md-4 pr-md-2'>
                                <p className='data-label'>Correo Eléctronico</p>
                                <p className='data-content'>{this.state.user.email}</p>
                            </div>
                            <div className='col col-12 col-md-4 pr-md-2'>
                                <p className='data-label'>Nivel de cuenta</p>
                                <p className='data-content'>{this.state.user.level}</p>
                            </div>
                            <div className='col col-12 col-md-4 pr-md-2'>
                                <p className='data-label'>Contraseña</p>
                                <p className='data-content'><Link className='psw-update' to="/password_update">Cambiar Contraseña</Link></p>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        );
    }
}

export default DataUser;