import React from "react";
import './credit.scss';
import CheckCircleOutlineIcon from '@material-ui/icons/CheckCircleOutline';

import api from '../../../api';
import { Link } from 'react-router-dom';
import Load from '../../../componets/Loading/Load';



class CreditLiquidated extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            credits_approved: this.props ? this.props.credits : [],
            loading: true,
        }
        this.abortController = new AbortController();
        this.signal = this.abortController.signal;
    }

    componentDidMount() {
        this.loading()
    }
    loading = () => {
        this.setState({
            ...this.state,
            loading: false,
        });
    }

    componentWillUnmount() {
        this.abortController.abort();
    }
    render() {
        if(this.state.loading){
            return <Load/>
        }
        let credits_liquidated2=this.props.credits ? this.props.credits : [];
        let request_L='';
        if (this.props.credits) {
             request_L = Object.values(this.props.credits).map((credit) => {
                return (
                    <React.Fragment>
                        <div className='row m-0'>
                            <div className='col col-2 col-md-1 detail-icon'>
                                <CheckCircleOutlineIcon fontSize="large" />
                            </div>
                            <div className='col col-10 col-md-11 detail-content'>
                                <div className='message'>Crédito Liquidado</div>
                                <div className='detail-amount'>$ {credit.value} {credit.currency}</div>
                                <div><Link  to={{ pathname: '/details_info_credit', state: { id: credit.id} }}>Ver Detalles</Link></div>
                            </div>
                        </div><br />
                    </React.Fragment>
    
                )
    
            })
        }
        return (
            <React.Fragment>
                {credits_liquidated2.length!=0 &&
                    <div className='credit-details liquidate'>
                        {request_L}
                    </div>
                }
            </React.Fragment>
            

        );
    }
}

export default CreditLiquidated;