import React from "react";
import './homeuser.scss';
import PaymentIcon from '@material-ui/icons/Payment';
import api from '../../api.js';
import PersonIcon from '@material-ui/icons/Person';
import ExitToAppIcon from '@material-ui/icons/ExitToApp';
import NotificationsIcon from '@material-ui/icons/Notifications';
import { NavLink } from 'react-router-dom';

class Menu extends React.Component {
    handleLogout = e => {
       
        console.log(localStorage.getItem("token"))
          api.logout(localStorage.getItem("token"))
            .then(data => {
                console.log(data);
                localStorage.removeItem("token");
                localStorage.removeItem("user");
                window.location='/';
            });
    }

    render() {
        return (
            <div className='menu'>
                <ul>
                    <li className='mt-0'>
                        <NavLink exact className='btn-menu' activeClassName='active' to="/credit"><PaymentIcon className='mr-2'/> Mi Crédito</NavLink>
                    </li>
                    <li className='mt-0'>
                        <NavLink exact className='btn-menu' activeClassName='active' to="/profile"><PersonIcon className='mr-2'/> Mi Perfil</NavLink>
                    </li>
                    <li className='mt-0'>
                        <NavLink exact className='btn-menu' activeClassName='active' to="/notifications"><NotificationsIcon className='mr-2'/> Notificaciones</NavLink>
                    </li>
                    <li className='mt-0'>
                        <NavLink exact className='btn-menu' to=''  onClick={this.handleLogout}><ExitToAppIcon className='mr-2'/> Cerrar Sesión</NavLink>
                    </li>
                </ul>
            </div>
        );
    }
}

export default Menu;