import React from "react";
import './useradmin.scss';
import { BrowserRouter as Router, Switch, Route } from "react-router-dom";
import Menu from './Menu';
import 'bootstrap/dist/css/bootstrap.min.css';

import Credit from './Credit/Credit';
import Request from './Requests/Rrequest';
import User from './Users/User';
import Client from './Clients/Client';
import MoreCreditSupport from './Messages/MoreCreditSupport';
import ProblemCreditsSupport from './Messages/ProblemCreditsSupport';
import GeneralSupports from './Messages/GeneralSupport';
import ClientHistory from './History/ClientHistory';
import AdminHistory from './History/AdminHistory';
import MessageReadMoreCredits from './Messages/MessageReadMoreCredit';
import MessageReadProblemCredits from './Messages/MessageReadProblemCredit';
import MessageReadGeneralSupports from './Messages/MessageReadGeneralSupports';
import HistoryC from './Clients/HistoryC';

const routes = [
  {
    path: "/tabulator",
    component: Credits
  },
  {
    path: "/credit_applications",
    component: CreditAplicatios
  },
  {
    path: "/users",
    component: Users
  },
  {
    path: "/clients",
    component: Clients
  },
  {
    path: "/credit_more_support",
    component: MoreCreditSupports
  },
  {
    path: "/problems_credits_support",
    component: ProblemCreditsSupports
  },
  {
    path: "/general_support",
    component: GeneralSupport
  },
  {
    path: "/client_history",
    component: ClientHistorys
  },
  {
    path: "/admin_history",
    component: AdminHistorys
  },
  {
    path: "/message_ready_moreCredits",
    component: MessageReadMoreCredit
  },
  {
    path: "/message_ready_problemsCredits",
    component: MessageReadProblem
  },
  {
    path: "/message_ready_generalSupport",
    component: MessageReadGeneralSupport
  },
  {
    path: "/history_credits_client",
    component: HistoryCreditsClient
  },
];

export default function RouteConfigExample() {
    return (
        <Router>
            <div>
              <Menu/>
                
                <Switch>
                  {routes.map((route, i) => (
                      <RouteWithSubRoutes key={i} {...route} />
                  ))}
                  </Switch>
            </div>
        </Router>
    );
}

function RouteWithSubRoutes(route) {
  return (
    <Route
      path={route.path}
      render={props => (
        <route.component {...props} routes={route.routes} />
      )}
    />
  );
}

function Credits() {
  return <div className='content-panel'><Credit/></div>;
}

function CreditAplicatios() {
  return <div className='content-panel'><Request/></div>;
}

function Users() {
  return <div className='content-panel'><User/></div>;
}

function Clients() {
  return <div className='content-panel'><Client/></div>;
}

function MoreCreditSupports() {
  return <div className='content-panel'><MoreCreditSupport/></div>;
}

function ProblemCreditsSupports() {
  return <div className='content-panel'><ProblemCreditsSupport/></div>;
}

function GeneralSupport() {
  return <div className='content-panel'><GeneralSupports/></div>;
}

function ClientHistorys() {
  return <div className='content-panel'><ClientHistory/></div>;
}

function AdminHistorys() {
  return <div className='content-panel'><AdminHistory/></div>;
}
function MessageReadMoreCredit() {
  return <div className='content-panel'><MessageReadMoreCredits/></div>;
}
function MessageReadProblem() {
  return <div className='content-panel'><MessageReadProblemCredits/></div>;
}
function MessageReadGeneralSupport() {
  return <div className='content-panel'><MessageReadGeneralSupports/></div>;
}
function HistoryCreditsClient() {
  return <div className='content-panel'><HistoryC/></div>;
}