import React, { useState } from 'react';
import { makeStyles, Theme, createStyles } from '@material-ui/core/styles';
import Modal from '@material-ui/core/Modal';
import Backdrop from '@material-ui/core/Backdrop';
import Fade from '@material-ui/core/Fade';
import './modal.scss';
import '../../assets/css/style.scss'
import Label from '../Label/Label';
import api from '../../api';
import swal from 'sweetalert';
import CloseIcon from '@material-ui/icons/Close';
import Load from '../Loading/Load';
import InputFilesMultiValue from '../Input/InputFilesMultiValue';
import axios from 'axios';


const useStyles = makeStyles((theme: Theme) =>
    createStyles({
        modal: {
            display: 'flex',
            alignItems: 'center',
            justifyContent: 'center',
        },
        paper: {
            backgroundColor: theme.palette.background.paper,
            border: 'none !important',
            borderRadius: '15px',
            width: '95%',
            maxWidth: '500px',
            boxShadow: theme.shadows[5],
            padding: theme.spacing(2, 4, 3),
        },
    }),
);

export default function TransferedProofModal(props) {
    const classes = useStyles();
    const [open, setOpen] = React.useState(false);
    const [filesProof, setFilesProof] = useState([]);
    const [loading, setIsLogin] = useState(false);



    const handleOpen = () => {
        setOpen(true);
    };

    const handleClose = () => {
        setOpen(false);
    };

    function handleFile(e) {
        console.log(e.target.files)
        let files = e.target.files;
        setFilesProof(files)
    }

    function handleSubmitImages(e) {
        e.preventDefault();
        setIsLogin(true);
            let formData = new FormData()
            for (let i = 0; i < filesProof.length; i++) {
                formData.append(`file[]`, filesProof[i])
            }
            formData.append('credit_state_id', props.credit_state_id)
            let token = localStorage.getItem("token");
            console.log(token)
            console.log(props.id_credit_date)
            axios.post(`http://165.22.0.9/api/credit_user/${props.id_credit_date}/changeStatus`, formData, {
                headers: {
                    // 'Content-Type': 'multipart/form-data',
                    'Content-Type': 'application/json',

                    Authorization: `Bearer ${token}`,
                }
            })
                .then((response) => {
                    console.log(response)
                    if (response.data.type == 'error' && response.data.code == 500) {
                        setIsLogin(false);
                        swal({
                            title: "Importante!",
                            text: response.data.message,
                            icon: "warning",
                            button: true,
                            dangerMode: false,
                        })
                            .then(willDelete => {
                                if (willDelete) {
                                    window.location = '/request';
                                    handleClose()
                                    setIsLogin(false);
                                }
                            })
                    } else {
                        setIsLogin(false);
                        swal({
                            title: "Correcto!",
                            text: response.data.message,
                            icon: "success",
                            button: true,
                            dangerMode: false,
                        })
                            .then(willDelete => {
                                if (willDelete) {
                                    window.location = '/request';
                                    handleClose()
                                    setIsLogin(false);
                                }
                            })
                    }
                })
                .catch((error) => {
                    console.log(error)
                })
    }

    return (
        <React.Fragment>
            <button type="button" onClick={handleOpen} className="btn btn-primary btn-sm mr-2" >Depositado</button>
            <Modal
                aria-labelledby="transition-modal-title"
                aria-describedby="transition-modal-description"
                className={classes.modal}
                open={open}
                onClose={handleClose}
                closeAfterTransition
                BackdropComponent={Backdrop}
                BackdropProps={{
                    timeout: 500,
                }}
            >
                <Fade in={open}>
                    {loading == true
                        ?
                        <Load />
                        :
                        <div className={classes.paper}>
                            <div className='modal-contact'>
                                <div style={{ textAlign: "right" }}><button onClick={handleClose}><CloseIcon fontSize="large" /></button></div>
                                <h3 className='text-center'>Subir Voucher</h3>

                                <form>
                                    <div className='form-group'>
                                        <InputFilesMultiValue
                                            attribute={{
                                                id: 'proof_of_payments',
                                                name: 'proof_of_payments',
                                                type: 'file',
                                                accept: ".jpg, .png, .jpeg",
                                            }}
                                            key='1'
                                            handleChange={handleFile}
                                        />
                                    </div>
                                    <div className='form-group'>
                                        <button className='btn-dark btn-block bt-submit' onClick={handleSubmitImages}>Subir</button>
                                    </div>
                                </form>
                            </div>
                        </div>
                    }
                </Fade>
            </Modal>
        </React.Fragment>
    );
}