import React, { useState } from "react";
import InputFileValue from '../../../componets/Input/InputFileValue';
import RequestMessage from '../../../componets/Messages/RequestMessage';

//Material UI
import { makeStyles } from '@material-ui/core/styles';
import { blue } from '@material-ui/core/colors';
import InfoOutlinedIcon from '@material-ui/icons/InfoOutlined';
import Popper from '@material-ui/core/Popper';
import Button from '@material-ui/core/Button';
import Fade from '@material-ui/core/Fade';
import Paper from '@material-ui/core/Paper';
import Typography from '@material-ui/core/Typography';
import PhotoCameraIcon from '@material-ui/icons/PhotoCamera';
import AddAPhotoIcon from '@material-ui/icons/AddAPhoto';

// Assests
import Info1 from '../../../assets/images/Info-1.png';
import Info2 from '../../../assets/images/Info-2.png';
import Info3 from '../../../assets/images/Info-3.png';
import Info4 from '../../../assets/images/Info-4.png';

const useStyles = makeStyles((theme) => ({
    root: {
        width: 500,
    },
    typography: {
        padding: theme.spacing(2),
    },
}));

export default function FileUser(props) {
    const state = props.state;
    const classes = useStyles();
    const [openONE, setOpenONE] = useState(false);
    const [openTWO, setOpenTWO] = useState(false);
    const [openTHREE, setOpenTHREE] = useState(false);
    const [openFOUR, setOpenFOUR] = useState(false);
    const [placementONE, setPlacementONE] = useState();
    const [placementTWO, setPlacementTWO] = useState();
    const [placementTHREE, setPlacementTHREE] = useState();
    const [placementFOUR, setPlacementFOUR] = useState();
    const [anchorElONE, setAnchorElONE] = useState(null);
    const [anchorElTWO, setAnchorElTWO] = useState(null);
    const [anchorElTHREE, setAnchorElTHREE] = useState(null);
    const [anchorElFOUR, setAnchorElFOUR] = useState(null);



    const handleClickONE = (newPlacementONE) => (event) => {
        setAnchorElONE(event.currentTarget);
        setOpenONE((prev) => placementONE !== newPlacementONE || !prev);
        setPlacementONE(newPlacementONE);
    };
    const handleClickTWO = (newPlacementTWO) => (event) => {
        setAnchorElTWO(event.currentTarget);
        setOpenTWO((prev) => placementTWO !== newPlacementTWO || !prev);
        setPlacementTWO(newPlacementTWO);
    };
    const handleClickTHREE = (newPlacementTHREE) => (event) => {
        setAnchorElTHREE(event.currentTarget);
        setOpenTHREE((prev) => placementTHREE !== newPlacementTHREE || !prev);
        setPlacementTHREE(newPlacementTHREE);
    };
    const handleClickFOUR = (newPlacementFOUR) => (event) => {
        setAnchorElFOUR(event.currentTarget);
        setOpenFOUR((prev) => placementFOUR !== newPlacementFOUR || !prev);
        setPlacementFOUR(newPlacementFOUR);
    };

    return (
        <React.Fragment>
            <div className='title-card'>
                <br /><h1>Archivos </h1>
            </div>
            <div className='body-card'>
                {state.messageRequestFile &&
                    <div className='col-xl-12 col-xs-12 col-md-12 col-sm-12'>
                        <div className='form-group'><br />
                            <RequestMessage attribute={state.messageRequestFile} />
                        </div><br />
                    </div>
                }
                <div className='user-info'>
                    <div className='row mt-2'>
                        {/* <form> */}
                        <div className='col col-12 col-md-6 mt-3'>
                            <div className='form-group text-center'>
                                <p className='data-label'>Identificación Oficial - Frontal
                                    <Button onClick={handleClickONE('top')}><InfoOutlinedIcon style={{ fontSize: 15, color: blue[800] }}></InfoOutlinedIcon></Button>
                                    <Popper open={openONE} anchorEl={anchorElONE} placement={placementONE} transition>
                                        {({ TransitionProps }) => (
                                            <Fade {...TransitionProps} timeout={350}>
                                                <Paper>
                                                    <Typography className={classes.typography}>
                                                        <div className='info'>
                                                            <p>Identificación Oficial - Frontal</p>
                                                            <div className='info-img'>
                                                                <img src={Info1} alt="Fotografía de Identificación Oficial Frontal" />
                                                            </div>
                                                        </div>
                                                    </Typography>
                                                </Paper>
                                            </Fade>
                                        )}
                                    </Popper>
                                </p>
                                <label htmlFor='photo_ine_front' className='btn-upload'>
                                    <div>
                                        <PhotoCameraIcon fontSize="small"/> 
                                        Subir Foto
                                    </div>

                                    <InputFileValue
                                        attribute={{
                                            camera: 'camera',
                                            id: 'photo_ine_front',
                                            name: 'photo_ine_front',
                                            type: 'file',
                                            accept: ".jpg, .png, .jpeg",
                                        }}
                                        key={props.fileKey}
                                        handleChange={props.handleFile}
                                    />
                                </label>
                            </div>
                            {state.client.photo_ine_front &&
                                <p className='data-label text-success'>Archivo Subido</p>
                            }


                        </div>
                        <div className='col col-12 col-md-6 mt-3'>
                            <div className='form-group text-center'>
                                <p className='data-label'>Identificación Oficial - Posterior
                                <Button onClick={handleClickTWO('top')}><InfoOutlinedIcon style={{ fontSize: 15, color: blue[800] }}></InfoOutlinedIcon></Button>
                                    <Popper open={openTWO} anchorEl={anchorElTWO} placement={placementTWO} transition>
                                        {({ TransitionProps }) => (
                                            <Fade {...TransitionProps} timeout={350}>
                                                <Paper>
                                                    <Typography className={classes.typography}>
                                                        <div className='info'>
                                                            <p>Identificación Oficial - Posterior</p>
                                                            <div className='info-img'>
                                                                <img src={Info2} alt="Fotografía de Identificación Oficial Frontal" />
                                                            </div>
                                                        </div>
                                                    </Typography>
                                                </Paper>
                                            </Fade>
                                        )}
                                    </Popper>
                                </p>
                                <label htmlFor='photo_ine_back' className='btn-upload'>
                                    <div>
                                        <PhotoCameraIcon fontSize="small"/> 
                                        Subir Foto
                                    </div>
                                    <InputFileValue
                                        attribute={{
                                            camera: 'camera',
                                            id: 'photo_ine_back',
                                            name: 'photo_ine_back',
                                            type: 'file',
                                            accept: ".jpg, .png, .jpeg"
                                        }}
                                        key={props.fileKey}
                                        handleChange={props.handleFile}
                                    />
                                </label>
                            </div>
                            {state.client.photo_ine_back &&
                                <p className='data-label text-success'>Archivo Subido</p>
                            }

                        </div>
                        <div className='col col-12 col-md-6 mt-3'>
                            <div className='form-group text-center'>
                                <p className='data-label'>Selfie con Identificación
                                <Button onClick={handleClickTHREE('top')}><InfoOutlinedIcon style={{ fontSize: 15, color: blue[800] }}></InfoOutlinedIcon></Button>
                                    <Popper open={openTHREE} anchorEl={anchorElTHREE} placement={placementTHREE} transition>
                                        {({ TransitionProps }) => (
                                            <Fade {...TransitionProps} timeout={350}>
                                                <Paper>
                                                    <Typography className={classes.typography}>
                                                        <div className='info'>
                                                            <p>Selfie con Identificación</p>
                                                            <div className='info-img'>
                                                                <img src={Info3} alt="Fotografía de Identificación Oficial Frontal" />
                                                            </div>
                                                        </div>
                                                    </Typography>
                                                </Paper>
                                            </Fade>
                                        )}
                                    </Popper>
                                </p>
                                <label htmlFor='photo_face' className='btn-upload'>
                                    <div>
                                        <PhotoCameraIcon fontSize="small"/> 
                                        Subir Foto
                                    </div>
                                    <InputFileValue
                                        attribute={{
                                            camera: 'camera',
                                            id: 'photo_face',
                                            name: 'photo_face',
                                            type: 'file',
                                            accept: ".jpg, .png, .jpeg"
                                        }}
                                        key={props.fileKey}
                                        handleChange={props.handleFile}
                                    />
                                </label>
                            </div>
                            {state.client.photo_face &&
                                <p className='data-label text-success'>Archivo Subido</p>
                            }

                        </div>
                        <div className='col col-12 col-md-6 mt-3'>
                            <div className='form-group text-center'>
                                <p className='data-label'>Comprobante Domiliciario
                                <Button onClick={handleClickFOUR('top')}><InfoOutlinedIcon style={{ fontSize: 15, color: blue[800] }}></InfoOutlinedIcon></Button>
                                    <Popper open={openFOUR} anchorEl={anchorElFOUR} placement={placementFOUR} transition>
                                        {({ TransitionProps }) => (
                                            <Fade {...TransitionProps} timeout={350}>
                                                <Paper>
                                                    <Typography className={classes.typography}>
                                                     <div className='info'>
                                                            <p>Comprobante Domiliciario</p>
                                                            <div className='info-img'>
                                                                <img src={Info4} alt="Fotografía de Identificación Oficial Frontal" />
                                                            </div>
                                                        </div>
                                                    </Typography>
                                                </Paper>
                                            </Fade>
                                        )}
                                    </Popper>
                                </p>
                                <label htmlFor='domiciliary_proof' className='btn-upload'>
                                    <div>
                                        <PhotoCameraIcon fontSize="small"/> 
                                        Subir Foto
                                    </div>
                                    <InputFileValue
                                        attribute={{
                                            camera: 'camera',
                                            id: 'domiciliary_proof',
                                            name: 'domiciliary_proof',
                                            type: 'file',
                                            accept: ".jpg, .png, .jpeg"
                                        }}
                                        key={props.fileKey}
                                        handleChange={props.handleFile}
                                    />
                                </label>
                            </div>
                            {state.client.domiciliary_proof &&
                                <p className='data-label text-success'>Archivo Subido</p>
                            }

                        </div>
                        <div className='col-12 text-center mt-3'>
                            <button className='btn-dark' disabled={props.state.disabledButton} onClick={props.handleSubmitImages} >Subir Imágenes</button>
                        </div>
                        {/* </form> */}
                    </div>
                </div>
            </div>
        </React.Fragment>
    );
}
