const isSalary = (value) => {
    if (value.length < 3) {
        return 'Llene correctamente la información';
    }else if(!value.trim()){
        return 'Esta informacion es requerida';
    } else if (!(/^[0-9]{0,20}(\.?)[0-9]{0,2}$/.test(value))) {
        return 'Llene correctamente esta infomación';
    }
    return "nodata";
}
export default isSalary;