import React, { useState } from 'react';
import '../../../src/assets/css/style.scss';
import './login.scss';
import Label from './../../componets/Label/Label';
import Input from './../../componets/Input/Input';
import LoginButton from '../../componets/LoginButton/LoginButton';
import Header from '../../componets/Header/Hedear';
import RequestMessage from '../../componets/Messages/RequestMessage';
import { Redirect } from "react-router-dom";
import { Link } from 'react-router-dom';


import api from '../../api';

const Login = () => {

    const [email, setEmail] = useState('');
    const [password, setPassword] = useState('');
    const [isLogin, setIsLogin] = useState(false);
    const [emailError, setEmailError] = useState(false);
    const [passwordError, setPasswordError] = useState(false);

    const [disabledButton, setDisabledButton] = useState(true);

    const [messageRequest , setMessageRequest] = useState('');


    function handleChange(name, value) {
        setMessageRequest('');
        if (name === 'email') {
            const regex = /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
            if (!value.trim()) {
                setEmailError('Correo Electrónico requerido')
                setPasswordError(true);
            } else if (!regex.test(String(value).toLowerCase())) {
                setEmailError('Correo Electrónico no válido')
                setPasswordError(true);
            } else {
                setEmail(value);
                setEmailError(false);
                setPasswordError(true);
            }
        } else {
            if (value.length < 8) {
                setPasswordError(true);
                setDisabledButton(true);
            } else {
                setPassword(value);
                setPasswordError(false);
                setDisabledButton(false);
            }
        }
    };

    function handleSubmit() {
        let account = { email, password }
        api.login(account)
            .then(data => {
                console.log(data);
                console.log(data.code);
                if (data.code==401) {
                    setMessageRequest([{label: data.message, id:1}]);
                }else{
                    localStorage.setItem("token", data.access_token);
                    localStorage.setItem("user", JSON.stringify(data.user));
                    setIsLogin(true);
                    window.location = '/credit';
                }
            })
            .catch((error) => {
                console.log(error)
            })
            
    };




    if (localStorage.getItem('token')) return <Redirect to="/credit" />;
    return (

        <section>
            <Header />
            <div className='card-login card card-form space-form'>
                <h3 className='text-center'>Iniciar Sesión</h3>
                <div className='form-group'>
                    <LoginButton />
                </div>
                <div className='form-group'>
                    {messageRequest &&
                        <RequestMessage attribute={messageRequest}/> 
                    }
                </div>
                <div className='form-group'>
                    <Label text='Correo electrónico' />
                    <Input
                        attribute={{
                            id: 'email',
                            name: 'email',
                            type: 'text',
                            placeholder: 'Correo Electrónico'
                        }}
                        handleChange={handleChange}
                    />
                    {emailError &&
                        <label className='label-error'>{emailError}</label>
                    }
                </div>
                <div className='form-group'>
                    <Label text='Contraseña' />
                    <Input
                        attribute={{
                            id: 'password',
                            name: 'password',
                            type: 'password',
                            placeholder: 'Contraseña'
                        }}
                        handleChange={handleChange}
                    />
                    {passwordError &&
                        <label className='label-error'>La contraseña debe contener al menos 8 carácteres.</label>
                    }
                </div>
                <div className='text-center'>
                    <button disabled={disabledButton} className='btn-dark btn-block bt-submit' onClick={handleSubmit}>Ingresar</button>
                </div>
                <div className='login-btn-sidebar'>
                    <p>
                        No tienes cuenta <Link className='link' to="/register">Crear cuenta</Link>
                    </p>
                </div>
                <div className='row recovery'>
                    <div className='col-6 btn-recovery'>
                        <Link className='link text-center recovery-item' to="/recover_password">Recuperar contraseña</Link>
                    </div>
                    <div className='col-6 btn-recovery'>
                        <Link className='link text-center recovery-item' to="/recover_email">Recuperar Correo</Link>
                    </div>
                </div>
            </div>
        </section>
    )
};

export default Login;