import React from "react";
import api from '../../../api';
import Load from '../../../componets/Loading/Load';


class Credit extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            tabulador: [],
            loading: true,
        }
        this.abortController = new AbortController();
        this.signal = this.abortController.signal;
    }
    componentDidMount() {
        this.getCreditsTabulador()
    }
    getCreditsTabulador = () => {
        api.lists.getCreditsTabulador(this.signal)
            .then(res => {
                console.log(res)
                this.setState({
                    ...this.state,
                    tabulador: res,
                    loading:false,
                });
            });
    }
    render() {
        if (this.state.loading) {
            return <Load />
        }
        let credits = '';
        let creditsAll = this.state.tabulador ? this.state.tabulador : [];

        credits = Object.values(creditsAll).map((info) => {
            let creditsDate = info.credit_date ? info.credit_date : [];
            
            let time_credits_date = '';
            time_credits_date = Object.values(creditsDate).map((date_credit) => {
                return (
                    <ul style={{listStyle: "none"}}>
                        <li>{date_credit.num_date} {date_credit.date_name}</li>
                    </ul>

                )
            })
            let interest_credits_date = '';
            interest_credits_date = Object.values(creditsDate).map((date_credit) => {
                return (
                    <ul style={{listStyle: "none"}}>
                        <li>{date_credit.interest} %</li>
                    </ul>

                )
            })
            let num_pagos_credits_date = '';
            num_pagos_credits_date = Object.values(creditsDate).map((date_credit) => {
                let plural=date_credit.num_of_payments > 1 ? 'pagos' : 'pago'
                return (
                    <ul style={{listStyle: "none"}}>
                        <li>{date_credit.num_of_payments} {plural}</li>
                    </ul>

                )
            })
            let total_payments_credits_date = '';
            total_payments_credits_date = Object.values(creditsDate).map((date_credit) => {
                return (
                    <ul style={{listStyle: "none"}}>
                        <li>$ {date_credit.total_payment} </li>
                    </ul>

                )
            })
            return (
                <tr key={info.id} className='text-center'>
                    <td>{info.id}</td>
                    <td>{info.value} {info.currency.iso}</td>
                    <td>{time_credits_date}</td>
                    <td>{interest_credits_date}</td>
                    <td>{num_pagos_credits_date}</td>
                    <td>{total_payments_credits_date}</td>
                </tr>

            )
        })
        return (
            <section className='container'>
                <div className='row'>
                    <div className='col-12 mt-4'>
                        <div className='card p-4'>
                            Tabulador de créditos
                            <div className='table-responsive'>
                                <table className="table">
                                    <thead>
                                        <tr className='text-center'>
                                            <th scope="col">ID</th>
                                            <th scope="col">Crédito</th>
                                            <th scope="col">Tiempo</th>
                                            <th scope="col">% de interes</th>
                                            <th scope="col">No. de pagos</th>
                                            <th scope="col">Cant a pagar</th>
                                        </tr>
                                    </thead>
                                    <tbody>
                                        {credits}
                                    </tbody>
                                </table>
                            </div>
                        </div>
                    </div>
                </div>
            </section>

        );
    }
}

export default Credit;