import React from "react";
import api from '../../api';

class Credit extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            simulator: [],
            loading: true,
        }
        this.abortController = new AbortController();
        this.signal = this.abortController.signal;
    }
    componentDidMount() {
        this.getSimulator()
    }
    getSimulator = () => {
        api.lists.getSimulator(this.signal)
            .then(res => {
                this.setState({
                    ...this.state,
                    simulator: res,
                });
            });
    }
    render() {
        let credits = '';
        let creditsAll = this.state.simulator ? this.state.simulator : [];

        credits = Object.values(creditsAll).map((info) => {
            let creditsDate = info.rules ? info.rules : [];
                
            let rules_credits_simulator = '';
            rules_credits_simulator = Object.values(creditsDate).map((rules) => {
                let pago = 'pago';
                        if (rules.num_of_payments != 1) {
                            pago = 'pagos';
                        }
                return (
                    <div className='col col-12 col-md-4 mb-2'>
                        <div className='second-amount'>
                            <p>{rules.num_of_payments} {pago} de <span>${rules.total_pay}</span></p>
                            <p>Y un interes del <span>{rules.interest} %</span></p>
                        </div>
                    </div>
                )
            })
            return (
                    <div className='credit-item' key={info.credit}>
                        <span className='credit-mount'>
                            $ {info.credit}
                        </span>
                        <div className='row'>
                            <p className='col col-12 mb-1'>Con los siguientes planes</p>
                            {rules_credits_simulator}
                        </div>
                    </div>
            )
        })

        return (
            <React.Fragment>
                {credits}
            </React.Fragment>
        );
    }
}

export default Credit;