import React from "react";
import CreditApproved from '../Credit/CreditApproved';
import CreditRejected from '../Credit/CreditRejected';
import CreditLiquidated from "./CreditLiquidated";

class CreditHistory extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
        }
        this.abortController = new AbortController();
        this.signal = this.abortController.signal;
    }
    render() {
        console.log(this.props.data)
        let approved=this.props.data.credits_approved ? this.props.data.credits_approved : []
        let reject=this.props.data.credits_reject ? this.props.data.credits_reject : []
        let liquidated=this.props.data.credits_liquidated ? this.props.data.credits_liquidated : []
        return (
            <div className='card-panel p-4'>
                <div className='title-card'>
                    <h1>Historial</h1>
                </div>
                <div className='body-card'>
                    {approved.length == 0 && reject.length == 0 && liquidated.length == 0
                        ?
                        <div className='credit-item text-center'>
                            <label>Todavía no tienes Historial</label>
                        </div>
                        :
                        <div className='credit-item'>
                            <CreditApproved credits={approved} />
                            <CreditRejected credits={reject} />
                            <CreditLiquidated credits={liquidated} />
                        </div>
                    }

                </div>
            </div>
        );
    }
}

export default CreditHistory;