import React, { Component } from 'react';
import { BrowserRouter, Switch, Route, Redirect } from "react-router-dom";
import Error from './Pages/Error/Error';
import HomeUser from './Pages/HomeUser/HomeUser';
import HomeAdmin from './Pages/UserAdmin/HomeAdmin';
// import PrivateRoute from './/componets/PrivateRoute';

class Router extends Component {

    render() {
        if (!localStorage.getItem('token')) return <Redirect to="/login" />;
        return (


            <BrowserRouter>
                <Switch>
                    <Route exact path="/credit" component={HomeUser} />
                    <Route exact path="/profile" component={HomeUser} />
                    <Route exact path="/credit_payments" component={HomeUser} />
                    <Route exact path="/payment_methods" component={HomeUser} />
                    <Route exact path="/credit_history" component={HomeUser} />
                    <Route exact path="/credit_details" component={HomeUser} />
                    <Route exact path="/details_info_credit" component={HomeUser} />


                    <Route exact path="/notifications" component={HomeUser} />
                    <Route exact path="/tabulator" component={HomeAdmin} />
                    <Route exact path="/clients" component={HomeAdmin} />
                    <Route exact path="/request" component={HomeAdmin} />
                    <Route exact path="/credit_applications" component={HomeAdmin} />
                    <Route exact path="/users" component={HomeAdmin} />
                    <Route exact path="/credit_more_support" component={HomeAdmin} />
                    <Route exact path="/problems_credits_support" component={HomeAdmin} />
                    <Route exact path="/general_support" component={HomeAdmin} />
                    <Route exact path="/client_history" component={HomeAdmin} />
                    <Route exact path="/message_ready_moreCredits" component={HomeAdmin} />
                    <Route exact path="/message_ready_problemsCredits" component={HomeAdmin} />
                    <Route exact path="/message_ready_generalSupport" component={HomeAdmin} />
                    <Route exact path="/history_credits_client" component={HomeAdmin} />
                    <Route component={Error} />
                </Switch>
            </BrowserRouter>
        );

    }
}

export default Router;