import React from "react";

const InputFilesMultiValue = ( {attribute,fileKey, handleChange, param} ) => {
    return(
        <div>
          <input
            id={attribute.id}
            name={attribute.name}
            type={attribute.type}
            accept={attribute.accept}
            key={fileKey}
            multiple
            onChange={(e) => handleChange(e)}
            className={param ? 'input-error' : 'input'}
          />
        </div>
    );
}

export default InputFilesMultiValue;