import React, { useEffect, useState } from "react";
import './credit.scss';
import InfoPayment from '../../../componets/Modal/InfoPayment';
import Load from '../../../componets/Loading/Load';
import { useLocation } from "react-router-dom";
import api from '../../../api';
import GeneratePDF from "./GeneratePDF";
import PaymentForm from "./Payment";
import { Link } from 'react-router-dom';
import KeyboardBackspaceIcon from '@material-ui/icons/KeyboardBackspace';
import RequestMessage from '../../../componets/Messages/RequestMessage';

const PaymentMethods = () => {
    let schedule_payment_id = useLocation();
    useEffect(() => {
        getPlataform();
    }, []);

    const [pathImages, setPathImages] = useState('');
    const [plataforms, setPlataforms] = useState('');
    const [loading, setLoading] = useState(true);
    const [messageRequest , setMessageRequest] = useState('');


    const [createPDF, setCreatePDF] = useState(false);
    const [infoTalon, setInfoTalon] = useState('');

    const [infoChargeRequest, setInfoChargeRequest] = useState(false);
    const [card_id_plataform, setCard_id_plataform] = useState('');

    const getPlataform = async () => {
        api.lists.paymentsPlataform()
            .then(res => {
                setPlataforms(res)
                setLoading(false);
            });
        setPathImages(api.base_url_img())
    };
    function handlePay(e, id) {
        e.preventDefault();
        const body = {
            schedule_payment_id: schedule_payment_id.state.id,
            payment_platform_id: id,
        }
        api.create.pay_schedule(body)
            .then(res => {
                console.log(res)
                if (res.code) {
                    setMessageRequest([{label: res.message, id:1}]);
                } else {
                    setLoading(false);
                    setCreatePDF(true);
                    setInfoTalon(res);
                }
            });
    }

    function handleCard(e, id) {
        e.preventDefault();
        setInfoChargeRequest(true);
        setCard_id_plataform(id);

    }

    if (loading == true) {
        return <Load />
    } else {
        if (createPDF == true) {
            return (
                <React.Fragment>
                    <GeneratePDF info={infoTalon} />
                </React.Fragment>
            )
        } else if (infoChargeRequest == true) {
            return (
                <React.Fragment>
                    <PaymentForm schedule_id={schedule_payment_id.state.id} plataform_id={card_id_plataform} />
                </React.Fragment>
            )
        } else {
            let plataformsShow = Object.values(plataforms).map((plataform) => {
                let path_image = pathImages + plataform.image
                return (
                    <React.Fragment>
                        <div className='col col-6 col-md-4 col-xl-2 mb-2'>
                            <div className='card-method'>
                                <img src={path_image} alt="Método de pago con Oxxo" />
                            </div>
                            {
                                plataform.name == "Oxxo Pay"
                                    ?
                                    <button className='btn-dark btn-block' onClick={(e) => handlePay(e, plataform.id)}  >{plataform.name}</button>
                                    :
                                    <button className='btn-dark btn-block' onClick={(e) => handleCard(e, plataform.id)}  >{plataform.name}</button>
                            }
                        </div>
                    </React.Fragment>

                )

            })
            return (
                <React.Fragment>
                    <div className='card-panel mb-footer'>
                        <div className='title-card'>
                            <Link to="/credit"><KeyboardBackspaceIcon fontSize="large" /> </Link>
                            <h1 className='text-center'>Selecciona un método de pago</h1>
                        </div>
                        <div className='body-card'>
                            <div className='form-group'>
                                {messageRequest &&
                                    <RequestMessage attribute={messageRequest} />
                                }
                            </div>
                            <div className='row mt-4'>
                                {plataformsShow}
                            </div>
                        </div>
                    </div>
                </React.Fragment>
            )

        }
    }
};

export default PaymentMethods;





