import React from "react";
import { Link } from 'react-router-dom';
import ExpandMoreIcon from '@material-ui/icons/ExpandMore';
import api from '../../api';

class Credit extends React.Component {
    handleLogout = e => {
       
        console.log(localStorage.getItem("token"))
          api.logout(localStorage.getItem("token"))
            .then(data => {
                console.log(data);
                localStorage.removeItem("token");
                localStorage.removeItem("user");
                window.location='/admin';
            });
    }

    render() {

        let user=[];
        if (localStorage.getItem('user')) {
             user = JSON.parse(localStorage.getItem('user'));
        }

        return (
            <React.Fragment>
                <header className='header-admin'>
                    <div className='row container'>
                        <div className='col col-12'>
                            <div className='user-admin'>
                                <div className='mr-6'>
                                    <p className='mr-6'>{user.name} {user.last_name}</p>
                                </div>

                                <div className='btn-option'>
                                    <span className='down-menu'>
                                        <ExpandMoreIcon/>
                                        <ul>
                                            <li>
                                                <Link to=''  onClick={this.handleLogout}>Cerrar Sesión</Link>
                                            </li>
                                        </ul>
                                    </span>
                                </div>
                                
                            </div>
                        </div>
                    </div>
                </header>
                <nav className='menu-admin'>
                    <ul className='row container'>
                        <li className='col-2 m-0'>
                            <a className='nav-item'>Crédito</a>
                            <ul className='sub-menu'>
                                <li className='m-0'><Link className='item-link' to='/tabulator'>Tabulador de créditos MX</Link></li>
                            </ul>
                        </li>
                        <li className='col-2 m-0'>
                            <a className='nav-item'>Solicitudes</a>
                            <ul className='sub-menu'>
                                <li className='m-0'><Link className='item-link' to='credit_applications'>Solicitudes de Créditos</Link></li>
                            </ul>
                        </li>
                        <li className='col-2 m-0'>
                            <a className='nav-item'>Usuarios</a>
                            <ul>
                                <li className='m-0'><Link className='item-link' to='users'>Listas de usuarios</Link></li>
                            </ul>
                        </li>
                        <li className='col-2 m-0'>
                            <a className='nav-item'>Clientes</a>
                            <ul>
                                <li className='m-0'><Link className='item-link' to='/clients'>Lista de Clientes</Link></li>
                            </ul>
                        </li>
                        <li className='col-2 m-0'>
                            <a className='nav-item'>Historial</a>
                            <ul>
                                <li className='m-0'><Link className='item-link' to='/client_history'>Historial de Clientes</Link></li>
                                <li className='m-0'><Link className='item-link' to='/admin_history'>Historial de Personal</Link></li>
                            </ul>
                        </li>
                        <li className='col-2 m-0'>
                            <a className='nav-item'>Soporte</a>
                            <ul>
                                <li className='m-0'><Link className='item-link' to='/general_support'>Mensajes de Soporte</Link></li>
                                <li className='m-0'><Link className='item-link' to='/credit_more_support'>Mensajes de Solicitar más Crédito</Link></li>
                                <li className='m-0'><Link className='item-link' to='/problems_credits_support'>Mensajes de Problemas con Créditos</Link></li>
                            </ul>
                        </li>
                    </ul>
                </nav>
            </React.Fragment>
        );
    }
}

export default Credit;