import React, {useState} from 'react';
import '../../../src/assets/css/style.scss';
import Label from './../../componets/Label/Label';
import Input from './../../componets/Input/Input';
import Header from '../../componets/Header/Hedear';
import { NavLink } from 'react-router-dom';
import axios from 'axios';
import RequestMessage from '../../componets/Messages/RequestMessage';



const RecoverEmail = () =>{
    const [email, setEmail] = useState('');
    const [phone, setPhone] = useState('');
    const [password, setPassword] = useState('');
    const [passwordconfirm, setPasswordconfirm] = useState('');
    const [emailError, setEmailError] = useState(false);
    const [phoneError, setPhoneError] = useState(false);
    const [passwordError, setPasswordError] = useState(false);
    const [passwordErrorConfirm, setPasswordErrorConfirm] = useState(false);
    
    const [disabledButton, setDisabledButton] = useState(true);
    const [ErrorsMessage, setErrorsMessage] = useState('');
    const [messageRequest , setMessageRequest] = useState('');


    function handleChange(name, value){
        setMessageRequest('');
        switch (name) {
            case 'email':
                const regex = /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;   
                if (!value.trim()) {
                    setEmailError('Correo Electrónico requerido')
                    setPasswordErrorConfirm(true);
                }else if(!regex.test(String(value).toLowerCase())){
                    setEmailError('Correo Electrónico no válido')
                    setPasswordErrorConfirm(true);
                }else{
                    setEmail(value);
                    setEmailError(false)
                    setPasswordErrorConfirm(true);
                    setPhoneError('Teléfono requerido');
                }
            break;
            case 'phone':
                if(!value.trim()){
                    setPhoneError('Teléfono requerido');
                    setDisabledButton(true);
                }else if(!(/^[0-9]{0,20}(\.?)[0-9]{0,2}$/.test(value))) {
                    setPhoneError('Teléfono inválido');
                    setDisabledButton(true);
                }else{
                    setPhone(value);
                    setPhoneError(false);
                    setDisabledButton(true);
                }
            break;
        }
    };

    function handleSubmit(){
        let account = {email,password,passwordconfirm, phone}
        console.log(account);
        if(account){
            axios.post('http://165.22.0.9/api/auth/signup',{
                email:email,
                phone:phone,
                password:password,
                password_confirmation:passwordconfirm
            })
            .then((response)=>{
                console.log(response)
                localStorage.setItem('token',response.data.access_token);
                localStorage.setItem('user',JSON.stringify(response.data.user));
                window.location='/credit';
            })
            .catch((error)=>{
                let errorsMe = [];

                let myError=error.response.data.errors;
                console.log(myError)
                Object.values(myError).forEach((error, i)  => {
                    console.log(error[0])
                    errorsMe.push({
                        label: error[0],
                        id: i,
                    });
                });
                console.log(errorsMe)
                setMessageRequest(errorsMe);
                
            })

        }
    };

  

    return(
        <section>
            <Header/>
            <div className='card card-form space-form'>
                <h3 className='text-center'>Recuperar Correo Electrónico</h3>
                <div className='form-group'>
                    {messageRequest &&
                        <RequestMessage attribute={messageRequest}/> 
                    }
                </div>

                <div className='form-group'>  
                    <Label text='CURP'/>
                    <Input
                        attribute={{
                            id: 'email',
                            name: 'email',
                            type: 'email',
                            placeholder: 'CURP',
                           
                        }}
                        handleChange={handleChange}
                    />
                        { emailError && 
                            <label className='label-error'>{emailError}</label>
                        }
                </div>
                <div className='form-group'>  
                    <Label text='Teléfono'/>
                    <Input
                        attribute={{
                            id: 'phone',
                            name: 'phone',
                            type: 'text',
                            placeholder: 'Teléfono',
                           
                        }}
                        handleChange={handleChange}
                    />
                        { phoneError && 
                            <label className='label-error'>{phoneError}</label>
                        }
                </div>
                <div className='text-center'>
                    <button disabled={disabledButton} className='btn-dark full-width bt-submit' onClick={handleSubmit}>Recuperar Correo</button>
                </div>

                <div className='login-btn-sidebar'>
                    <p>
                        <NavLink className='link' to="/login">Iniciar Sesión</NavLink>
                    </p>
                </div>
            </div>
        </section>
       
    )
};

export default RecoverEmail;