import React from 'react';
import api from '../../../api';
import Load from '../../../componets/Loading/Load';
import { Link } from 'react-router-dom';
import KeyboardBackspaceIcon from '@material-ui/icons/KeyboardBackspace';
import moment from 'moment';

class HistoryCreditsClients extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            loading: true,
        }
        this.abortController = new AbortController();
        this.signal = this.abortController.signal;
    }
    componentDidMount() {
        this.isLoading()

    }
    isLoading = () => {
        this.setState({
            ...this.state,
            loading: false,

        });
    }
    render() {
        console.log(this.props)
        if (this.state.loading) {
            return <Load />
        }
        let credits_date = '';
        let creditsDateAll = this.props.data.credit_date_user ? this.props.data.credit_date_user : [];

        credits_date = Object.values(creditsDateAll).map((credit) => {
            let pluralpago='pago';
            if (credit.credit_date.num_of_payments > 1) {
                pluralpago='pagos'
            }
            let datename=credit.credit_date.date.name
            if (credit.credit_date.num_date > 1) {
                datename=credit.credit_date.date.plural_name
            }
            return (
                <tr key={credit.id} className='text-center'>
                    <td>{credit.folio}</td>
                    <td>$ {credit.credit_date.credit.value}</td>
                    <td>{credit.credit_date.num_of_payments} {pluralpago} en {credit.credit_date.num_date} {datename}</td>
                    <td>{credit.credit_date.interest} %</td>
                    <td>{moment(credit.created_at).format("DD-MM-YYYY")}</td>
                    <td>{credit.credit_state.name}</td>
                    <td>Historial de Pagos</td>

                </tr>
            )

        })
        return (
            <section className='container'>
                <div className='row'>
                    <div className='col-12 mt-4'>
                        <div className='card p-4'>
                            <div className="col col-12">
                                <div className='row'>
                                    <div className='col-2'>
                                        <Link to="/clients"><KeyboardBackspaceIcon fontSize="large" /> </Link>
                                    </div>
                                    <div className='col-10'>
                                        <h5>Historial de Créditos</h5>
                                    </div>
                                </div>
                            </div>
                            <div className="col col-12">
                                <div className='row'>
                                    <div className='col-12 text-right'>
                                        Usuario: <b>{this.props.data.name} {this.props.data.last_name} </b>
                                     </div>
                                </div>
                            </div>
                            <div className='card-body'>
                                <div className='table-responsive'>
                                    <table className="table">
                                        <thead>
                                            <tr className='text-center'>
                                                <th scope="col">Folio</th>
                                                <th scope="col">Crédito</th>
                                                <th scope="col">Numero Pagos</th>
                                                <th scope="col">Intereses</th>
                                                <th scope="col">Fecha Creación</th>
                                                <th scope="col">Estado</th>
                                                <th scope="col">Acciones</th>
                                            </tr>
                                        </thead>
                                        <tbody>
                                            {this.props.data.credit_date_user.length == 0
                                                ?
                                                <tr className='text-center'>
                                                    <td colSpan="7">No hay historial de Créditos</td>
                                                </tr>
                                                :
                                                credits_date
                                            }
                                        </tbody>
                                    </table>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </section>
        );
    }
}

export default HistoryCreditsClients;