import React, { useState } from 'react';
import { makeStyles, Theme, createStyles } from '@material-ui/core/styles';
import Modal from '@material-ui/core/Modal';
import Backdrop from '@material-ui/core/Backdrop';
import Fade from '@material-ui/core/Fade';
import './modal.scss';
import '../../assets/css/style.scss'
import Label from '../Label/Label';
import api from '../../api';
import swal from 'sweetalert';
import CloseIcon from '@material-ui/icons/Close';
import Load from '../Loading/Load';

const useStyles = makeStyles((theme: Theme) =>
    createStyles({
        modal: {
            display: 'flex',
            alignItems: 'center',
            justifyContent: 'center',
        },
        paper: {
            backgroundColor: theme.palette.background.paper,
            border: 'none !important',
            borderRadius: '15px',
            width: '95%',
            maxWidth: '500px',
            boxShadow: theme.shadows[5],
            padding: theme.spacing(2, 4, 3),
        },
    }),
);

export default function TransitionsModal(props) {
    const classes = useStyles();
    const [open, setOpen] = React.useState(false);
    const [asunto, setAsunto] = useState(props.type_S);
    const [mensaje, setMensaje] = useState('');
    const [loading, setIsLogin] = useState(false);

    const handleOpen = () => {
        setOpen(true);
    };

    const handleClose = () => {
        setOpen(false);
    };

    function handleChangeTxtArea(event) {
        setMensaje(event.target.value)
    };

    function handleSubmit(e) {
        e.preventDefault();
        setIsLogin(true);
        const body = {
            support_type_id: props.type_S.id,
            message: mensaje,
        }
        api.create.supporMessage(body)
            .then(res => {
                console.log(res)
                setIsLogin(false);
                let title_Mesa='Correcto!'
                let icon_Mesa='success'
                if (res.code==500) {
                    title_Mesa='Error!'
                    icon_Mesa='error'
                }
                swal({
                    title: title_Mesa,
                    text: res.message,
                    icon: icon_Mesa,
                    button: true,
                    dangerMode: false,
                })
                    .then(willDelete => {
                        if (willDelete) {
                            window.location = '/credit';
                            setOpen(false);
                            setIsLogin(false);
                        }
                    })
            });
    }
    return (
        <React.Fragment>
            <div onClick={handleOpen} className='btn-dark btn-block text-center m-1'>
                {props.text}
                {/* <div className='btn-dark btn-block text-center m-1'>{props.text}</div> */}
            </div>
            <Modal
                aria-labelledby="transition-modal-title"
                aria-describedby="transition-modal-description"
                className={classes.modal}
                open={open}
                onClose={handleClose}
                closeAfterTransition
                BackdropComponent={Backdrop}
                BackdropProps={{
                    timeout: 500,
                }}
            >
                <Fade in={open}>
                    {loading == true
                        ?
                        <Load />
                        :
                        <div className={classes.paper}>
                            <div className='modal-contact'>
                                <div style={{ textAlign: "right" }}><button onClick={handleClose}><CloseIcon fontSize="large" /></button></div>
                                <h3 className='text-center'>Contacto</h3>

                                <fomr>
                                    <div className='form-group'>
                                        <Label text='Asunto' />
                                        <input
                                            id='asunto'
                                            name='asunto'
                                            value={props.type_S ? props.type_S.label : ''}
                                            placeholder='Asunto'
                                            type='text'
                                            disabled
                                            className='input'
                                        />
                                    </div>
                                    <div className='form-group'>
                                        <Label text='Mensaje' />
                                        <textarea className='form-control' placeholder='Mensaje' onChange={handleChangeTxtArea} />
                                    </div>
                                    <div className='form-group'>
                                        <button className='btn-dark btn-block bt-submit' onClick={(e) => handleSubmit(e)}>Enviar mensaje</button>
                                    </div>
                                </fomr>
                            </div>
                        </div>
                    }
                </Fade>
            </Modal>
        </React.Fragment>
    );
}