import React, {useState, useEffect} from 'react';
import { BrowserRouter, Switch, Route, } from "react-router-dom";
import Router from './Router';
import Login from './Pages/Login/Login';
import Home from './Pages/Home/Home';
import Register from './Pages/Register/Register';
import RecoverPassword from './Pages/Recovery/RecoverPassword';
import RecoverEmail from './Pages/Recovery/RecoverEmail';
import LoginAdmin from './Pages/UserAdmin/Login';

import './assets/css/style.scss';

function App() {
  return (
    <div className="App">
          <BrowserRouter>
            <Switch>
              <Route exact path={"/"} component={Home}/>
              <Route exact path="/login" component={Login} />
              <Route exact path="/register" component={Register} />
              <Route exact path="/recover_password" component={RecoverPassword} />
              <Route exact path="/recover_email" component={RecoverEmail} />
              <Route exact path="/admin" component={LoginAdmin} />
              <Route path="/" component={Router} />
              {/* <PrivateRoute exact path="/home_user" component={HomeUser} /> */}
            </Switch>
          </BrowserRouter>
    </div>
  )
}

export default App;